import type { EmotionTag, IntensityLevel } from './emotion';

// Emotion counts from emojis
export type EmotionCounts = Record<EmotionTag, number>;

// Energy counts (with inertia multiplier applied)
export type EmotionEnergy = Record<EmotionTag, number>;

// Reply type classification
export type ReplyType = 'reversal' | 'mixed' | 'single';

// Mixed emotion pair key (sorted alphabetically, e.g., "joy+sad")
export type MixedPairKey = string;

// Reversal pool ID
export type ReversalPoolId = string;

// Mixed threshold rule
export interface MixedRule {
  id: string;
  emotionA: EmotionTag;
  thresholdA: number;
  emotionB: EmotionTag;
  thresholdB: number;
  pairKey: MixedPairKey;
}

// Reversal trigger rule
export interface ReversalRule {
  id: string;
  from: EmotionTag;
  to: EmotionTag;
  poolId: ReversalPoolId;
  label?: string;
}

// Reply catalog structure
export interface ReplyCatalog {
  single: Record<EmotionTag, Record<IntensityLevel, string[]>>;
  mixed: Record<MixedPairKey, Record<IntensityLevel, string[]>>;
  reversal: Record<ReversalPoolId, string[]>;
}

// Emotion state tracking for inertia and reversal
export interface EmotionState {
  previousDominant: EmotionTag | null;
  dominantStreak: Record<EmotionTag, number>;
  lastReversalTimestamp: number | null;

  // NEW: carry buffer for building intensity across multiple messages
  carryByEmotion: Record<EmotionTag, number>;
  
  // Last timestamp (ms) when the engine processed a message.
  lastMessageTimestamp: number | null;
}

// Engine result
export interface EmotionEngineResult {
  replyType: ReplyType;
  reply: string;
  dominantEmotion: EmotionTag;
  intensity: IntensityLevel;
  matchedRule?: string;
  inertiaMultiplier: number;
}

// Inertia multipliers by streak count
export const INERTIA_MULTIPLIERS: Record<number, number> = {
  1: 1.0,
  2: 1.15,
  3: 1.3,
  4: 1.45,
  5: 1.6,
};

// Intensity thresholds (total count -> intensity level)
export const INTENSITY_THRESHOLDS: { max: number; level: IntensityLevel }[] = [
  { max: 5, level: 1 },
  { max: 12, level: 2 },
  { max: 20, level: 3 },
  { max: 29, level: 4 },
  { max: 30, level: 5 },
];

// Reversal cooldown in milliseconds
export const REVERSAL_COOLDOWN_MS = 0
