import React, { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Plus, Trash2, User, Bot, Save, Library, ArrowLeft, X } from "lucide-react";
import { toast } from "sonner";
import {
  ConversationLine,
  CustomConversation,
  saveConversation,
  deleteConversation,
  getCustomConversations,
  getConversationTitle,
  LINE_TEXT_LIMIT,
  MAX_LINES_PER_CONVERSATION,
} from "@/lib/customMessages";
import { useAppStore } from "@/stores/appStore";

type ViewMode = "creator" | "library";

const CustomMessageCreator: React.FC = () => {
  const { characterProfile, playerProfile } = useAppStore();
  const [viewMode, setViewMode] = useState<ViewMode>("creator");
  const [lines, setLines] = useState<ConversationLine[]>([]);
  const [newSpeaker, setNewSpeaker] = useState<"character" | "user">("character");
  const [newText, setNewText] = useState("");
  const [conversations, setConversations] = useState<CustomConversation[]>(getCustomConversations);

  const refreshLibrary = () => {
    setConversations(getCustomConversations());
  };

  const handleAddLine = () => {
    if (!newText.trim()) {
      toast.warning("Please enter some text");
      return;
    }
    if (lines.length >= MAX_LINES_PER_CONVERSATION) {
      toast.warning(`Maximum ${MAX_LINES_PER_CONVERSATION} lines allowed`);
      return;
    }
    setLines([...lines, { speaker: newSpeaker, text: newText.trim().slice(0, LINE_TEXT_LIMIT) }]);
    setNewText("");
  };

  const handleRemoveLine = (index: number) => {
    setLines(lines.filter((_, i) => i !== index));
  };

  const handleSave = () => {
    if (lines.length === 0) {
      toast.warning("Add at least one line to save");
      return;
    }
    saveConversation(lines);
    setLines([]);
    refreshLibrary();
    toast.success("Conversation saved!");
    // Mark daily action for custom message creation
    useAppStore.getState().markDailyAction('customMessage');
  };

  const handleDelete = (id: string) => {
    deleteConversation(id);
    refreshLibrary();
    toast.success("Conversation deleted");
  };

  const handleTextChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    if (value.length <= LINE_TEXT_LIMIT) {
      setNewText(value);
    }
  };

  if (viewMode === "library") {
    return (
      <div className="space-y-3">
        <div className="flex items-center gap-2 mb-3">
          <motion.button
            whileTap={{ scale: 0.95 }}
            onClick={() => setViewMode("creator")}
            className="p-1.5 rounded-lg bg-muted hover:bg-muted/80"
          >
            <ArrowLeft className="w-4 h-4" />
          </motion.button>
          <h4 className="font-medium text-sm">Saved Conversations</h4>
          <span className="text-xs text-muted-foreground">({conversations.length})</span>
        </div>

        {conversations.length === 0 ? (
          <p className="text-xs text-muted-foreground text-center py-4">
            No saved conversations yet
          </p>
        ) : (
          <div className="space-y-2 max-h-48 overflow-y-auto">
            {conversations.map((conv) => (
              <div
                key={conv.id}
                className="flex items-center justify-between p-2 bg-muted/50 rounded-lg"
              >
                <div className="flex-1 min-w-0">
                  <p className="text-xs truncate">{getConversationTitle(conv)}</p>
                  <p className="text-[10px] text-muted-foreground">
                    {conv.lines.length} line{conv.lines.length !== 1 ? "s" : ""}
                  </p>
                </div>
                <motion.button
                  whileTap={{ scale: 0.9 }}
                  onClick={() => handleDelete(conv.id)}
                  className="p-1.5 rounded-lg text-destructive hover:bg-destructive/10"
                >
                  <Trash2 className="w-3.5 h-3.5" />
                </motion.button>
              </div>
            ))}
          </div>
        )}
      </div>
    );
  }

  return (
    <div className="space-y-3">
      <div className="flex items-center justify-between">
        {/* <h4 className="font-medium text-sm">Create Conversation</h4> */}
        <motion.button
          whileTap={{ scale: 0.95 }}
          onClick={() => {
            refreshLibrary();
            setViewMode("library");
          }}
          className="flex items-center gap-1 px-2 py-1 rounded-lg bg-muted text-xs hover:bg-muted/80"
        >
          <Library className="w-3 h-3" />
          Library ({conversations.length})
        </motion.button>
      </div>

      {/* Current lines preview */}
      {lines.length > 0 && (
        <div className="space-y-1.5 max-h-32 overflow-y-auto p-2 bg-muted/30 rounded-lg">
          <AnimatePresence>
            {lines.map((line, index) => (
              <motion.div
                key={index}
                initial={{ opacity: 0, x: -10 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: 10 }}
                className={`flex items-start gap-2 p-1.5 rounded-lg text-xs ${
                  line.speaker === "character"
                    ? "bg-secondary/50"
                    : "bg-primary/10 ml-4"
                }`}
              >
                {line.speaker === "character" ? (
                  <Bot className="w-3 h-3 mt-0.5 shrink-0" />
                ) : (
                  <User className="w-3 h-3 mt-0.5 shrink-0" />
                )}
                <span className="flex-1 break-words">{line.text}</span>
                <button
                  onClick={() => handleRemoveLine(index)}
                  className="p-0.5 rounded hover:bg-destructive/20 text-muted-foreground hover:text-destructive shrink-0"
                >
                  <X className="w-3 h-3" />
                </button>
              </motion.div>
            ))}
          </AnimatePresence>
        </div>
      )}

      {/* Add new line */}
      <div className="space-y-2">
        <div className="flex gap-1">
          <motion.button
            whileTap={{ scale: 0.95 }}
            onClick={() => setNewSpeaker("character")}
            className={`flex items-center gap-1 px-2 py-1 rounded-lg text-xs transition-colors ${
              newSpeaker === "character"
                ? "bg-secondary text-secondary-foreground"
                : "bg-muted text-muted-foreground"
            }`}
          >
            <Bot className="w-3 h-3" />
            {characterProfile.name}
          </motion.button>
          <motion.button
            whileTap={{ scale: 0.95 }}
            onClick={() => setNewSpeaker("user")}
            className={`flex items-center gap-1 px-2 py-1 rounded-lg text-xs transition-colors ${
              newSpeaker === "user"
                ? "bg-primary text-primary-foreground"
                : "bg-muted text-muted-foreground"
            }`}
          >
            <User className="w-3 h-3" />
            {playerProfile.name || "You"}
          </motion.button>
        </div>

        <div className="flex gap-2">
          <input
            type="text"
            value={newText}
            onChange={handleTextChange}
            onKeyDown={(e) => e.key === "Enter" && handleAddLine()}
            placeholder="Enter line text..."
            maxLength={LINE_TEXT_LIMIT}
            className="flex-1 px-2 py-1.5 bg-muted rounded-lg text-xs focus:outline-none focus:ring-2 focus:ring-primary"
          />
          <motion.button
            whileTap={{ scale: 0.95 }}
            onClick={handleAddLine}
            disabled={lines.length >= MAX_LINES_PER_CONVERSATION}
            className="p-1.5 bg-primary text-primary-foreground rounded-lg disabled:opacity-50"
          >
            <Plus className="w-4 h-4" />
          </motion.button>
        </div>

        <div className="flex items-center justify-between">
          <span className="text-[10px] text-muted-foreground">
            {newText.length}/{LINE_TEXT_LIMIT} chars  {lines.length}/{MAX_LINES_PER_CONVERSATION} lines
          </span>
          {lines.length > 0 && (
            <motion.button
              whileTap={{ scale: 0.95 }}
              onClick={handleSave}
              className="flex items-center gap-1 px-2 py-1 bg-primary text-primary-foreground rounded-lg text-xs"
            >
              <Save className="w-3 h-3" />
              Save
            </motion.button>
          )}
        </div>
      </div>
    </div>
  );
};

export default CustomMessageCreator;
