import React from 'react';
import { motion } from 'framer-motion';
import { Store, Coins } from 'lucide-react';
import { STORE_ITEMS, StoreItem } from '@/lib/storeConfig';
import { useAppStore } from '@/stores/appStore';
import { toast } from 'sonner';
import { storePanelT, StorePanelUI } from '@/lib/i18n';

// Get localized item info
function getLocalizedItem(item: StoreItem, t: StorePanelUI): {
  name: string;
  description: string;
} {
  switch (item.type) {
    case 'emoji':
      return {
        name: t.emojiBox,
        description: t.emojiBoxDesc
      };
    case 'food':
      return {
        name: t.foodBox,
        description: t.foodBoxDesc
      };
    case 'music':
      return {
        name: t.musicBox,
        description: t.musicBoxDesc
      };
    default:
      return {
        name: item.name,
        description: item.description
      };
  }
}
interface StorePanelProps {
  onClose: () => void;
}
const StorePanel: React.FC<StorePanelProps> = ({
  onClose
}) => {
  const {
    points,
    purchaseRandomBox
  } = useAppStore();
  const handlePurchase = (item: StoreItem) => {
    if (points < item.price) {
      toast.error(storePanelT().notEnoughPoints(item.price - points));
      return;
    }
    const result = purchaseRandomBox(item.type);
    if (result.success) {
      toast.success(result.message);
    } else {
      toast.error(result.message);
    }
  };
  return <div className="p-4 h-full flex flex-col px-0 py-0">
      {/* Header */}
      <div className="flex items-center justify-between mb-0 my-[8px] mx-[8px] px-[8px] py-[8px]">
        <div className="flex items-center gap-2">
          <Store className="w-4 h-4 text-primary" />
          <h3 className="font-semibold text-foreground text-sm">{storePanelT().store}</h3>
        </div>
        <div className="flex items-center gap-1 bg-primary/10 px-2 py-1 rounded-full">
          <Coins className="w-3 h-3 text-primary" />
          <span className="text-xs font-semibold text-primary">{points}</span>
        </div>
      </div>

      {/* Store Items */}
      <div className="flex-1 overflow-y-auto overflow-x-hidden">
        <div className="grid grid-cols-3 gap-2 mx-[8px] mt-0 px-[8px] my-[8px] py-[3px]">
          {STORE_ITEMS.map(item => {
          const canAfford = points >= item.price;
          const t = storePanelT();
          const localizedItem = getLocalizedItem(item, t);
          return <motion.button key={item.id} whileHover={canAfford ? {
            scale: 1.02
          } : {}} whileTap={canAfford ? {
            scale: 0.98
          } : {}} onClick={() => handlePurchase(item)} disabled={!canAfford} className={`relative flex flex-col items-center gap-1 p-3 rounded-2xl transition-all ${canAfford ? 'bg-widget-bg hover:ring-2 hover:ring-primary/30 cursor-pointer' : 'bg-muted/50 opacity-60 cursor-not-allowed'}`}>
                <div className="text-2xl mb-1">{item.icon}</div>
                <span className="text-[10px] font-medium text-foreground text-center leading-tight">
                  {localizedItem.name}
                </span>
                <div className="flex items-center gap-0.5 mt-1">
                  <Coins className="w-3 h-3 text-primary" />
                  <span className={`text-[10px] font-bold ${canAfford ? 'text-primary' : 'text-muted-foreground'}`}>
                    {item.price}
                  </span>
                </div>
                <span className="text-[8px] text-muted-foreground text-center mt-0.5">
                  {localizedItem.description}
                </span>
              </motion.button>;
        })}
        </div>
      </div>
    </div>;
};
export default StorePanel;