import React, { useEffect, useCallback, useState, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Play, Pause, RotateCcw, Settings, X, Check, Coffee, VolumeX, Volume2 } from 'lucide-react';
import { useAppStore } from '@/stores/appStore';
import { Switch } from '@/components/ui/switch';
import { pomodoroT } from '@/lib/i18n';

const PomodoroWidget: React.FC = () => {
  const {
    pomodoroMinutes,
    pomodoroSeconds,
    isPomodoroRunning,
    isBreakMode,
    taskDuration,
    breakDuration,
    breakEnabled,
    autoStartBreak,
    pendingBreak,
    pomodoroMuted,
    setPomodoroTime,
    setIsPomodoroRunning,
    setTaskDuration,
    setBreakDuration,
    setBreakEnabled,
    setAutoStartBreak,
    setPomodoroMuted,
    completePomodoroSession,
    completeBreak,
    startBreak,
    resetToFocus
  } = useAppStore();

  const [showSettings, setShowSettings] = useState(false);
  const [tempTaskDuration, setTempTaskDuration] = useState(taskDuration);
  const [tempBreakDuration, setTempBreakDuration] = useState(breakDuration);
  const [tempBreakEnabled, setTempBreakEnabled] = useState(breakEnabled);
  const [tempAutoStartBreak, setTempAutoStartBreak] = useState(autoStartBreak);
  const [tempMuted, setTempMuted] = useState(pomodoroMuted);

  // Smooth progress animation state
  const [smoothProgress, setSmoothProgress] = useState(0);
  const lastTickRef = useRef<number>(Date.now());
  const animationFrameRef = useRef<number>();

  const totalSeconds = pomodoroMinutes * 60 + pomodoroSeconds;
  const maxSeconds = (isBreakMode ? breakDuration : taskDuration) * 60;

  // Smooth animation loop for progress indicator
  useEffect(() => {
    const animate = () => {
      const now = Date.now();
      const elapsed = (now - lastTickRef.current) / 1000; // seconds since last tick
      
      if (isPomodoroRunning && maxSeconds > 0) {
        // Calculate current progress including sub-second interpolation
        const currentTotalSeconds = totalSeconds - elapsed;
        const progress = Math.max(0, Math.min(100, (maxSeconds - currentTotalSeconds) / maxSeconds * 100));
        setSmoothProgress(progress);
      } else {
        // Not running, just show exact progress
        const progress = maxSeconds > 0 ? (maxSeconds - totalSeconds) / maxSeconds * 100 : 0;
        setSmoothProgress(progress);
      }
      
      animationFrameRef.current = requestAnimationFrame(animate);
    };

    animationFrameRef.current = requestAnimationFrame(animate);
    
    return () => {
      if (animationFrameRef.current) {
        cancelAnimationFrame(animationFrameRef.current);
      }
    };
  }, [isPomodoroRunning, totalSeconds, maxSeconds]);

  // Update lastTickRef when seconds change (timer ticks)
  useEffect(() => {
    lastTickRef.current = Date.now();
  }, [pomodoroSeconds]);

  const tick = useCallback(() => {
    if (pomodoroMinutes === 0 && pomodoroSeconds === 0) {
      // Play completion sound if not muted
      if (!pomodoroMuted) {
        try {
          const audio = new Audio('https://assets.mixkit.co/active_storage/sfx/2869/2869-preview.mp3');
          audio.volume = 0.1;
          audio.play().catch(() => {});
        } catch (e) {
          // Ignore audio errors
        }
      }
      
      if (isBreakMode) {
        completeBreak();
      } else {
        completePomodoroSession();
      }
      return;
    }
    if (pomodoroSeconds === 0) {
      setPomodoroTime(pomodoroMinutes - 1, 59);
    } else {
      setPomodoroTime(pomodoroMinutes, pomodoroSeconds - 1);
    }
  }, [pomodoroMinutes, pomodoroSeconds, isBreakMode, pomodoroMuted, setPomodoroTime, completePomodoroSession, completeBreak]);

  useEffect(() => {
    let interval: NodeJS.Timeout | null = null;
    if (isPomodoroRunning) {
      interval = setInterval(tick, 1000);
    }
    return () => {
      if (interval) clearInterval(interval);
    };
  }, [isPomodoroRunning, tick]);

  // Initialize timer to user's configured duration on first render
  useEffect(() => {
    // Only set if timer isn't running and we're in focus mode with default values
    if (!isPomodoroRunning && !isBreakMode && pomodoroMinutes === 25 && pomodoroSeconds === 0) {
      if (taskDuration !== 25) {
        setPomodoroTime(taskDuration, 0);
      }
    }
  }, []);

  // Update timer when taskDuration changes (only if not running and in focus mode)
  useEffect(() => {
    if (!isPomodoroRunning && !isBreakMode && !pendingBreak) {
      setPomodoroTime(taskDuration, 0);
    }
  }, [taskDuration]);

  const handleReset = () => {
    // Always reset to focus mode with configured focus duration
    resetToFocus();
  };

  const handleOpenSettings = () => {
    setTempTaskDuration(taskDuration);
    setTempBreakDuration(breakDuration);
    setTempBreakEnabled(breakEnabled);
    setTempAutoStartBreak(autoStartBreak);
    setTempMuted(pomodoroMuted);
    setShowSettings(true);
  };

  const handleSaveSettings = () => {
    setTaskDuration(tempTaskDuration);
    setBreakDuration(tempBreakDuration);
    setBreakEnabled(tempBreakEnabled);
    setAutoStartBreak(tempAutoStartBreak);
    setPomodoroMuted(tempMuted);
    if (!isPomodoroRunning && !isBreakMode) {
      setPomodoroTime(tempTaskDuration, 0);
    }
    setShowSettings(false);
  };

  const formatTime = (min: number, sec: number) => {
    return `${min.toString().padStart(2, '0')}:${sec.toString().padStart(2, '0')}`;
  };

  const circumference = 2 * Math.PI * 45; // 283 approx

  return (
    <div className="bg-widget-bg rounded-3xl p-5 shadow-widget my-[10px] relative overflow-hidden mx-0 px-[16px] py-[16px]">
      <AnimatePresence mode="wait">
        {showSettings ? (
          <motion.div
            key="settings"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            className="space-y-4"
          >
            <div className="flex items-center justify-between">
              <h3 className="font-semibold text-foreground">{pomodoroT().timerSettings}</h3>
              <motion.button
                whileTap={{ scale: 0.9 }}
                onClick={() => setShowSettings(false)}
                className="p-1 rounded-full hover:bg-muted"
              >
                <X className="w-5 h-5 text-muted-foreground" />
              </motion.button>
            </div>

            <div className="space-y-3">
              <div>
                <label className="text-sm text-muted-foreground mb-1 block">{pomodoroT().taskTime}</label>
                <input
                  type="number"
                  min={1}
                  max={60}
                  value={tempTaskDuration === 0 ? '' : tempTaskDuration}
                  onChange={(e) => {
                    const val = e.target.value;
                    if (val === '') {
                      setTempTaskDuration(0);
                    } else {
                      setTempTaskDuration(Math.max(1, Math.min(60, parseInt(val) || 1)));
                    }
                  }}
                  onBlur={() => {
                    if (tempTaskDuration === 0) setTempTaskDuration(1);
                  }}
                  className="w-full px-3 py-2 bg-muted rounded-xl text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                />
              </div>

              {/* Break Time Enabled Toggle */}
              <div className="flex items-center justify-between py-2">
                <label className="text-sm text-muted-foreground">{pomodoroT().breakTimeEnabled}</label>
                <Switch checked={tempBreakEnabled} onCheckedChange={setTempBreakEnabled} />
              </div>

              {tempBreakEnabled && (
                <>
                  <div>
                    <label className="text-sm text-muted-foreground mb-1 block">{pomodoroT().breakTime}</label>
                    <input
                      type="number"
                      min={1}
                      max={30}
                      value={tempBreakDuration === 0 ? '' : tempBreakDuration}
                      onChange={(e) => {
                        const val = e.target.value;
                        if (val === '') {
                          setTempBreakDuration(0);
                        } else {
                          setTempBreakDuration(Math.max(1, Math.min(30, parseInt(val) || 1)));
                        }
                      }}
                      onBlur={() => {
                        if (tempBreakDuration === 0) setTempBreakDuration(1);
                      }}
                      className="w-full px-3 py-2 bg-muted rounded-xl text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                    />
                  </div>

                  {/* Auto-start Break Toggle */}
                  <div className="flex items-center justify-between py-2">
                    <label className="text-sm text-muted-foreground">{pomodoroT().autoStartBreak}</label>
                    <Switch checked={tempAutoStartBreak} onCheckedChange={setTempAutoStartBreak} />
                  </div>
                </>
              )}

              {/* Mute Toggle */}
              <div className="flex items-center justify-between py-2">
                <div className="flex items-center gap-2">
                  {tempMuted ? (
                    <VolumeX className="w-4 h-4 text-muted-foreground" />
                  ) : (
                    <Volume2 className="w-4 h-4 text-muted-foreground" />
                  )}
                  <label className="text-sm text-muted-foreground">{pomodoroT().muteCompletionSound}</label>
                </div>
                <Switch checked={tempMuted} onCheckedChange={setTempMuted} />
              </div>
            </div>

            <div className="flex gap-2">
              <motion.button
                whileTap={{ scale: 0.95 }}
                onClick={() => setShowSettings(false)}
                className="flex-1 py-2 bg-muted text-muted-foreground rounded-xl"
              >
                {pomodoroT().cancel}
              </motion.button>
              <motion.button
                whileTap={{ scale: 0.95 }}
                onClick={handleSaveSettings}
                className="flex-1 py-2 bg-primary text-primary-foreground rounded-xl flex items-center justify-center gap-2"
              >
                <Check className="w-4 h-4" />
                {pomodoroT().save}
              </motion.button>
            </div>
          </motion.div>
        ) : (
          <motion.div
            key="timer"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
          >
            <div className="flex items-center justify-between mb-4">
              <h3 className="font-semibold text-foreground">
                {isBreakMode ? pomodoroT().breakTimeTitle : pendingBreak ? pomodoroT().readyForBreak : pomodoroT().focusTimer}
              </h3>
              <motion.button
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
                onClick={handleOpenSettings}
                className="p-1.5 rounded-full hover:bg-muted"
              >
                <Settings className="w-4 h-4 text-muted-foreground" />
              </motion.button>
            </div>

            <div className="flex items-center justify-center gap-6">
              {/* Timer circle with smooth animation */}
              <div className="relative w-28 h-28">
                <svg className="w-full h-full -rotate-90" viewBox="0 0 100 100">
                  <circle
                    cx="50"
                    cy="50"
                    r="45"
                    fill="none"
                    stroke="hsl(var(--muted))"
                    strokeWidth="8"
                  />
                  <circle
                    cx="50"
                    cy="50"
                    r="45"
                    fill="none"
                    stroke={isBreakMode || pendingBreak ? "hsl(var(--accent))" : "hsl(var(--primary))"}
                    strokeWidth="8"
                    strokeLinecap="round"
                    strokeDasharray={circumference}
                    strokeDashoffset={circumference - (circumference * smoothProgress) / 100}
                    style={{ transition: 'stroke 0.3s ease' }}
                  />
                </svg>
                <div className="absolute inset-0 flex items-center justify-center">
                  <span className="text-2xl font-bold text-foreground">
                    {formatTime(pomodoroMinutes, pomodoroSeconds)}
                  </span>
                </div>
              </div>

              {/* Controls */}
              <div className="flex flex-col gap-2">
                {pendingBreak ? (
                  <motion.button
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    onClick={startBreak}
                    className="w-12 h-12 rounded-full bg-accent flex items-center justify-center shadow-lg"
                  >
                    <Coffee className="w-5 h-5 text-accent-foreground" />
                  </motion.button>
                ) : (
                  <motion.button
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    onClick={() => setIsPomodoroRunning(!isPomodoroRunning)}
                    className={`w-12 h-12 rounded-full flex items-center justify-center shadow-lg ${isBreakMode ? 'bg-accent' : 'bg-primary'}`}
                  >
                    {isPomodoroRunning ? (
                      <Pause className="w-5 h-5 text-primary-foreground" />
                    ) : (
                      <Play className="w-5 h-5 text-primary-foreground ml-0.5" />
                    )}
                  </motion.button>
                )}
                <motion.button
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={handleReset}
                  className="w-12 h-12 rounded-full bg-muted flex items-center justify-center"
                >
                  <RotateCcw className="w-5 h-5 text-muted-foreground" />
                </motion.button>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default PomodoroWidget;
