import React, { useState } from "react";
import { motion } from "framer-motion";
import { Gift, Sparkles, Store, Trophy, MessageSquare, Trash2 } from "lucide-react";
import { toast } from "sonner";
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog";
import { attachmentMenuT } from "@/lib/i18n";
interface AttachmentMenuProps {
  onGiftClick: () => void;
  onFortuneClick: () => void;
  onStoreClick: () => void;
  onChallengesClick: () => void;
  onTalkClick: () => void;
  onClearChat: () => void;
  canUseFortune: boolean;
}
const AttachmentMenu: React.FC<AttachmentMenuProps> = ({
  onGiftClick,
  onFortuneClick,
  onStoreClick,
  onChallengesClick,
  onTalkClick,
  onClearChat,
  canUseFortune
}) => {
  const [showClearDialog, setShowClearDialog] = useState(false);
  const handleFortuneClick = () => {
    if (!canUseFortune) {
      toast.info(attachmentMenuT().useAgainTomorrow);
      return;
    }
    onFortuneClick();
  };
  const handleClearConfirm = () => {
    onClearChat();
    setShowClearDialog(false);
    toast.success(attachmentMenuT().chatCleared);
  };
  const buttons = [{
    icon: Gift,
    label: attachmentMenuT().gift,
    onClick: onGiftClick,
    color: "bg-primary/10 text-primary",
    disabled: false
  }, {
    icon: Sparkles,
    label: attachmentMenuT().todaysFortune,
    onClick: handleFortuneClick,
    color: canUseFortune ? "bg-accent/20 text-accent-foreground" : "bg-muted text-muted-foreground",
    disabled: !canUseFortune
  }, {
    icon: Store,
    label: attachmentMenuT().store,
    onClick: onStoreClick,
    color: "bg-primary/10 text-primary",
    disabled: false
  }, {
    icon: Trophy,
    label: attachmentMenuT().challenges,
    onClick: onChallengesClick,
    color: "bg-accent/20 text-accent-foreground",
    disabled: false
  }, {
    icon: MessageSquare,
    label: attachmentMenuT().talk,
    onClick: onTalkClick,
    color: "bg-primary/10 text-primary",
    disabled: false
  }, {
    icon: Trash2,
    label: attachmentMenuT().clearChat,
    onClick: () => setShowClearDialog(true),
    color: "bg-destructive/10 text-destructive",
    disabled: false
  }];
  return <>
      <div className="p-4 h-full flex flex-col px-0 py-0">
        <div className="flex-1 overflow-y-hidden overflow-x-hidden">
          <div className="grid grid-cols-4 gap-2 my-[16px] mx-[16px]">
            {buttons.map(button => <motion.button key={button.label} whileHover={{
            scale: 1.05
          }} whileTap={{
            scale: 0.95
          }} onClick={button.onClick} className={`relative flex flex-col items-center gap-1.5 p-3 rounded-2xl ${button.color} transition-colors ${button.disabled ? "opacity-60" : ""}`}>
                <div className="relative">
                  <button.icon className="w-5 h-5" />
                </div>
                <span className="text-[10px] font-medium text-center leading-tight">{button.label}</span>
                {button.disabled && <span className="absolute -top-1 -right-1 text-[8px] bg-muted px-1 rounded">{attachmentMenuT().usedLabel}</span>}
              </motion.button>)}
          </div>
        </div>
      </div>

      <AlertDialog open={showClearDialog} onOpenChange={setShowClearDialog}>
        <AlertDialogContent className="max-w-[280px] rounded-2xl">
          <AlertDialogHeader>
            <AlertDialogTitle>{attachmentMenuT().clearChatQuestion}</AlertDialogTitle>
            <AlertDialogDescription>
              {attachmentMenuT().clearChatDescription}
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>{attachmentMenuT().cancel}</AlertDialogCancel>
            <AlertDialogAction onClick={handleClearConfirm} className="bg-destructive text-destructive-foreground hover:bg-destructive/90">
              {attachmentMenuT().clear}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </>;
};
export default AttachmentMenu;