import React from "react";
import { motion } from "framer-motion";
import { MessageCircle, Settings, ClipboardList } from "lucide-react";
import { useAppStore } from "@/stores/appStore";
import PomodoroWidget from "../widgets/PomodoroWidget";
import TodoWidget from "../widgets/TodoWidget";
import YouTubePlayer from "../widgets/YouTubePlayer";
const HomeScreen: React.FC = () => {
  const {
    setCurrentScreen,
    hasUnreadMessages
  } = useAppStore();
  const containerVariants = {
    hidden: {
      opacity: 0
    },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };
  const itemVariants = {
    hidden: {
      opacity: 0,
      y: 20
    },
    visible: {
      opacity: 1,
      y: 0
    }
  };
  return <motion.div variants={containerVariants} initial="hidden" animate="visible" className="h-full bg-phone-home-bg p-4 flex flex-col mx-0">
      {/* Date header */}
      <motion.div variants={itemVariants} className="text-center mb-2">
        <p className="text-muted-foreground text-sm">
          {new Date().toLocaleDateString("en-US", {
          weekday: "long",
          month: "long",
          day: "numeric"
        })}
        </p>
        <h1 className="text-2xl font-bold text-foreground mt-1">I Like Texxting</h1>
      </motion.div>

      {/* Widgets area */}
      <div className="flex-1 space-y-4 overflow-y-auto pb-24">
        {/* Music Player above Focus Timer */}
        <motion.div variants={itemVariants}>
          <YouTubePlayer />
        </motion.div>

        <motion.div variants={itemVariants}>
          <PomodoroWidget />
        </motion.div>

        <motion.div variants={itemVariants}>
          <TodoWidget />
        </motion.div>
      </div>

      {/* App dock */}
      <motion.div variants={itemVariants} className="absolute bottom-8 left-4 right-4 bg-widget-bg/90 backdrop-blur-xl rounded-3xl py-4 px-6 shadow-widget flex justify-around items-center mx-0">
        {/* Chat App */}
        <motion.button whileHover={{
        scale: 1.1
      }} whileTap={{
        scale: 0.95
      }} onClick={() => setCurrentScreen("chat")} className="relative flex flex-col items-center gap-1">
          <div className="w-14 h-14 rounded-2xl bg-primary flex items-center justify-center shadow-lg">
            <MessageCircle className="w-7 h-7 text-primary-foreground" />
          </div>
          {hasUnreadMessages && <motion.div initial={{
          scale: 0
        }} animate={{
          scale: 1
        }} className="absolute -top-1 -right-1 w-3 h-3 bg-destructive rounded-full" />}
          <span className="text-xs text-muted-foreground">Message</span>
        </motion.button>

        {/* Records */}
        <motion.button whileHover={{
        scale: 1.1
      }} whileTap={{
        scale: 0.95
      }} onClick={() => setCurrentScreen("records")} className="flex flex-col items-center gap-1">
          <div className="w-14 h-14 rounded-2xl bg-secondary flex items-center justify-center shadow-lg">
            <ClipboardList className="w-7 h-7 text-accent-foreground" />
          </div>
          <span className="text-xs text-muted-foreground">Records</span>
        </motion.button>

        {/* Settings */}
        <motion.button whileHover={{
        scale: 1.1
      }} whileTap={{
        scale: 0.95
      }} onClick={() => setCurrentScreen("settings")} className="flex flex-col items-center gap-1">
          <div className="w-14 h-14 rounded-2xl bg-muted flex items-center justify-center shadow-lg">
            <Settings className="w-7 h-7 text-muted-foreground" />
          </div>
          <span className="text-xs text-muted-foreground">Settings</span>
        </motion.button>
      </motion.div>
    </motion.div>;
};
export default HomeScreen;