import React from 'react';
import { motion } from 'framer-motion';
import { ArrowLeft, Lock, Heart } from 'lucide-react';
import { useAppStore } from '@/stores/appStore';
import CharacterAvatar from '../chat/CharacterAvatar';
import { zodiacInfo, AFFINITY_THRESHOLDS, getZodiacFavorites } from '@/lib/zodiac';
import { Progress } from '@/components/ui/progress';
import { characterProfileT, zodiacT, monthT } from '@/lib/i18n';
const CharacterProfileScreen: React.FC = () => {
  const {
    setCurrentScreen,
    characterProfile,
    getZodiacSign,
    getEffectiveFavorites
  } = useAppStore();
  const zodiac = getZodiacSign();
  const zodiacDisplay = zodiac ? zodiacInfo[zodiac] : null;

  // Use effective favorites (Advanced Settings override zodiac preferences)
  const effectiveFavorites = getEffectiveFavorites();
  const zodiacFavorites = getZodiacFavorites(zodiac);
  const formatBirthday = (isoDate: string) => {
    if (!isoDate) return '';
    // Parse MM-DD or YYYY-MM-DD format
    let month: number, day: number;
    if (/^\d{2}-\d{2}$/.test(isoDate)) {
      [month, day] = isoDate.split('-').map(Number);
    } else {
      const date = new Date(isoDate);
      month = date.getMonth() + 1;
      day = date.getDate();
    }
    const months = monthT();
    return `${months[month as keyof typeof months]} ${day}`;
  };

  // Get localized zodiac name
  const getLocalizedZodiacName = () => {
    if (!zodiac || !zodiacDisplay) return '';
    const zodiacNames = zodiacT();
    return `${zodiacDisplay.symbol} ${zodiacNames[zodiac]}`;
  };
  const ProfileField = ({
    label,
    value,
    locked = false,
    unlockThreshold
  }: {
    label: string;
    value: string;
    locked?: boolean;
    unlockThreshold?: number;
  }) => <div className="flex items-center justify-between py-3 border-b border-border last:border-b-0">
      <span className="text-sm text-muted-foreground">{label}</span>
      {locked ? <div className="flex items-center gap-2 text-muted-foreground">
          <Lock className="w-3 h-3" />
          <span className="text-sm">???</span>
          {unlockThreshold && <span className="text-xs opacity-60">({unlockThreshold} )</span>}
        </div> : <span className="text-sm text-foreground font-medium">{value || ''}</span>}
    </div>;
  return <motion.div initial={{
    y: 50,
    opacity: 0
  }} animate={{
    y: 0,
    opacity: 1
  }} exit={{
    y: 50,
    opacity: 0
  }} className="h-full bg-phone-home-bg flex flex-col">
      <div className="flex items-center gap-3 px-4 py-3 bg-widget-bg/80 backdrop-blur-lg border-b border-border">
        <motion.button whileHover={{
        scale: 1.1
      }} whileTap={{
        scale: 0.9
      }} onClick={() => setCurrentScreen('chat')} className="p-2 -ml-2 rounded-full hover:bg-muted">
          <ArrowLeft className="w-5 h-5 text-foreground" />
        </motion.button>
        <h2 className="font-semibold text-foreground text-lg">{characterProfileT().profile}</h2>
      </div>

      <div className="flex-1 overflow-y-auto">
        <div className="flex flex-col items-center bg-gradient-to-b from-primary/10 to-transparent my-0 py-[16px]">
          <CharacterAvatar size="xl" interactive={false} />
          <h2 className="mt-4 text-xl font-bold text-foreground">{characterProfile.name}</h2>
        </div>
        <div className="px-4 py-4">
          <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
            <div className="flex items-center justify-between mb-2">
              <div className="flex items-center gap-2">
                <Heart className="w-4 h-4 text-primary" />
                <span className="font-semibold text-foreground">{characterProfileT().affinity}</span>
              </div>
              <span className="text-sm font-bold text-primary">{characterProfile.affinity}/100</span>
            </div>
            <Progress value={characterProfile.affinity} className="h-2" />
            {/* <p className="text-xs text-muted-foreground mt-2 text-center">
              {characterProfile.affinity < AFFINITY_THRESHOLDS.food && characterProfileT().sendEmojis}
              {characterProfile.affinity >= AFFINITY_THRESHOLDS.food && characterProfile.affinity < AFFINITY_THRESHOLDS.place && characterProfileT().keepChatting}
              {characterProfile.affinity >= AFFINITY_THRESHOLDS.place && characterProfile.affinity < AFFINITY_THRESHOLDS.music && characterProfileT().gettingCloser}
              {characterProfile.affinity >= AFFINITY_THRESHOLDS.music && characterProfileT().knowMeWell}
            </p> */}
          </div>
        </div>

        <div className="px-4 pb-6 space-y-4">
          <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
            <h3 className="font-semibold text-foreground mb-2">{characterProfileT().basicInfo}</h3>
            <ProfileField label={characterProfileT().name} value={characterProfile.name} />
            <ProfileField label={characterProfileT().birthday} value={formatBirthday(characterProfile.birthday)} />
            <ProfileField label={characterProfileT().zodiac} value={getLocalizedZodiacName()} />
          </div>

          <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
            <h3 className="font-semibold text-foreground mb-2">{characterProfileT().favorites}</h3>
            <ProfileField label={characterProfileT().food} value={effectiveFavorites.food || zodiacFavorites.food} locked={!characterProfile.unlocks.food} unlockThreshold={AFFINITY_THRESHOLDS.food} />
            <ProfileField label={characterProfileT().place} value={effectiveFavorites.place || zodiacFavorites.place} locked={!characterProfile.unlocks.place} unlockThreshold={AFFINITY_THRESHOLDS.place} />
            <ProfileField label={characterProfileT().music} value={effectiveFavorites.music || zodiacFavorites.music} locked={!characterProfile.unlocks.music} unlockThreshold={AFFINITY_THRESHOLDS.music} />
          </div>
        </div>
      </div>
    </motion.div>;
};
export default CharacterProfileScreen;