import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Sparkles, MessageCircle, Heart, Timer } from 'lucide-react';
import { useAppStore } from '@/stores/appStore';

const NotificationBanner: React.FC = () => {
  const notifications = useAppStore((state) => state.notifications);

  const getIcon = (type: string) => {
    switch (type) {
      case 'emoji':
        return <Sparkles className="w-4 h-4" />;
      case 'unlock':
        return <Heart className="w-4 h-4" />;
      case 'pomodoro':
        return <Timer className="w-4 h-4" />;
      default:
        return <MessageCircle className="w-4 h-4" />;
    }
  };

  return (
    <div className="absolute top-[50px] left-2 right-2 z-[100] space-y-2">
      <AnimatePresence>
        {notifications.map((notification) => (
          <motion.div
            key={notification.id}
            initial={{ y: -100, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            exit={{ y: -100, opacity: 0 }}
            transition={{ type: 'spring', damping: 25, stiffness: 300 }}
            className={`flex items-center gap-3 px-4 py-3 rounded-2xl shadow-lg ${
              notification.type === 'unlock' 
                ? 'bg-primary text-primary-foreground' 
                : 'bg-notification-bg text-notification-foreground'
            }`}
          >
            <div className={`w-8 h-8 rounded-lg flex items-center justify-center ${
              notification.type === 'unlock' ? 'bg-white/20' : 'bg-primary'
            }`}>
              {getIcon(notification.type)}
            </div>
            <span className="text-sm font-medium flex-1">{notification.message}</span>
          </motion.div>
        ))}
      </AnimatePresence>
    </div>
  );
};

export default NotificationBanner;
