// Get a random preference based on zodiac
export function getRandomPreference(
  zodiac: ZodiacSign | null,
  category: 'foods' | 'places' | 'music' | 'giftItems'
): string {
  if (!zodiac) return '';
  const prefs = zodiacPreferences[zodiac]?.[category];
  if (!prefs || prefs.length === 0) return '';
  return prefs[Math.floor(Math.random() * prefs.length)];
}

// Get all preferences for a zodiac sign
export function getZodiacPreferences(zodiac: ZodiacSign | null): ZodiacPreferences | null {
  if (!zodiac) return null;
  return zodiacPreferences[zodiac] || null;
}
