// Image validation and resizing utility

export interface ImageValidationResult {
  valid: boolean;
  error?: string;
}

// Constants for validation
const MAX_FILE_SIZE = 10 * 1024; // 150kb - for size check (auto-resize if larger)
const ALLOWED_TYPES = ["image/jpeg", "image/png"];
const OUTPUT_SIZE = 128; // Output square size

/**
 * Validates an image file before processing (only file type check now)
 */
export function validateImageFile(file: File): ImageValidationResult {
  // Check file type
  if (!ALLOWED_TYPES.includes(file.type)) {
    return { valid: false, error: "JPG/JPEG  " };
  }

  return { valid: true };
}

/**
 * Check if file needs resizing (over 150KB)
 */
export function needsResize(file: File): boolean {
  return file.size > MAX_FILE_SIZE;
}

/**
 * Resizes and center-crops an image to a square
 * Returns a Blob of the processed image
 */
export function resizeAndCropImage(img: HTMLImageElement): Promise<Blob> {
  return new Promise((resolve, reject) => {
    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d");

    if (!ctx) {
      reject(new Error("Could not get canvas context"));
      return;
    }

    canvas.width = OUTPUT_SIZE;
    canvas.height = OUTPUT_SIZE;

    const { width, height } = img;

    // Calculate center crop
    const minDimension = Math.min(width, height);
    const sx = (width - minDimension) / 2;
    const sy = (height - minDimension) / 2;

    // Draw cropped and scaled image
    ctx.drawImage(
      img,
      sx,
      sy,
      minDimension,
      minDimension, // Source rectangle (center crop)
      0,
      0,
      OUTPUT_SIZE,
      OUTPUT_SIZE, // Destination rectangle
    );

    // Convert to blob (PNG for quality)
    canvas.toBlob(
      (blob) => {
        if (blob) {
          resolve(blob);
        } else {
          reject(new Error("Failed to create image blob"));
        }
      },
      "image/png",
      1.0,
    );
  });
}

/**
 * Loads an image file and returns an HTMLImageElement
 */
export function loadImageFromFile(file: File): Promise<HTMLImageElement> {
  return new Promise((resolve, reject) => {
    const img = new Image();
    const url = URL.createObjectURL(file);

    img.onload = () => {
      URL.revokeObjectURL(url);
      resolve(img);
    };

    img.onerror = () => {
      URL.revokeObjectURL(url);
      reject(new Error("Failed to load image"));
    };

    img.src = url;
  });
}

/**
 * Full pipeline: validate, load, resize, and return blob
 * Now auto-resizes files over 150KB instead of rejecting them
 */
export async function processAvatarImage(file: File): Promise<{ blob: Blob } | { error: string }> {
  // Validate file type only
  const fileValidation = validateImageFile(file);
  if (!fileValidation.valid) {
    return { error: fileValidation.error! };
  }

  // Load image
  let img: HTMLImageElement;
  try {
    img = await loadImageFromFile(file);
  } catch {
    return { error: "Failed to load image file" };
  }

  // Always resize and crop to OUTPUT_SIZE (256x256)
  // This handles both oversized files and dimension issues
  try {
    const blob = await resizeAndCropImage(img);
    return { blob };
  } catch {
    return { error: "Failed to process image" };
  }
}
