import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Plus, Check, X, Eye, EyeOff } from 'lucide-react';
import { useAppStore } from '@/stores/appStore';
import { todoT } from '@/lib/i18n';
const TodoWidget: React.FC = () => {
  const {
    todos,
    addTodo,
    toggleTodo,
    removeTodo,
    showCompletedTodos,
    setShowCompletedTodos
  } = useAppStore();
  const [newTodoText, setNewTodoText] = useState('');
  const [isAdding, setIsAdding] = useState(false);
  const handleAddTodo = () => {
    if (newTodoText.trim()) {
      addTodo(newTodoText.trim());
      setNewTodoText('');
      setIsAdding(false);
    }
  };
  const activeTodos = todos.filter(t => !t.completed);
  const completedTodos = todos.filter(t => t.completed);
  const completedCount = completedTodos.length;
  return <div className="bg-widget-bg rounded-3xl p-5 shadow-widget mx-0 my-0 px-[16px] py-[16px]">
      <div className="flex items-center justify-between mb-4">
        <h3 className="font-semibold text-foreground">{todoT().todaysTasks}</h3>
        <div className="flex items-center gap-2">
          <span className="text-xs text-muted-foreground">
            {completedCount}/{todos.length} {todoT().done}
          </span>
          <motion.button whileTap={{
          scale: 0.9
        }} onClick={() => setShowCompletedTodos(!showCompletedTodos)} className="p-1.5 rounded-full hover:bg-muted" title={showCompletedTodos ? todoT().hideCompleted : todoT().showCompleted}>
            {showCompletedTodos ? <Eye className="w-4 h-4 text-primary" /> : <EyeOff className="w-4 h-4 text-muted-foreground" />}
          </motion.button>
        </div>
      </div>

      <div className="space-y-3 overflow-visible overflow-x-hidden my-[16px]">
        {/* Active todos */}
        <AnimatePresence>
          {activeTodos.map(todo => <motion.div key={todo.id} initial={{
          opacity: 0,
          x: -20
        }} animate={{
          opacity: 1,
          x: 0
        }} exit={{
          opacity: 0,
          x: 20
        }} className="flex items-center gap-3 p-3 bg-muted rounded-xl group">
              <motion.button whileTap={{
            scale: 0.9
          }} onClick={() => toggleTodo(todo.id)} className="w-6 h-6 rounded-full border-2 flex items-center justify-center transition-colors border-muted-foreground">
                {/* Empty circle for active */}
              </motion.button>
              <span className="flex-1 text-sm text-foreground">{todo.text}</span>
              <motion.button whileTap={{
            scale: 0.9
          }} onClick={() => removeTodo(todo.id)} className="opacity-0 group-hover:opacity-100 p-1 hover:bg-destructive/10 rounded transition-opacity">
                <X className="w-4 h-4 text-destructive" />
              </motion.button>
            </motion.div>)}
        </AnimatePresence>

        {/* Completed todos (shown when toggle is on) */}
        <AnimatePresence>
          {showCompletedTodos && completedTodos.map(todo => <motion.div key={todo.id} initial={{
          opacity: 0,
          height: 0
        }} animate={{
          opacity: 1,
          height: 'auto'
        }} exit={{
          opacity: 0,
          height: 0
        }} className="flex items-center gap-3 p-3 bg-muted/50 rounded-xl group">
                <motion.button whileTap={{
            scale: 0.9
          }} onClick={() => toggleTodo(todo.id)} className="w-6 h-6 rounded-full border-2 flex items-center justify-center transition-colors bg-success border-success">
                  <Check className="w-4 h-4 text-primary-foreground" />
                </motion.button>
                <span className="flex-1 text-sm line-through text-muted-foreground">
                  {todo.text}
                </span>
                <motion.button whileTap={{
            scale: 0.9
          }} onClick={() => removeTodo(todo.id)} className="opacity-0 group-hover:opacity-100 p-1 hover:bg-destructive/10 rounded transition-opacity">
                  <X className="w-4 h-4 text-destructive" />
                </motion.button>
              </motion.div>)}
        </AnimatePresence>

        {/* {todos.length === 0 && !isAdding && <p className="text-center text-muted-foreground text-sm py-4">
            {todoT().noTasksYet}
          </p>} */}

        {isAdding ? <motion.div initial={{
        opacity: 0,
        y: 10
      }} animate={{
        opacity: 1,
        y: 0
      }} className="flex items-center gap-2 mx-0 py-[6px] px-[6px]">
            <input type="text" value={newTodoText} onChange={e => setNewTodoText(e.target.value)} onKeyDown={e => e.key === 'Enter' && handleAddTodo()} placeholder={todoT().whatNeedsToBeDone} autoFocus className="flex-1 py-2 bg-muted rounded-xl text-sm text-foreground placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-primary px-[16px]" />
            <motion.button whileTap={{
          scale: 0.9
        }} onClick={handleAddTodo} className="p-2 bg-primary rounded-xl">
              <Check className="w-4 h-4 text-primary-foreground" />
            </motion.button>
            <motion.button whileTap={{
          scale: 0.9
        }} onClick={() => setIsAdding(false)} className="p-2 bg-muted rounded-xl">
              <X className="w-4 h-4 text-muted-foreground" />
            </motion.button>
          </motion.div> : <motion.button whileTap={{
        scale: 0.98
      }} onClick={() => setIsAdding(true)} className="w-full flex items-center justify-center gap-2 py-3 border-2 border-dashed border-muted-foreground/30 rounded-xl text-muted-foreground hover:border-primary hover:text-primary transition-colors">
            <Plus className="w-4 h-4" />
            <span className="text-sm">{todoT().addTask}</span>
          </motion.button>}
      </div>
    </div>;
};
export default TodoWidget;