import React, { useRef, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ArrowLeft } from 'lucide-react';
import { useAppStore } from '@/stores/appStore';
import EmojiPanel from '../chat/EmojiPanel';
import MessageBubble from '../chat/MessageBubble';
import CharacterAvatar from '../chat/CharacterAvatar';

const ChatScreen: React.FC = () => {
  const {
    messages,
    setCurrentScreen,
    chatWallpaper,
    characterProfile,
  } = useAppStore();
  const messagesEndRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'auto' });
  }, [messages]);

  const getWallpaperStyle = () => {
    switch (chatWallpaper) {
      case 'stars':
        return 'bg-gradient-to-b from-indigo-100 to-purple-100 dark:from-indigo-900/30 dark:to-purple-900/30';
      case 'hearts':
        return 'bg-gradient-to-b from-rose-50 to-pink-100 dark:from-rose-900/20 dark:to-pink-900/30';
      case 'clouds':
        return 'bg-gradient-to-b from-sky-50 to-blue-100 dark:from-sky-900/20 dark:to-blue-900/30';
      default:
        return 'bg-phone-chat-bg';
    }
  };

  return (
    <div className={`h-full flex flex-col ${getWallpaperStyle()}`}>
      {/* Chat header */}
      <motion.div
        initial={{ y: -20, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        className="flex items-center gap-3 px-4 py-3 bg-widget-bg/80 backdrop-blur-lg border-b border-border"
      >
        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          onClick={() => setCurrentScreen('home')}
          className="p-2 -ml-2 rounded-full hover:bg-muted"
        >
          <ArrowLeft className="w-5 h-5 text-foreground" />
        </motion.button>
        
        <CharacterAvatar 
          size="sm" 
          onClick={() => setCurrentScreen('profile')}
        />
        
        <div className="flex-1">
          <h2 className="font-semibold text-foreground">{characterProfile.name}</h2>
          <p className="text-xs text-success">Online</p>
        </div>
      </motion.div>

      {/* Messages area */}
      <div className="flex-1 overflow-y-auto overflow-x-hidden px-4 py-4 space-y-3">
        <AnimatePresence initial={false}>
          {messages.map((message) => (
            <MessageBubble key={message.id} message={message} />
          ))}
        </AnimatePresence>
        <div ref={messagesEndRef} />
      </div>

      {/* Emoji input */}
      <EmojiPanel />
    </div>
  );
};

export default ChatScreen;
