// Zodiac-based favorites - now delegates unlock messages to toneCatalogs
import type { ZodiacSign } from './zodiac';
import { zodiacPreferences } from './zodiacPreferences';
import { ToneKey, getZodiacUnlockMessagesForTone, ZodiacUnlockMessages } from './toneCatalogs';

export interface ZodiacFavorites {
  food: string;
  place: string;
  music: string;
}

// Re-export ZodiacUnlockMessages from toneCatalogs for convenience
export type { ZodiacUnlockMessages } from './toneCatalogs';

// Generate favorites from zodiacPreferences
function generateFavoritesFromPreferences(zodiac: ZodiacSign): ZodiacFavorites {
  const prefs = zodiacPreferences[zodiac];
  return {
    food: prefs.foods[0] || 'Strawberry cake ',
    place: prefs.places[0] || 'A quiet park by the lake ',
    music: prefs.music[0] || 'Lo-fi and acoustic songs ',
  };
}

// Get favorites for a specific zodiac sign
export function getZodiacFavorites(zodiac: ZodiacSign | null): ZodiacFavorites {
  if (!zodiac) {
    return {
      food: 'Strawberry cake ',
      place: 'A quiet park by the lake ',
      music: 'Lo-fi and acoustic songs ',
    };
  }
  return generateFavoritesFromPreferences(zodiac);
}

// Get unlock messages for a specific zodiac sign and tone
// Now delegates to toneCatalogs
export function getZodiacUnlockMessages(zodiac: ZodiacSign | null, tone: ToneKey = 'default'): ZodiacUnlockMessages {
  return getZodiacUnlockMessagesForTone(tone, zodiac);
}
