// Custom Message Library - Storage and types

export interface ConversationLine {
  speaker: "character" | "user";
  text: string;
}

export interface CustomConversation {
  id: string;
  lines: ConversationLine[];
  createdAt: number;
}

const STORAGE_KEY = "custom-messages-library";
const LINE_TEXT_LIMIT = 50;
const MAX_LINES_PER_CONVERSATION = 10;

// Track last played to avoid repeats
let lastPlayedId: string | null = null;

/**
 * Get all saved conversations
 */
export function getCustomConversations(): CustomConversation[] {
  try {
    const stored = localStorage.getItem(STORAGE_KEY);
    if (!stored) return [];
    return JSON.parse(stored) as CustomConversation[];
  } catch {
    return [];
  }
}

export function generateId(): string {
  return `${Date.now().toString(36)}-${Math.random().toString(36).slice(2, 8)}`;
}

/**
 * Save a new conversation
 */
export function saveConversation(lines: ConversationLine[]): CustomConversation {
  const conversations = getCustomConversations();
  const newConversation: CustomConversation = {
    // id: crypto.randomUUID(),
    id: generateId(),
    lines: lines.map((line) => ({
      speaker: line.speaker,
      text: line.text.slice(0, LINE_TEXT_LIMIT),
    })),
    createdAt: Date.now(),
  };
  conversations.push(newConversation);
  localStorage.setItem(STORAGE_KEY, JSON.stringify(conversations));
  return newConversation;
}

/**
 * Delete a conversation by ID
 */
export function deleteConversation(id: string): void {
  const conversations = getCustomConversations();
  const filtered = conversations.filter((c) => c.id !== id);
  localStorage.setItem(STORAGE_KEY, JSON.stringify(filtered));
  if (lastPlayedId === id) {
    lastPlayedId = null;
  }
}

/**
 * Get a random conversation, avoiding the last played one
 */
export function getRandomConversation(): CustomConversation | null {
  const conversations = getCustomConversations();
  if (conversations.length === 0) return null;

  // Filter out last played if we have more than one conversation
  let candidates = conversations;
  if (conversations.length > 1 && lastPlayedId) {
    candidates = conversations.filter((c) => c.id !== lastPlayedId);
  }

  const randomIndex = Math.floor(Math.random() * candidates.length);
  const selected = candidates[randomIndex];
  lastPlayedId = selected.id;
  return selected;
}

/**
 * Generate a preview title from conversation
 */
export function getConversationTitle(conversation: CustomConversation): string {
  const firstLine = conversation.lines[0];
  if (!firstLine) return "Empty conversation";

  const preview = firstLine.text.slice(0, 20);
  const suffix = firstLine.text.length > 20 ? "..." : "";
  // const date = new Date(conversation.createdAt).toLocaleDateString();

  // return `"${preview}${suffix}" - ${date}`;
  return `"${preview}${suffix}"`;
}

/**
 * Check if library has conversations
 */
export function hasConversations(): boolean {
  return getCustomConversations().length > 0;
}

export { LINE_TEXT_LIMIT, MAX_LINES_PER_CONVERSATION };
