// Utility to get default replies for a single emotion and intensity
export function getDefaultSingleReplies(emotion: EmotionTag, intensity: IntensityLevel): string[] {
  return DEFAULT_CATALOG.single[emotion]?.[intensity] ?? DEFAULT_CATALOG.single.daily[1];
}

// Utility to get default replies for a mixed pair and intensity
export function getDefaultMixedReplies(pairKey: string, intensity: IntensityLevel): string[] {
  return DEFAULT_CATALOG.mixed[pairKey]?.[intensity] ?? [];
}

// Utility to get default reversal replies
export function getDefaultReversalReplies(poolId: string): string[] {
  return DEFAULT_CATALOG.reversal[poolId] ?? [];
}
