import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ArrowLeft, Clock, CheckSquare, Trash2 } from 'lucide-react';
import { useAppStore } from '@/stores/appStore';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from '@/components/ui/alert-dialog';

const RecordsScreen: React.FC = () => {
  const { 
    setCurrentScreen, 
    pomodoroRecords, 
    completedTodoRecords,
    deleteRecordsByDate,
  } = useAppStore();

  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [dateToDelete, setDateToDelete] = useState<string | null>(null);

  // Group records by date
  const groupByDate = <T extends { completedAt: string }>(records: T[]) => {
    const grouped: Record<string, T[]> = {};
    records.forEach((record) => {
      if (!grouped[record.completedAt]) {
        grouped[record.completedAt] = [];
      }
      grouped[record.completedAt].push(record);
    });
    return grouped;
  };

  const pomodoroByDate = groupByDate(pomodoroRecords);
  const todosByDate = groupByDate(completedTodoRecords);

  // Get all unique dates sorted descending
  const allDates = [...new Set([...Object.keys(pomodoroByDate), ...Object.keys(todosByDate)])].sort(
    (a, b) => new Date(b).getTime() - new Date(a).getTime()
  );

  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr);
    const today = new Date().toISOString().split('T')[0];
    const yesterday = new Date(Date.now() - 86400000).toISOString().split('T')[0];

    if (dateStr === today) return 'Today';
    if (dateStr === yesterday) return 'Yesterday';
    return date.toLocaleDateString('en-US', { weekday: 'short', month: 'short', day: 'numeric' });
  };

  const handleDeleteClick = (date: string) => {
    setDateToDelete(date);
    setDeleteDialogOpen(true);
  };

  const confirmDelete = () => {
    if (dateToDelete) {
      deleteRecordsByDate(dateToDelete);
    }
    setDeleteDialogOpen(false);
    setDateToDelete(null);
  };

  return (
    <motion.div
      initial={{ x: 50, opacity: 0 }}
      animate={{ x: 0, opacity: 1 }}
      exit={{ x: 50, opacity: 0 }}
      className="h-full bg-phone-home-bg flex flex-col"
    >
      {/* Header */}
      <div className="flex items-center gap-3 px-4 py-3 bg-widget-bg/80 backdrop-blur-lg border-b border-border">
        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          onClick={() => setCurrentScreen('home')}
          className="p-2 -ml-2 rounded-full hover:bg-muted"
        >
          <ArrowLeft className="w-5 h-5 text-foreground" />
        </motion.button>
        <h2 className="font-semibold text-foreground text-lg">Records</h2>
      </div>

      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {allDates.length === 0 ? (
          <div className="flex flex-col items-center justify-center h-full text-center">
            <div className="text-4xl mb-3"></div>
            <p className="text-muted-foreground">No records yet.</p>
            <p className="text-sm text-muted-foreground">Complete tasks or focus sessions to see them here!</p>
          </div>
        ) : (
          allDates.map((date) => (
            <motion.div
              key={date}
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              className="bg-widget-bg rounded-2xl p-4 shadow-widget"
            >
              {/* Date header with delete button */}
              <div className="flex items-center justify-between mb-3">
                <h3 className="font-semibold text-foreground">{formatDate(date)}</h3>
                <motion.button
                  whileTap={{ scale: 0.9 }}
                  onClick={() => handleDeleteClick(date)}
                  className="p-1.5 rounded-full hover:bg-destructive/10 transition-colors"
                  aria-label="Delete all records for this date"
                >
                  <Trash2 className="w-4 h-4 text-destructive" />
                </motion.button>
              </div>

              {/* Pomodoro Records */}
              {pomodoroByDate[date] && pomodoroByDate[date].length > 0 && (
                <div className="mb-3">
                  <div className="flex items-center gap-2 text-sm text-primary mb-2">
                    <Clock className="w-4 h-4" />
                    <span>Focus Sessions: {pomodoroByDate[date].length}</span>
                  </div>
                  <div className="space-y-1">
                    {pomodoroByDate[date].map((record) => (
                      <div
                        key={record.id}
                        className="px-3 py-2 bg-primary/10 rounded-xl"
                      >
                        <span className="text-sm text-primary">
                          {record.durationMinutes ? `${record.durationMinutes} min` : '25 min'}
                        </span>
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {/* Todo Records */}
              {todosByDate[date] && todosByDate[date].length > 0 && (
                <div>
                  <div className="flex items-center gap-2 text-sm text-success mb-2">
                    <CheckSquare className="w-4 h-4" />
                    <span>Completed Tasks: {todosByDate[date].length}</span>
                  </div>
                  <div className="space-y-1">
                    {todosByDate[date].map((record) => (
                      <div
                        key={record.id}
                        className="px-3 py-2 bg-success/10 rounded-xl"
                      >
                        <span className="text-sm text-foreground truncate block">
                          {record.text}
                        </span>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </motion.div>
          ))
        )}
      </div>

      {/* Confirmation Dialog */}
      <AlertDialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Delete all records?</AlertDialogTitle>
            <AlertDialogDescription>
              This will delete all records for {dateToDelete ? formatDate(dateToDelete) : 'this date'}. This action cannot be undone.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction onClick={confirmDelete} className="bg-destructive text-destructive-foreground hover:bg-destructive/90">
              Delete
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </motion.div>
  );
};

export default RecordsScreen;
