import React, { useState, useEffect } from 'react';
import { Signal, Wifi, Battery, Coins } from 'lucide-react';
import { useAppStore } from '@/stores/appStore';

const StatusBar: React.FC = () => {
  const [time, setTime] = useState(new Date());
  const { points, updateAppOpenTime } = useAppStore();

  useEffect(() => {
    const interval = setInterval(() => {
      setTime(new Date());
      updateAppOpenTime();
    }, 1000);
    return () => clearInterval(interval);
  }, [updateAppOpenTime]);

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString('en-US', {
      hour: 'numeric',
      minute: '2-digit',
      hour12: true
    });
  };

  return (
    <div className="absolute top-0 left-0 right-0 h-[44px] flex items-center justify-between px-6 pt-[30px] z-40 bg-transparent py-[31px] mx-[14px]">

      <span className="text-sm font-semibold text-foreground">
        {formatTime(time)}
      </span>

      <div className="flex items-center gap-0.5 bg-primary/10 px-1.5 py-0.5 rounded-full">
        <Coins className="w-3 h-3 text-primary" />
        <span className="text-[10px] font-bold text-primary">{points}</span>
      </div>

      <div className="flex items-center gap-2">
        <Signal className="w-4 h-4 text-foreground" />
        <Wifi className="w-4 h-4 text-foreground" />
        <Battery className="w-5 h-4 text-foreground" />
      </div>
    </div>
  );
};

export default StatusBar;