// Challenges/Achievements configuration with emoji and item rewards

import type { ItemDefinition } from './items';

export type RewardType = 'coins' | 'emoji' | 'item';

export interface ChallengeReward {
  type: RewardType;
  // For coins
  points?: number;
  // For emoji
  emojiId?: string;
  // For item
  itemId?: string;
  itemQuantity?: number;
}

export interface Challenge {
  id: string;
  title: string;
  description: string;
  descriptionKo: string;
  unlockCondition: ChallengeCondition;
  reward: ChallengeReward;
  // Legacy support - points only
  rewardPoints?: number;
}

export type ChallengeCondition =
  | { type: 'pomodoro_count'; count: number }
  | { type: 'todo_count'; count: number }
  | { type: 'chat_count'; count: number }
  | { type: 'points_earned'; amount: number }
  | { type: 'affinity_reached'; level: number }
  | { type: 'emoji_unlocked'; count: number }
  | { type: 'gift_given'; count: number }
  | { type: 'consecutive_days'; days: number }
  | { type: 'usage_minutes'; minutes: number };

