// Get challenge progress (0-100)
export function getChallengeProgress(
  condition: ChallengeCondition,
  state: {
    pomodoroSessionsCompleted: number;
    completedTodoRecords: { length: number };
    chatMessageCount: number;
    totalPointsEarned: number;
    characterProfile: { affinity: number };
    giftGivenCount: number;
    consecutiveDays: number;
    emojis: { unlocked: boolean }[];
    totalUsageMinutes: number;
  }
): number {
  let current = 0;
  let target = 1;

  switch (condition.type) {
    case 'pomodoro_count':
      current = state.pomodoroSessionsCompleted;
      target = condition.count;
      break;
    case 'todo_count':
      current = state.completedTodoRecords.length;
      target = condition.count;
      break;
    case 'chat_count':
      current = state.chatMessageCount;
      target = condition.count;
      break;
    case 'points_earned':
      current = state.totalPointsEarned;
      target = condition.amount;
      break;
    case 'affinity_reached':
      current = state.characterProfile.affinity;
      target = condition.level;
      break;
    case 'gift_given':
      current = state.giftGivenCount;
      target = condition.count;
      break;
    case 'consecutive_days':
      current = state.consecutiveDays;
      target = condition.days;
      break;
    case 'emoji_unlocked':
      current = state.emojis.filter(e => e.unlocked).length;
      target = condition.count;
      break;
    case 'usage_minutes':
      current = state.totalUsageMinutes;
      target = condition.minutes;
      break;
  }

  return Math.min(100, Math.floor((current / target) * 100));
}

// Get display text for challenge progress
export function getChallengeProgressText(
  condition: ChallengeCondition,
  state: {
    pomodoroSessionsCompleted: number;
    completedTodoRecords: { length: number };
    chatMessageCount: number;
    totalPointsEarned: number;
    characterProfile: { affinity: number };
    giftGivenCount: number;
    consecutiveDays: number;
    emojis: { unlocked: boolean }[];
    totalUsageMinutes: number;
  }
): string {
  let current = 0;
  let target = 1;

  switch (condition.type) {
    case 'pomodoro_count':
      current = state.pomodoroSessionsCompleted;
      target = condition.count;
      return `${current}/${target}`;
    case 'todo_count':
      current = state.completedTodoRecords.length;
      target = condition.count;
      return `${current}/${target}`;
    case 'chat_count':
      current = state.chatMessageCount;
      target = condition.count;
      return `${current}/${target}`;
    case 'points_earned':
      current = state.totalPointsEarned;
      target = condition.amount;
      return `${current}/${target}`;
    case 'affinity_reached':
      current = state.characterProfile.affinity;
      target = condition.level;
      return `${current}/${target}`;
    case 'gift_given':
      current = state.giftGivenCount;
      target = condition.count;
      return `${current}/${target}`;
    case 'consecutive_days':
      current = state.consecutiveDays;
      target = condition.days;
      return `${current}/${target} days`;
    case 'emoji_unlocked':
      current = state.emojis.filter(e => e.unlocked).length;
      target = condition.count;
      return `${current}/${target}`;
    case 'usage_minutes':
      current = state.totalUsageMinutes;
      target = condition.minutes;
      return `${current}/${target} min`;
  }
}
