// Hook to manage auto message scheduling with "please reply" behavior
import { useEffect, useRef, useCallback } from 'react';
import { useAppStore } from '@/stores/appStore';
import { getRandomAutoMessage, resetLastAutoMessage } from '@/lib/autoMessageCatalog';
import { getRandomDelayForPreset } from '@/lib/autoMessagePresets';
import { pickRandomPleaseReplyMessages, ToneKey } from '@/lib/toneCatalogs';

const CONSECUTIVE_THRESHOLD = 5;
const PLEASE_REPLY_COUNT = 3;
const PLEASE_REPLY_WINDOW_MS = 60 * 1000;

export function useAutoMessage() {
  const autoMessageEnabled = useAppStore(state => state.autoMessageEnabled);
  const autoMessagePreset = useAppStore(state => state.autoMessagePreset);
  const isPomodoroRunning = useAppStore(state => state.isPomodoroRunning);
  const addCharacterMessage = useAppStore(state => state.addCharacterMessage);
  const currentScreen = useAppStore(state => state.currentScreen);
  const setHasUnreadMessages = useAppStore(state => state.setHasUnreadMessages);
  const setAutoMessageEnabled = useAppStore(state => state.setAutoMessageEnabled);
  const messages = useAppStore(state => state.messages);
  const characterProfile = useAppStore(state => state.characterProfile);
  const playerProfile = useAppStore(state => state.playerProfile);
  const voiceTone = (characterProfile?.voiceTone ?? 'default') as ToneKey;
  const playerName = playerProfile?.name ?? '';
  
  const timeoutRef = useRef<number | null>(null);
  const pendingMessageRef = useRef<boolean>(false);
  const consecutiveAutoMessagesRef = useRef<number>(0);
  const pleaseReplyCountRef = useRef<number>(0);
  const lastUserMessageCountRef = useRef<number>(0);
  
  // Track user messages to reset counters
  useEffect(() => {
    const userMessageCount = messages.filter(m => m.isUser).length;
    if (userMessageCount > lastUserMessageCountRef.current) {
      // User replied! Reset counters
      lastUserMessageCountRef.current = userMessageCount;
      consecutiveAutoMessagesRef.current = 0;
      pleaseReplyCountRef.current = 0;
      // Reset last auto message tracking to allow any message after user reply
      resetLastAutoMessage();
    }
  }, [messages]);
  
  // Clear timeout helper
  const clearScheduledTimeout = useCallback(() => {
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
      timeoutRef.current = null;
    }
  }, []);
  
  // Schedule next message
  useEffect(() => {
    clearScheduledTimeout();
    
    if (!autoMessageEnabled || isPomodoroRunning) {
      if (isPomodoroRunning) {
        pendingMessageRef.current = true;
      }
      return;
    }
    
    const scheduleMessage = () => {
      const delay = getRandomDelayForPreset(autoMessagePreset);
      timeoutRef.current = window.setTimeout(() => {
        consecutiveAutoMessagesRef.current++;
        
        // Check if we've hit the threshold for "please reply" sequence
        if (consecutiveAutoMessagesRef.current >= CONSECUTIVE_THRESHOLD && pleaseReplyCountRef.current === 0) {
          // Get tone-specific please reply messages
          const selectedMessages = pickRandomPleaseReplyMessages(voiceTone, PLEASE_REPLY_COUNT, playerName);
          const intervalMs = PLEASE_REPLY_WINDOW_MS / PLEASE_REPLY_COUNT;
          
          selectedMessages.forEach((msg, index) => {
            setTimeout(() => {
              addCharacterMessage(msg);
              if (currentScreen !== 'chat') {
                setHasUnreadMessages(true);
              }
              pleaseReplyCountRef.current++;
              
              if (pleaseReplyCountRef.current >= PLEASE_REPLY_COUNT) {
                setAutoMessageEnabled(false);
                consecutiveAutoMessagesRef.current = 0;
                pleaseReplyCountRef.current = 0;
              }
            }, intervalMs * index);
          });
        } else {
          // Get tone-specific auto message
          const message = getRandomAutoMessage(voiceTone, playerName);
          addCharacterMessage(message);
          
          if (currentScreen !== 'chat') {
            setHasUnreadMessages(true);
          }
          
          // Schedule next
          scheduleMessage();
        }
      }, delay);
    };
    
    scheduleMessage();
    
    return clearScheduledTimeout;
  }, [autoMessageEnabled, autoMessagePreset, isPomodoroRunning, addCharacterMessage, currentScreen, setHasUnreadMessages, setAutoMessageEnabled, clearScheduledTimeout, voiceTone, playerName]);
  
  // Handle pomodoro ending with pending message
  useEffect(() => {
    if (!isPomodoroRunning && pendingMessageRef.current && autoMessageEnabled) {
      pendingMessageRef.current = false;
      const message = getRandomAutoMessage(voiceTone, playerName);
      addCharacterMessage(message);
      if (currentScreen !== 'chat') {
        setHasUnreadMessages(true);
      }
    }
  }, [isPomodoroRunning, autoMessageEnabled, addCharacterMessage, currentScreen, setHasUnreadMessages, voiceTone]);
  
  return null;
}
