// Auto message catalog for random character-initiated messages
// Now delegates to toneCatalogs for tone-aware messages

import { 
  ToneKey, 
  AutoMessageEntry, 
  getCatalogSection, 
  getRandomAutoMessageForTone 
} from './toneCatalogs';
import { processTemplate } from './templateText';

// Storage key for user customizations (per tone)
const STORAGE_KEY_PREFIX = 'auto-message-catalog';

// Track last message to prevent back-to-back repeats
let lastAutoMessageText: string | null = null;

// Get storage key for a specific tone
function getStorageKey(tone: ToneKey): string {
  return `${STORAGE_KEY_PREFIX}-${tone}`;
}

// Get messages from localStorage or defaults for a specific tone
export function getAutoMessages(tone: ToneKey = 'default'): AutoMessageEntry[] {
  try {
    const stored = localStorage.getItem(getStorageKey(tone));
    if (stored) {
      const parsed = JSON.parse(stored);
      if (Array.isArray(parsed) && parsed.length > 0) {
        return parsed;
      }
    }
  } catch (e) {
    console.error('Failed to load auto messages:', e);
  }
  return getCatalogSection(tone, 'autoMessages');
}

// Save messages to localStorage for a specific tone
export function saveAutoMessages(messages: AutoMessageEntry[], tone: ToneKey = 'default'): void {
  try {
    localStorage.setItem(getStorageKey(tone), JSON.stringify(messages));
  } catch (e) {
    console.error('Failed to save auto messages:', e);
  }
}

// Reset to defaults for a specific tone
export function resetAutoMessages(tone: ToneKey = 'default'): void {
  localStorage.removeItem(getStorageKey(tone));
}

// Weighted random selection for a specific tone (with playerName interpolation)
// Prevents back-to-back repeats
export function getRandomAutoMessage(tone: ToneKey = 'default', playerName?: string): string {
  // Check if user has customized messages for this tone
  try {
    const stored = localStorage.getItem(getStorageKey(tone));
    if (stored) {
      const parsed = JSON.parse(stored);
      if (Array.isArray(parsed) && parsed.length > 0) {
        // Filter out last message if we have more than 1 option
        const availableMessages = parsed.length > 1 
          ? parsed.filter((m: AutoMessageEntry) => m.text !== lastAutoMessageText)
          : parsed;
        
        const totalWeight = availableMessages.reduce((sum: number, m: AutoMessageEntry) => sum + m.weight, 0);
        let random = Math.random() * totalWeight;
        
        for (const msg of availableMessages) {
          random -= msg.weight;
          if (random <= 0) {
            const result = processTemplate(msg.text, { playerName });
            lastAutoMessageText = msg.text;
            return result;
          }
        }
        const result = processTemplate(availableMessages[0]?.text ?? 'Hey! ', { playerName });
        lastAutoMessageText = availableMessages[0]?.text ?? null;
        return result;
      }
    }
  } catch (e) {
    console.error('Failed to load auto messages:', e);
  }
  
  // Fall back to tone catalog defaults (with playerName interpolation and repeat prevention)
  return getRandomAutoMessageForToneNoRepeat(tone, playerName);
}

// Get random auto message with repeat prevention
function getRandomAutoMessageForToneNoRepeat(tone: ToneKey, playerName?: string): string {
  const messages = getCatalogSection(tone, 'autoMessages');
  if (!messages.length) return processTemplate('Hey! ', { playerName });

  // Filter out last message if we have more than 1 option
  const availableMessages = messages.length > 1 
    ? messages.filter(m => m.text !== lastAutoMessageText)
    : messages;

  const totalWeight = availableMessages.reduce((sum, m) => sum + m.weight, 0);
  let random = Math.random() * totalWeight;

  for (const msg of availableMessages) {
    random -= msg.weight;
    if (random <= 0) {
      lastAutoMessageText = msg.text;
      return processTemplate(msg.text, { playerName });
    }
  }

  lastAutoMessageText = availableMessages[0].text;
  return processTemplate(availableMessages[0].text, { playerName });
}

// Reset last message tracking (e.g., when user replies)
export function resetLastAutoMessage(): void {
  lastAutoMessageText = null;
}

// Legacy exports for backwards compatibility
export type { AutoMessageEntry } from './toneCatalogs';
export { getCatalogSection as getDefaultAutoMessages } from './toneCatalogs';
