export function generateId(): string {
  return `${Date.now().toString(36)}-${Math.random().toString(36).slice(2, 8)}`;
}

function isPreferredItem(itemName: string, itemCategory: ItemCategory, zodiac: ZodiacSign | null): boolean {
  if (!zodiac) return false;
  const prefs = zodiacPreferences[zodiac];
  if (!prefs) return false;

  // Map item category to preference array
  let prefArray: string[];
  switch (itemCategory) {
    case "food":
      prefArray = prefs.foods;
      break;
    case "place":
      prefArray = prefs.places;
      break;
    case "music":
      prefArray = prefs.music;
      break;
    case "gift":
      prefArray = prefs.giftItems;
      break;
    default:
      prefArray = [...prefs.foods, ...prefs.music, ...prefs.giftItems];
  }

  return prefArray.some((pref) => {
    const prefName = pref.replace(/[\u{1F300}-\u{1F9FF}]|[\u{2600}-\u{26FF}]|[\u{2700}-\u{27BF}]/gu, "").trim();
    return itemName.includes(prefName) || prefName.includes(itemName);
  });
}

function isChallengeConditionMet(condition: ChallengeCondition, state: AppState): boolean {
  switch (condition.type) {
    case "pomodoro_count":
      return state.pomodoroSessionsCompleted >= condition.count;
    case "todo_count":
      return state.completedTodoRecords.length >= condition.count;
    case "chat_count":
      return state.chatMessageCount >= condition.count;
    case "points_earned":
      return state.totalPointsEarned >= condition.amount;
    case "affinity_reached":
      return state.characterProfile.affinity >= condition.level;
    case "gift_given":
      return state.giftGivenCount >= condition.count;
    case "consecutive_days":
      return state.consecutiveDays >= condition.days;
    case "emoji_unlocked":
      return state.emojis.filter((e) => e.unlocked).length >= condition.count;
    case "usage_minutes":
      return state.totalUsageMinutes >= condition.minutes;
    default:
      return false;
  }
}

export const useAppStore = create<AppState>()(
  persist(
    (set, get) => ({
      onboardingCompleted: false,
      setOnboardingCompleted: (completed) => {
        set({ onboardingCompleted: completed });

        //    
        if (!completed) return;

        const state = get();

        //      (: import data )
        if (state.messages.length > 0) return;

        const timeOfDay = getTimeOfDay();
        const tone = state.getCharacterTone(); //   tone    
        const playerName = state.playerProfile.name;

        const greeting = getGreetingForTone(tone, timeOfDay, playerName);

        set({
          messages: [{ id: generateId(), content: greeting, isUser: false, timestamp: new Date() }],
          chatCreatedAt: Date.now(),
        });
      },
      currentScreen: "home",
      setCurrentScreen: (screen) => {
        set({ currentScreen: screen });
        if (screen === "chat") {
          set({ hasUnreadMessages: false });
        }
        get().checkAndGrantHourlyReward();
        get().updateAppOpenTime();
      },
      playerProfile: initialPlayerProfile,
      setPlayerName: (name) => set((state) => ({ playerProfile: { ...state.playerProfile, name } })),
      setPlayerBirthday: (birthday) => set((state) => ({ playerProfile: { ...state.playerProfile, birthday } })),
      setPlayerFavoriteFood: (favoriteFood) =>
        set((state) => ({ playerProfile: { ...state.playerProfile, favoriteFood } })),
      setPlayerFavoritePlace: (favoritePlace) =>
        set((state) => ({ playerProfile: { ...state.playerProfile, favoritePlace } })),
      setPlayerFavoriteMusic: (favoriteMusic) =>
        set((state) => ({ playerProfile: { ...state.playerProfile, favoriteMusic } })),
      points: 0,
      totalPointsEarned: 0,
      addPoints: (amount) => {
        set((state) => ({ points: state.points + amount, totalPointsEarned: state.totalPointsEarned + amount }));
        get().checkAllChallenges();
      },
      spendPoints: (amount) => {
        const current = get().points;
        if (current < amount) return false;
        set({ points: current - amount });
        return true;
      },
      appOpenAccumulatedMs: 0,
      lastAppOpenTimestamp: null,
      updateAppOpenTime: () => {
        const now = Date.now();
        const last = get().lastAppOpenTimestamp;
        if (last) {
          const elapsed = now - last;
          if (elapsed > 0 && elapsed < 5 * 60 * 1000) {
            const newAccumulated = get().appOpenAccumulatedMs + elapsed;
            const hourMs = 60 * 60 * 1000;
            const previousHours = Math.floor(get().appOpenAccumulatedMs / hourMs);
            const newHours = Math.floor(newAccumulated / hourMs);
            if (newHours > previousHours) {
              const hoursToAward = newHours - previousHours;
              get().addPoints(hoursToAward * POINTS_CONFIG.APP_OPEN_HOUR);
              get().addNotification(
                notifyT().pointsForStayingActive(hoursToAward * POINTS_CONFIG.APP_OPEN_HOUR),
                "points",
              );
            }
            set({ appOpenAccumulatedMs: newAccumulated });
          }
        }
        set({ lastAppOpenTimestamp: now });
      },
      chatMessageCount: 0,
      giftGivenCount: 0,
      completedChallenges: [],
      checkAndCompleteChallenge: (challengeId) => {
        const challenge = CHALLENGES.find((c) => c.id === challengeId);
        if (!challenge) return;
        const alreadyCompleted = get().completedChallenges.some((c) => c.id === challengeId);
        if (alreadyCompleted) return;
        if (isChallengeConditionMet(challenge.unlockCondition, get())) {
          set((state) => ({
            completedChallenges: [
              ...state.completedChallenges,
              { id: challengeId, completedAt: Date.now(), claimed: false },
            ],
          }));
          // Auto-claim for now
          get().claimChallengeReward(challengeId);
        }
      },
      claimChallengeReward: (challengeId) => {
        const challenge = CHALLENGES.find((c) => c.id === challengeId);
        if (!challenge) return;
        const completed = get().completedChallenges.find((c) => c.id === challengeId);
        if (!completed || completed.claimed) return;

        const reward = challenge.reward;
        if (reward.type === "coins" && reward.points) {
          get().addPoints(reward.points);
          get().addNotification(notifyT().challengeCompleted(challenge.title, reward.points), "challenge");
        } else if (reward.type === "emoji" && reward.emojiId) {
          get().unlockEmoji(reward.emojiId);
          get().addNotification(notifyT().challengeCompletedEmoji(challenge.title, reward.emojiId), "challenge");
        } else if (reward.type === "item" && reward.itemId) {
          get().addItemToInventory(reward.itemId, reward.itemQuantity || 1);
          get().addNotification(notifyT().challengeCompletedItem(challenge.title), "challenge");
        }

        set((state) => ({
          completedChallenges: state.completedChallenges.map((c) =>
            c.id === challengeId ? { ...c, claimed: true } : c,
          ),
        }));
      },
      checkAllChallenges: () => {
        CHALLENGES.forEach((challenge) => get().checkAndCompleteChallenge(challenge.id));
      },
      addItemToInventory: (itemId, quantity = 1) => {
        const itemDef = findItemById(itemId);
        if (!itemDef) return;
        for (let i = 0; i < quantity; i++) {
          const newItem: InventoryItem = {
            id: `${itemDef.id}-${Date.now()}-${i}`,
            name: itemDef.name,
            icon: itemDef.icon,
            category: itemDef.category,
            receivedAt: Date.now(),
          };
          set((state) => ({ inventory: [...state.inventory, newItem] }));
        }
      },
      purchaseRandomBox: (type) => {
        const prices = { emoji: 100, food: 300, music: 300 };
        const price = prices[type];
        if (get().points < price) return { success: false, message: `Not enough points! Need ${price} points.` };
        if (type === "emoji") {
          const result = get().unlockRandomEmoji();
          if (result.success) {
            get().spendPoints(price);
            return { success: true, message: `Unlocked ${result.emoji}!` };
          }
          return { success: false, message: result.message };
        } else if (type === "food") {
          const itemDef = getRandomItem(foodItems);
          const newItem: InventoryItem = {
            id: `${itemDef.id}-${Date.now()}`,
            name: itemDef.name,
            icon: itemDef.icon,
            category: itemDef.category,
            receivedAt: Date.now(),
          };
          get().spendPoints(price);
          set((state) => ({ inventory: [...state.inventory, newItem] }));
          return { success: true, message: `Got ${itemDef.icon} ${itemDef.name}!` };
        } else if (type === "music") {
          const itemDef = getRandomItem(musicItems);
          const newItem: InventoryItem = {
            id: `${itemDef.id}-${Date.now()}`,
            name: itemDef.name,
            icon: itemDef.icon,
            category: itemDef.category,
            receivedAt: Date.now(),
          };
          get().spendPoints(price);
          set((state) => ({ inventory: [...state.inventory, newItem] }));
          return { success: true, message: `Got ${itemDef.icon} ${itemDef.name}!` };
        }
        return { success: false, message: "Unknown box type" };
      },

      messages: [],
      chatCreatedAt: 0,

      addMessage: (content, isUser) => {
        const newMessage: Message = { id: generateId(), content, isUser, timestamp: new Date() };
        set((state) => ({ messages: [...state.messages, newMessage] }));
        if (isUser) {
          set((state) => ({ chatMessageCount: state.chatMessageCount + 1 }));
          get().addPoints(POINTS_CONFIG.CHAT_MESSAGE);
          get().markDailyAction("conversation");
        }
      },
      addScriptedMessage: (content, isUser) => {
        if (isUser) {
          // User scripted message - add immediately with isScripted flag
          const newMessage: Message = {
            id: generateId(),
            content,
            isUser: true,
            timestamp: new Date(),
            isScripted: true,
          };
          set((state) => ({ messages: [...state.messages, newMessage] }));
        } else {
          // Character scripted message - add with typing animation
          const typingId = Date.now().toString();
          const typingMessage: Message = {
            id: typingId,
            content: "...",
            isUser: false,
            timestamp: new Date(),
            isTyping: true,
            isScripted: true,
          };
          set((state) => ({ messages: [...state.messages, typingMessage] }));
          setTimeout(
            () => {
              set((state) => ({
                messages: state.messages.map((m) => (m.id === typingId ? { ...m, content, isTyping: false } : m)),
              }));
            },
            400 + Math.random() * 300,
          );
        }
      },
      addCharacterMessage: (content: string) => {
        const typingId = Date.now().toString();
        const typingMessage: Message = {
          id: typingId,
          content: "...",
          isUser: false,
          timestamp: new Date(),
          isTyping: true,
        };
        set((state) => ({ messages: [...state.messages, typingMessage] }));
        setTimeout(() => {
          set((state) => ({
            messages: state.messages.map((m) => (m.id === typingId ? { ...m, content, isTyping: false } : m)),
          }));
          if (get().currentScreen !== "chat") {
            set({ hasUnreadMessages: true });
            get().addNotification(notifyT().newMessageFrom(get().characterProfile.name), "message");
          }
        }, getTypingDelay());
      },
      addSystemMessage: (content: string) => {
        const systemMessage: Message = {
          id: generateId(),
          content,
          isUser: false,
          timestamp: new Date(),
          isSystem: true,
        };
        set((state) => ({ messages: [...state.messages, systemMessage] }));
      },
      clearMessages: () => {
        set({ messages: [], chatCreatedAt: Date.now() });
      },
      hasUnreadMessages: false,
      setHasUnreadMessages: (value) => set({ hasUnreadMessages: value }),
      // Language
      language: "ko" as Language,
      setLanguage: (lang: Language) => {
        setI18nLanguage(lang);
        set({ language: lang });
      },
      // Auto Message
      autoMessageEnabled: false,
      setAutoMessageEnabled: (enabled) => set({ autoMessageEnabled: enabled }),
      autoMessagePreset: "regular" as const,
      setAutoMessagePreset: (preset) => set({ autoMessagePreset: preset }),
      emojis: initialEmojis,
      unlockEmoji: (emoji) => {
        set((state) => ({ emojis: state.emojis.map((e) => (e.emoji === emoji ? { ...e, unlocked: true } : e)) }));
        get().addNotification(notifyT().newEmojiUnlocked(emoji), "emoji");
        get().checkAllChallenges();
      },
      unlockRandomEmoji: () => {
        const lockedEmojis = get().emojis.filter((e) => !e.unlocked);
        if (lockedEmojis.length === 0) return { success: false, message: "All emojis already unlocked!" };
        const randomEmoji = lockedEmojis[Math.floor(Math.random() * lockedEmojis.length)];
        get().unlockEmoji(randomEmoji.emoji);
        return { success: true, emoji: randomEmoji.emoji, message: `Unlocked ${randomEmoji.emoji}!` };
      },
      selectedEmojis: [],
      addSelectedEmoji: (emoji) => {
        if (get().selectedEmojis.length >= EMOJI_SELECTION_LIMIT) {
          // Silently reject - shake animation handled in EmojiPanel
          return false;
        }
        set((state) => ({ selectedEmojis: [...state.selectedEmojis, emoji] }));
        return true;
      },
      removeSelectedEmoji: (index) =>
        set((state) => ({ selectedEmojis: state.selectedEmojis.filter((_, i) => i !== index) })),
      clearSelectedEmojis: () => set({ selectedEmojis: [] }),
      sendEmojis: () => {
        const emojis = get().selectedEmojis;
        if (emojis.length === 0) return;
        const tone = get().getCharacterTone();
        const result = processEmojis(
          emojis,
          { playerName: get().playerProfile.name, characterName: get().characterProfile.name },
          tone,
        );
        // Affinity: +1 per conversation (one send), NOT per emoji
        const affinityGain = 1;
        const newMessage = { id: generateId(), content: emojis.join(""), isUser: true, timestamp: new Date() };
        set((state) => ({ messages: [...state.messages, newMessage], chatMessageCount: state.chatMessageCount + 1 }));
        get().addPoints(POINTS_CONFIG.CHAT_MESSAGE);
        get().markDailyAction("conversation");
        if (result.reply) {
          const typingId = (Date.now() + 1).toString();
          const typingMessage = { id: typingId, content: "...", isUser: false, timestamp: new Date(), isTyping: true };
          set((state) => ({ messages: [...state.messages, typingMessage] }));
          setTimeout(() => {
            set((state) => ({
              messages: state.messages.map((m) =>
                m.id === typingId ? { ...m, content: result.reply, isTyping: false } : m,
              ),
            }));
            if (get().currentScreen !== "chat") {
              set({ hasUnreadMessages: true });
              get().addNotification(notifyT().newMessageFrom(get().characterProfile.name), "message");
            }
          }, getTypingDelay());
        }
        get().addAffinity(affinityGain);
        get().clearSelectedEmojis();
      },
      pomodoroMinutes: 25,
      pomodoroSeconds: 0,
      isPomodoroRunning: false,
      isBreakMode: false,
      pomodoroSessionsCompleted: 0,
      taskDuration: 25,
      breakDuration: 5,
      breakEnabled: true,
      autoStartBreak: true,
      pendingBreak: false,
      pomodoroMuted: false,
      setPomodoroTime: (minutes, seconds) => set({ pomodoroMinutes: minutes, pomodoroSeconds: seconds }),
      setIsPomodoroRunning: (value) => set({ isPomodoroRunning: value }),
      setIsBreakMode: (value) => set({ isBreakMode: value }),
      setTaskDuration: (minutes) => set({ taskDuration: minutes }),
      setBreakDuration: (minutes) => set({ breakDuration: minutes }),
      setBreakEnabled: (enabled) => set({ breakEnabled: enabled }),
      setAutoStartBreak: (enabled) => set({ autoStartBreak: enabled }),
      setPomodoroMuted: (muted) => set({ pomodoroMuted: muted }),
      startBreak: () => {
        const breakDur = get().breakDuration;
        set({
          isBreakMode: true,
          pomodoroMinutes: breakDur,
          pomodoroSeconds: 0,
          isPomodoroRunning: true,
          pendingBreak: false,
        });
      },
      resetToFocus: () => {
        const taskDur = get().taskDuration;
        set({
          isBreakMode: false,
          pomodoroMinutes: taskDur,
          pomodoroSeconds: 0,
          isPomodoroRunning: false,
          pendingBreak: false,
        });
      },
      completePomodoroSession: () => {
        const newCount = get().pomodoroSessionsCompleted + 1;
        const sessionDuration = get().taskDuration;
        const record: PomodoroRecord = {
          id: generateId(),
          completedAt: getDateString(),
          timestamp: Date.now(),
          durationMinutes: sessionDuration,
        };
        set((state) => ({ pomodoroSessionsCompleted: newCount, pomodoroRecords: [...state.pomodoroRecords, record] }));
        const timerPoints = sessionDuration * POINTS_CONFIG.TIMER_PER_MINUTE;
        get().addPoints(timerPoints);
        get().addNotification(notifyT().focusRewardPoints(timerPoints), "pomodoro");
        const affinity = get().characterProfile.affinity;
        const tier = getAffinityTier(affinity);
        const tone = get().getCharacterTone();
        const playerName = get().playerProfile.name;
        const reply = getCompletionReplyForTone(tone, "pomodoro", tier, playerName);
        get().addCharacterMessage(reply);
        // get().addNotification(`${get().characterProfile.name} is proud of you! `, "pomodoro");
        // get().addAffinity(5);
        get().checkAllChallenges();
        get().markDailyAction("pomodoro");
        const breakEnabled = get().breakEnabled;
        const autoStartBreak = get().autoStartBreak;
        if (breakEnabled) {
          if (autoStartBreak) {
            get().startBreak();
          } else {
            set({ isPomodoroRunning: false, pendingBreak: true });
          }
        } else {
          set({
            isBreakMode: false,
            pomodoroMinutes: get().taskDuration,
            pomodoroSeconds: 0,
            isPomodoroRunning: false,
          });
        }
      },
      completeBreak: () => {
        set({
          isBreakMode: false,
          pomodoroMinutes: get().taskDuration,
          pomodoroSeconds: 0,
          isPomodoroRunning: false,
          pendingBreak: false,
        });
      },
      pomodoroRecords: [],
      deletePomodoroRecord: (id) =>
        set((state) => ({ pomodoroRecords: state.pomodoroRecords.filter((r) => r.id !== id) })),
      deleteRecordsByDate: (date) =>
        set((state) => ({
          pomodoroRecords: state.pomodoroRecords.filter((r) => r.completedAt !== date),
          completedTodoRecords: state.completedTodoRecords.filter((r) => r.completedAt !== date),
        })),
      todos: [],
      addTodo: (text) => set((state) => ({ todos: [...state.todos, { id: generateId(), text, completed: false }] })),
      toggleTodo: (id) => {
        const todo = get().todos.find((t) => t.id === id);
        if (todo && !todo.completed) {
          const record: CompletedTodoRecord = {
            id: todo.id,
            text: todo.text,
            completedAt: getDateString(),
            timestamp: Date.now(),
          };
          set((state) => ({
            todos: state.todos.map((t) => (t.id === id ? { ...t, completed: true, completedAt: getDateString() } : t)),
            completedTodoRecords: [...state.completedTodoRecords, record],
          }));
          // get().addAffinity(2);
          get().checkAllChallenges();
          get().markDailyAction("todo");
          const affinity = get().characterProfile.affinity;
          const tier = getAffinityTier(affinity);
          const tone = get().getCharacterTone();
          const playerName = get().playerProfile.name;
          const reply = getCompletionReplyForTone(tone, "todo", tier, playerName);
          get().addCharacterMessage(reply);
        } else {
          set((state) => ({
            todos: state.todos.map((t) => (t.id === id ? { ...t, completed: false, completedAt: undefined } : t)),
          }));
        }
      },
      removeTodo: (id) => set((state) => ({ todos: state.todos.filter((t) => t.id !== id) })),
      showCompletedTodos: false,
      setShowCompletedTodos: (value) => set({ showCompletedTodos: value }),
      completedTodoRecords: [],
      deleteTodoRecord: (id) =>
        set((state) => ({ completedTodoRecords: state.completedTodoRecords.filter((r) => r.id !== id) })),
      totalUsageMinutes: 0,
      consecutiveDays: 0,
      lastActiveDate: null,
      incrementUsageTime: () => {
        const newTotal = get().totalUsageMinutes + 1;
        set({ totalUsageMinutes: newTotal });
        get().checkAllChallenges();
      },
      updateConsecutiveDays: () => {
        const today = new Date().toDateString();
        const lastActive = get().lastActiveDate;
        if (lastActive === today) return;
        const yesterday = new Date();
        yesterday.setDate(yesterday.getDate() - 1);
        if (lastActive === yesterday.toDateString()) {
          const newStreak = get().consecutiveDays + 1;
          set({ consecutiveDays: newStreak, lastActiveDate: today });
          get().checkAllChallenges();
        } else {
          set({ consecutiveDays: 1, lastActiveDate: today });
        }
      },
      characterProfile: initialCharacterProfile,
      setCharacterName: (name) => set((state) => ({ characterProfile: { ...state.characterProfile, name } })),
      setCharacterAvatar: (avatar) => set((state) => ({ characterProfile: { ...state.characterProfile, avatar } })),
      setCharacterBirthday: (birthday) =>
        set((state) => ({ characterProfile: { ...state.characterProfile, birthday } })),
      setCharacterVoiceTone: (tone) =>
        set((state) => ({ characterProfile: { ...state.characterProfile, voiceTone: tone } })),
      addAffinity: (amount) => {
        const currentProfile = get().characterProfile;
        const newAffinity = Math.min(100, currentProfile.affinity + amount);
        const newUnlocks = { ...currentProfile.unlocks };
        const zodiac = get().getZodiacSign();
        const tone = get().getCharacterTone();
        const playerName = get().playerProfile.name;
        const unlockMessages = getZodiacUnlockMessagesForTone(tone, zodiac, playerName);
        if (!currentProfile.unlocks.food && newAffinity >= AFFINITY_THRESHOLDS.food) {
          newUnlocks.food = true;
          get().addNotification(notifyT().favoriteFoodRevealed(currentProfile.name), "unlock");
          setTimeout(() => {
            get().addCharacterMessage(unlockMessages.food);
          }, 500);
        }
        if (!currentProfile.unlocks.place && newAffinity >= AFFINITY_THRESHOLDS.place) {
          newUnlocks.place = true;
          get().addNotification(notifyT().favoritePlaceRevealed(currentProfile.name), "unlock");
          setTimeout(() => {
            get().addCharacterMessage(unlockMessages.place);
          }, 500);
        }
        if (!currentProfile.unlocks.music && newAffinity >= AFFINITY_THRESHOLDS.music) {
          newUnlocks.music = true;
          get().addNotification(notifyT().favoriteMusicRevealed(currentProfile.name), "unlock");
          setTimeout(() => {
            get().addCharacterMessage(unlockMessages.music);
          }, 500);
        }
        set((state) => ({
          characterProfile: { ...state.characterProfile, affinity: newAffinity, unlocks: newUnlocks },
        }));
        get().checkAllChallenges();
      },
      getZodiacSign: () => {
        const b = get().characterProfile.birthday;
        if (/^\d{2}-\d{2}$/.test(b)) {
          return getZodiacSign(`2000-${b}`);
        }
        return getZodiacSign(b);
      },
      // Theme
      selectedTheme: "default",
      colorOverrides: {},
      setSelectedTheme: (theme) => {
        set({ selectedTheme: theme });
        get().applyTheme();
      },
      setColorOverride: (key, value) => {
        set((state) => ({ colorOverrides: { ...state.colorOverrides, [key]: value } }));
        get().applyTheme();
      },
      resetColorOverrides: () => {
        set({ colorOverrides: {} });
        get().applyTheme();
      },
      applyTheme: () => {
        const { selectedTheme, colorOverrides } = get();
        applyThemePalette(themePalettes[selectedTheme], colorOverrides);
      },
      chatWallpaper: "default",
      backgroundGradient: "none",
      setChatWallpaper: (wallpaper) => set({ chatWallpaper: wallpaper }),
      setBackgroundGradient: (gradient) => set({ backgroundGradient: gradient }),
      // Sound
      soundEnabled: true,
      soundVolume: 50,
      setSoundEnabled: (enabled) => set({ soundEnabled: enabled }),
      setSoundVolume: (volume) => set({ soundVolume: volume }),
      notifications: [],
      addNotification: (message, type) => {
        const notification = { id: generateId(), message, type };
        set((state) => ({ notifications: [...state.notifications, notification] }));
        setTimeout(() => {
          get().removeNotification(notification.id);
        }, 3000);
      },
      removeNotification: (id) => set((state) => ({ notifications: state.notifications.filter((n) => n.id !== id) })),
      inventory: [],
      lastRewardTimestamp: null,
      checkAndGrantHourlyReward: () => {
        const lastReward = get().lastRewardTimestamp;
        const now = Date.now();

        if (lastReward === null) {
          set({ lastRewardTimestamp: now });
          return;
        }

        if (now - lastReward >= HOURLY_REWARD_INTERVAL) {
          const favorites = get().getEffectiveFavorites();
          const itemDef = getHourlyRewardItem(favorites);

          const newItem: InventoryItem = {
            id: `${itemDef.id}-${generateId()}`,
            name: itemDef.name,
            icon: itemDef.icon,
            category: itemDef.category,
            receivedAt: now,
          };

          set((state) => ({
            inventory: [...state.inventory, newItem],
            lastRewardTimestamp: now,
          }));

          get().addNotification(notifyT().receivedGift(itemDef.icon, itemDef.name), "reward");
        }
      },
      removeFromInventory: (itemId: string) => {
        set((state) => ({ inventory: state.inventory.filter((item) => item.id !== itemId) }));
      },
      giveGift: (itemId: string) => {
        const item = get().inventory.find((i) => i.id === itemId);
        if (!item) return;
        const zodiac = get().getZodiacSign();
        const isPreferred = isPreferredItem(item.name, item.category, zodiac);
        const affinityGain = isPreferred ? 8 : 3;
        get().removeFromInventory(itemId);
        get().addAffinity(affinityGain);
        set((state) => ({ giftGivenCount: state.giftGivenCount + 1 }));
        get().checkAllChallenges();
        get().markDailyAction("gift");
        const tone = get().getCharacterTone();
        const playerName = get().playerProfile.name;
        const reply = getGiftReplyForTone(tone, item.name, isPreferred, playerName);
        get().addCharacterMessage(reply);
        get().addSystemMessage(t().giftGiven(item.name, affinityGain));
      },
      lastFortuneDate: null,
      canUseFortune: () => {
        const lastDate = get().lastFortuneDate;
        const today = getDateString();
        if (lastDate === today) return false;
        return true;
      },
      useFortune: () => {
        if (!get().canUseFortune()) return null;
        const tone = get().getCharacterTone();
        const playerName = get().playerProfile.name;
        const fortune = getFortuneForTone(tone, playerName);
        const today = getDateString();
        set({ lastFortuneDate: today });
        get().addCharacterMessage(` : ${fortune.label}\n${fortune.message}`);
        return fortune;
      },
      getEffectiveFavorites: () => {
        const player = get().playerProfile;
        const zodiac = get().getZodiacSign();
        const prefs = zodiac ? zodiacPreferences[zodiac] : null;
        return {
          food: player.favoriteFood || (prefs?.foods?.[0] ?? ""),
          place: player.favoritePlace || (prefs?.places?.[0] ?? ""),
          music: player.favoriteMusic || (prefs?.music?.[0] ?? ""),
        };
      },
      getCharacterTone: () => get().characterProfile.voiceTone || "default",
      // Daily Challenges
      dailyChallenges: null,
      dailyStats: {
        dateStamp: getLocalDateString(),
        giftGivenToday: false,
        customMessageAddedToday: false,
        conversationCountToday: 0,
        pomodoroCountToday: 0,
        todoCompletedToday: 0,
        talkUsedToday: false,
      },
      initializeDailyChallenges: () => {
        const current = get().dailyChallenges;
        const stats = get().dailyStats;
        const today = getLocalDateString();

        // Reset daily stats if new day
        if (stats.dateStamp !== today) {
          set({
            dailyStats: {
              dateStamp: today,
              giftGivenToday: false,
              customMessageAddedToday: false,
              conversationCountToday: 0,
              pomodoroCountToday: 0,
              todoCompletedToday: 0,
              talkUsedToday: false,
            },
          });
        }

        // Generate new challenges if needed
        if (needsDailyReset(current)) {
          set({ dailyChallenges: generateDailyChallenges() });
        }
      },
      checkAndCompleteDailyChallenges: () => {
        const state = get();
        const dailyChallenges = state.dailyChallenges;
        if (!dailyChallenges) return;

        const todayStats = state.getTodayStats();
        let updated = false;
        let newlyCompleted = 0;

        const updatedChallenges = dailyChallenges.challenges.map((challenge) => {
          if (challenge.completed) return challenge;

          if (isDailyChallengeComplete(challenge.condition, todayStats)) {
            updated = true;
            newlyCompleted++;
            // Award points for completing
            state.addPoints(DAILY_CHALLENGE_REWARD);
            state.addNotification(notifyT().dailyChallengeCompleted(DAILY_CHALLENGE_REWARD), "challenge");
            return { ...challenge, completed: true };
          }
          return challenge;
        });

        if (updated) {
          const allComplete = updatedChallenges.every((c) => c.completed);
          const shouldAwardBonus = allComplete && !dailyChallenges.bonusClaimed;

          set({
            dailyChallenges: {
              ...dailyChallenges,
              challenges: updatedChallenges,
              bonusClaimed: shouldAwardBonus ? true : dailyChallenges.bonusClaimed,
            },
          });

          if (shouldAwardBonus) {
            state.addPoints(DAILY_BONUS_REWARD);
            state.addNotification(notifyT().dailyChallengeBonus(DAILY_BONUS_REWARD), "challenge");
          }
        }
      },
      markDailyAction: (action) => {
        const stats = get().dailyStats;
        const today = getLocalDateString();

        // Reset if new day
        if (stats.dateStamp !== today) {
          set({
            dailyStats: {
              dateStamp: today,
              giftGivenToday: action === "gift",
              customMessageAddedToday: action === "customMessage",
              conversationCountToday: action === "conversation" ? 1 : 0,
              pomodoroCountToday: action === "pomodoro" ? 1 : 0,
              todoCompletedToday: action === "todo" ? 1 : 0,
              talkUsedToday: action === "talk",
            },
          });
        } else {
          set({
            dailyStats: {
              ...stats,
              giftGivenToday: stats.giftGivenToday || action === "gift",
              customMessageAddedToday: stats.customMessageAddedToday || action === "customMessage",
              conversationCountToday: stats.conversationCountToday + (action === "conversation" ? 1 : 0),
              pomodoroCountToday: stats.pomodoroCountToday + (action === "pomodoro" ? 1 : 0),
              todoCompletedToday: stats.todoCompletedToday + (action === "todo" ? 1 : 0),
              talkUsedToday: stats.talkUsedToday || action === "talk",
            },
          });
        }

        // Check if any daily challenges completed
        get().checkAndCompleteDailyChallenges();
      },
      getTodayStats: () => {
        const stats = get().dailyStats;
        const today = getLocalDateString();

        if (stats.dateStamp !== today) {
          return {
            giftGivenToday: false,
            customMessageAddedToday: false,
            conversationCountToday: 0,
            pomodoroCountToday: 0,
            todoCompletedToday: 0,
            talkUsedToday: false,
          };
        }

        return {
          giftGivenToday: stats.giftGivenToday,
          customMessageAddedToday: stats.customMessageAddedToday,
          conversationCountToday: stats.conversationCountToday,
          pomodoroCountToday: stats.pomodoroCountToday,
          todoCompletedToday: stats.todoCompletedToday,
          talkUsedToday: stats.talkUsedToday,
        };
      },
    }),
    {
      name: "companion-app-storage",
      partialize: (state) => ({
        onboardingCompleted: state.onboardingCompleted,
        playerProfile: state.playerProfile,
        messages: state.messages,
        chatCreatedAt: state.chatCreatedAt,
        emojis: state.emojis,
        pomodoroSessionsCompleted: state.pomodoroSessionsCompleted,
        pomodoroRecords: state.pomodoroRecords,
        todos: state.todos,
        completedTodoRecords: state.completedTodoRecords,
        showCompletedTodos: state.showCompletedTodos,
        totalUsageMinutes: state.totalUsageMinutes,
        consecutiveDays: state.consecutiveDays,
        lastActiveDate: state.lastActiveDate,
        selectedTheme: state.selectedTheme,
        colorOverrides: state.colorOverrides,
        chatWallpaper: state.chatWallpaper,
        backgroundGradient: state.backgroundGradient,
        characterProfile: state.characterProfile,
        soundEnabled: state.soundEnabled,
        soundVolume: state.soundVolume,
        taskDuration: state.taskDuration,
        breakDuration: state.breakDuration,
        breakEnabled: state.breakEnabled,
        autoStartBreak: state.autoStartBreak,
        inventory: state.inventory,
        lastRewardTimestamp: state.lastRewardTimestamp,
        lastFortuneDate: state.lastFortuneDate,
        points: state.points,
        totalPointsEarned: state.totalPointsEarned,
        appOpenAccumulatedMs: state.appOpenAccumulatedMs,
        lastAppOpenTimestamp: state.lastAppOpenTimestamp,
        chatMessageCount: state.chatMessageCount,
        giftGivenCount: state.giftGivenCount,
        completedChallenges: state.completedChallenges,
        autoMessageEnabled: state.autoMessageEnabled,
        autoMessagePreset: state.autoMessagePreset,
        dailyChallenges: state.dailyChallenges,
        dailyStats: state.dailyStats,
      }),
      merge: (persistedState, currentState) => {
        const persisted = persistedState as Partial<AppState>;

        // Merge emojis: add new emojis from code, preserve user's unlock progress
        const mergedEmojis = initialEmojis.map((codeEmoji) => {
          const savedEmoji = persisted.emojis?.find((e) => e.emoji === codeEmoji.emoji);
          if (savedEmoji) {
            // Preserve user's unlock status for existing emojis
            return { ...codeEmoji, unlocked: savedEmoji.unlocked };
          }
          // New emoji from code - use default unlock status
          return codeEmoji;
        });

        // Migrate inventory items to remove legacy zodiacCategory
        const migratedInventory = (persisted.inventory ?? []).map((item) => normalizeItemCategory(item));

        return {
          ...currentState,
          ...persisted,
          emojis: mergedEmojis,
          inventory: migratedInventory,
        };
      },
    },
  ),
);
