import type { EmotionTag, IntensityLevel } from './emotion';
import { createEmptyCounts } from './emotion';
import { emojiEmotionMap } from './emojiEmotionMap';
import { processTemplate } from '../templateText';
import type {
  EmotionCounts,
  EmotionEnergy,
  EmotionState,
  EmotionEngineResult,
  ReplyCatalog,
  MixedRule,
  ReversalRule,
} from './types';
import {
  INERTIA_MULTIPLIERS,
  INTENSITY_THRESHOLDS,
  REVERSAL_COOLDOWN_MS,
} from './types';
import { MIXED_RULES, REVERSAL_RULES } from './rules';
import { getCatalogSection, type ToneKey } from '../toneCatalogs';

// Emotion state storage key
const EMOTION_STATE_KEY = 'emotion-engine-state';

/**
 * Get emotion counts from selected emojis
 */
export function getEmotionCountsFromEmojis(emojis: string[]): EmotionCounts {
  const counts = createEmptyCounts();

  for (const emoji of emojis) {
    const emotion = emojiEmotionMap[emoji] ?? 'daily';
    counts[emotion] += 1;
  }

  return counts;
}

/**
 * Calculate intensity level from total count (1-30 -> 1-5)
 */
export function intensityFromTotal(total: number): IntensityLevel {
  const clamped = Math.min(30, Math.max(1, total));

  for (const threshold of INTENSITY_THRESHOLDS) {
    if (clamped <= threshold.max) {
      return threshold.level;
    }
  }

  return 5;
}

/**
 * Get inertia multiplier based on streak count
 */
export function getInertiaMultiplier(streak: number): number {
  if (streak >= 5) return INERTIA_MULTIPLIERS[5];
  return INERTIA_MULTIPLIERS[streak] ?? 1.0;
}

/**
 * Find dominant emotion from counts
 */
export function findDominantEmotion(counts: EmotionCounts): EmotionTag {
  let dominant: EmotionTag = 'daily';
  let maxCount = 0;

  for (const [emotion, count] of Object.entries(counts) as [EmotionTag, number][]) {
    // Prefer non-daily emotions in case of tie
    if (count > maxCount || (count === maxCount && emotion !== 'daily')) {
      maxCount = count;
      dominant = emotion;
    }
  }

  return dominant;
}

/**
 * Check if this is a single-emotion message (only one emotion type)
 */
export function isSingleEmotion(counts: EmotionCounts): boolean {
  let nonZeroCount = 0;

  for (const count of Object.values(counts)) {
    if (count > 0) nonZeroCount++;
    if (nonZeroCount > 1) return false;
  }

  return nonZeroCount === 1;
}

/**
 * Find matching mixed rule based on emotion counts
 */
export function findMatchingMixedRule(counts: EmotionCounts): MixedRule | null {
  for (const rule of MIXED_RULES) {
    const countA = counts[rule.emotionA];
    const countB = counts[rule.emotionB];

    if (countA >= rule.thresholdA && countB >= rule.thresholdB) {
      return rule;
    }
  }

  return null;
}

/**
 * Find matching reversal rule
 */
export function findMatchingReversalRule(
  previousDominant: EmotionTag | null,
  currentDominant: EmotionTag
): ReversalRule | null {
  if (!previousDominant || previousDominant === currentDominant) {
    return null;
  }

  for (const rule of REVERSAL_RULES) {
    if (rule.from === previousDominant && rule.to === currentDominant) {
      return rule;
    }
  }

  return null;
}

const CARRY_MAX = 30;
const CARRY_HALFLIFE_MS = 30 * 1000; // 10 sec
const CARRY_RESET_AFTER_MS = 2 * 60 * 1000; // 2 minutes

function clamp(n: number, min: number, max: number): number {
  return Math.min(max, Math.max(min, n));
}

function decayCarry(carry: Record<EmotionTag, number>, dtMs: number): Record<EmotionTag, number> {
  if (dtMs <= 0) return carry;
  const factor = Math.pow(0.5, dtMs / CARRY_HALFLIFE_MS);
  const next = { ...carry };
  for (const k of Object.keys(next) as EmotionTag[]) {
    next[k] = next[k] * factor;
    if (next[k] < 0.01) next[k] = 0;
  }
  return next;
}

/**
 * Load emotion state from localStorage
 */
export function loadEmotionState(): EmotionState {
  try {
    const stored = localStorage.getItem(EMOTION_STATE_KEY);
    if (stored) {
      const parsed = JSON.parse(stored) as Partial<EmotionState>;
      return {
        previousDominant: parsed.previousDominant ?? null,
        dominantStreak: (parsed.dominantStreak ?? (createEmptyCounts() as Record<EmotionTag, number>)) as Record<EmotionTag, number>,
        lastReversalTimestamp: parsed.lastReversalTimestamp ?? null,
        carryByEmotion: (parsed.carryByEmotion ?? (createEmptyCounts() as Record<EmotionTag, number>)) as Record<EmotionTag, number>,
        lastMessageTimestamp: parsed.lastMessageTimestamp ?? null,
      };
    }
  } catch (e) {
    console.error('Failed to load emotion state:', e);
  }

  return {
    previousDominant: null,
    dominantStreak: createEmptyCounts() as Record<EmotionTag, number>,
    lastReversalTimestamp: null,
    carryByEmotion: createEmptyCounts() as Record<EmotionTag, number>,
    lastMessageTimestamp: null,
  };
}

/**
 * Save emotion state to localStorage
 */
export function saveEmotionState(state: EmotionState): void {
  try {
    localStorage.setItem(EMOTION_STATE_KEY, JSON.stringify(state));
  } catch (e) {
    console.error('Failed to save emotion state:', e);
  }
}

/**
 * Apply inertia multiplier to dominant emotion
 */
export function applyInertia(
  counts: EmotionCounts,
  dominantEmotion: EmotionTag,
  streak: number
): EmotionEnergy {
  const energy: EmotionEnergy = { ...counts };
  const multiplier = getInertiaMultiplier(streak);
  energy[dominantEmotion] = counts[dominantEmotion] * multiplier;
  return energy;
}

/**
 * Pick a random reply from array
 */
function pickRandom(replies: string[]): string {
  if (replies.length === 0) return '';
  return replies[Math.floor(Math.random() * replies.length)];
}

/**
 * Main emotion engine - process emojis and return appropriate reply
 * @param emojis - Array of emoji strings to process
 * @param templateContext - Context for template variable replacement
 * @param tone - Voice tone to use for reply selection (defaults to 'default')
 */
export function processEmojis(
  emojis: string[],
  templateContext: { playerName: string; characterName: string },
  tone: ToneKey = 'default'
): EmotionEngineResult {
  if (emojis.length === 0) {
    return {
      replyType: 'single',
      reply: '',
      dominantEmotion: 'daily',
      intensity: 1,
      inertiaMultiplier: 1,
    };
  }

  // Load current state
  const state = loadEmotionState();

  // Load tone-specific catalog
  const catalog = getCatalogSection(tone, 'chat');

  // Get emotion counts
  const counts = getEmotionCountsFromEmojis(emojis);

  // Find dominant emotion
  const dominant = findDominantEmotion(counts);

  // Time-aware carry decay
  const now = Date.now();
  if (state.lastMessageTimestamp) {
    const dt = now - state.lastMessageTimestamp;
    if (dt >= CARRY_RESET_AFTER_MS) {
      state.carryByEmotion = createEmptyCounts() as Record<EmotionTag, number>;
    } else {
      state.carryByEmotion = decayCarry(state.carryByEmotion, dt);
    }
  }

  // Update carry buffer (only builds when the dominant emotion stays the same)
  let carryBonus = 0;

  // Update streak
  if (dominant === state.previousDominant) {
    const prevCarry = state.carryByEmotion[dominant] ?? 0;
    const nextCarry = clamp(prevCarry + counts[dominant], 0, CARRY_MAX);
    state.carryByEmotion[dominant] = nextCarry;

    // Avoid double-counting the current message in totalEnergy:
    // carryBonus represents "previously accumulated" energy only.
    carryBonus = Math.round(Math.max(0, nextCarry - counts[dominant]));
  } else {
    // Emotion changed => reset carry completely
    state.carryByEmotion = createEmptyCounts() as Record<EmotionTag, number>;
    state.carryByEmotion[dominant] = clamp(counts[dominant], 0, CARRY_MAX);
    carryBonus = 0;
  }

  const streak = state.dominantStreak[dominant];
  const inertiaMultiplier = getInertiaMultiplier(streak);

  // Apply inertia to get energy
  const energy = applyInertia(counts, dominant, streak);
  const baseTotalEnergy = Math.round(Object.values(energy).reduce((sum, e) => sum + e, 0));

  // NEW: add carry bonus from previous messages (same dominant emotion)
  const totalEnergy = baseTotalEnergy + carryBonus;

  const intensity = intensityFromTotal(totalEnergy);

  state.lastMessageTimestamp = now;

  let result: EmotionEngineResult;

  // Priority 1: Check reversal trigger
  // const now = Date.now();
  const timeSinceLastReversal = state.lastReversalTimestamp
    ? now - state.lastReversalTimestamp
    : null;
  const canReversal = !state.lastReversalTimestamp ||
    timeSinceLastReversal! >= REVERSAL_COOLDOWN_MS;

  // Debug logging for reversal
  console.group(' Reversal Check');
  console.log('Previous dominant:', state.previousDominant ?? '(none)');
  console.log('Current dominant:', dominant);
  console.log('Same emotion?', state.previousDominant === dominant);
  if (timeSinceLastReversal !== null) {
    const cooldownRemaining = Math.max(0, REVERSAL_COOLDOWN_MS - timeSinceLastReversal);
    console.log('Time since last reversal:', Math.round(timeSinceLastReversal / 1000) + 's');
    console.log('Cooldown remaining:', cooldownRemaining > 0 ? Math.round(cooldownRemaining / 1000) + 's' : 'None');
  } else {
    console.log('No previous reversal recorded');
  }
  console.log('Can trigger reversal?', canReversal);

  if (canReversal) {
    const reversalRule = findMatchingReversalRule(state.previousDominant, dominant);
    console.log('Matching rule found?', reversalRule ? `Yes: ${reversalRule.id} (${reversalRule.from}  ${reversalRule.to})` : 'No');

    if (reversalRule) {
      const replies = catalog.reversal[reversalRule.poolId] ?? [];
      console.log('Reply pool:', reversalRule.poolId, '| Replies available:', replies.length);

      if (replies.length > 0) {
        const replyTemplate = pickRandom(replies);
        const reply = processTemplate(replyTemplate, templateContext);

        state.lastReversalTimestamp = now;
        state.previousDominant = dominant;
        saveEmotionState(state);

        console.log(' REVERSAL TRIGGERED:', reversalRule.id);
        console.groupEnd();

        return {
          replyType: 'reversal',
          reply,
          dominantEmotion: dominant,
          intensity,
          matchedRule: reversalRule.id,
          inertiaMultiplier,
        };
      } else {
        console.log(' No replies in pool, falling through');
      }
    }
  } else {
    console.log(' On cooldown, skipping reversal check');
  }
  console.log(' Reversal not triggered, continuing to next priority');
  console.groupEnd();

  // Priority 2: Check mixed emotion trigger
  if (!isSingleEmotion(counts)) {
    const mixedRule = findMatchingMixedRule(counts);
    if (mixedRule) {
      const pairReplies = catalog.mixed[mixedRule.pairKey];
      if (pairReplies) {
        const replies = pairReplies[intensity] ?? pairReplies[1] ?? [];
        if (replies.length > 0) {
          const replyTemplate = pickRandom(replies);
          const reply = processTemplate(replyTemplate, templateContext);

          state.previousDominant = dominant;
          saveEmotionState(state);

          return {
            replyType: 'mixed',
            reply,
            dominantEmotion: dominant,
            intensity,
            matchedRule: mixedRule.id,
            inertiaMultiplier,
          };
        }
      }
    }
  }

  // Priority 3: Single emotion fallback
  const singleReplies = catalog.single[dominant];
  const replies = singleReplies?.[intensity] ?? singleReplies?.[1] ?? [];
  const replyTemplate = pickRandom(replies);
  const reply = processTemplate(replyTemplate, templateContext);

  // Update state
  state.previousDominant = dominant;
  saveEmotionState(state);

  return {
    replyType: 'single',
    reply,
    dominantEmotion: dominant,
    intensity,
    inertiaMultiplier,
  };
}

/**
 * Reset emotion state (clear streaks and previous dominant)
 */
export function resetEmotionState(): void {
  const freshState: EmotionState = {
    previousDominant: null,
    dominantStreak: createEmptyCounts() as Record<EmotionTag, number>,
    lastReversalTimestamp: null,
    carryByEmotion: createEmptyCounts() as Record<EmotionTag, number>,
    lastMessageTimestamp: null,
  };
  saveEmotionState(freshState);
}