// Apply theme palette to CSS variables
export function applyThemePalette(
  palette: ThemePalette,
  overrides: Partial<ThemePalette> = {}
): void {
  const finalPalette = { ...palette, ...overrides };
  const root = document.documentElement;

  root.style.setProperty('--background', finalPalette.background);
  root.style.setProperty('--foreground', finalPalette.foreground);
  root.style.setProperty('--card', finalPalette.card);
  root.style.setProperty('--card-foreground', finalPalette.cardForeground);
  root.style.setProperty('--primary', finalPalette.primary);
  root.style.setProperty('--primary-foreground', finalPalette.primaryForeground);
  root.style.setProperty('--secondary', finalPalette.secondary);
  root.style.setProperty('--secondary-foreground', finalPalette.secondaryForeground);
  root.style.setProperty('--muted', finalPalette.muted);
  root.style.setProperty('--muted-foreground', finalPalette.mutedForeground);
  root.style.setProperty('--accent', finalPalette.accent);
  root.style.setProperty('--accent-foreground', finalPalette.accentForeground);
  root.style.setProperty('--border', finalPalette.border);
  root.style.setProperty('--input', finalPalette.border);
  root.style.setProperty('--ring', finalPalette.primary);
  root.style.setProperty('--phone-frame', finalPalette.phoneFrame);
  root.style.setProperty('--phone-frame-accent', finalPalette.phoneFrameAccent);
  root.style.setProperty('--home-bg', finalPalette.homeBg);
  root.style.setProperty('--chat-bg', finalPalette.chatBg);
  root.style.setProperty('--bubble-user', finalPalette.bubbleUser);
  root.style.setProperty('--bubble-user-foreground', finalPalette.bubbleUserForeground);
  root.style.setProperty('--bubble-character', finalPalette.bubbleCharacter);
  root.style.setProperty('--bubble-character-foreground', finalPalette.bubbleCharacterForeground);
  root.style.setProperty('--widget-bg', finalPalette.widgetBg);
  root.style.setProperty('--popover', finalPalette.card);
  root.style.setProperty('--popover-foreground', finalPalette.cardForeground);
}

// Get resolved palette with overrides
export function getResolvedPalette(
  preset: ThemePreset,
  overrides: Partial<ThemePalette> = {}
): ThemePalette {
  return { ...themePalettes[preset], ...overrides };
}

