// Item definitions and reward pools for hourly rewards

export type ItemCategory = "food" | "music" | "gift" | "place";

export interface InventoryItem {
  id: string;
  name: string;
  icon: string;
  category: ItemCategory;
  receivedAt: number;
  source?: "default" | "advancedSettings";
}

export interface ItemDefinition {
  id: string;
  name: string;
  icon: string;
  category: ItemCategory;
  source?: "default" | "advancedSettings";
  locked?: boolean;
}

