// All default items combined
export const allItems: ItemDefinition[] = [...foodItems, ...musicItems, ...giftItems];

// Get random item from a pool
export function getRandomItem(pool: ItemDefinition[]): ItemDefinition {
  return pool[Math.floor(Math.random() * pool.length)];
}

// Create custom favorite items based on user preferences
export function createCustomFavoriteItems(favorites: { food: string; place: string; music: string }): ItemDefinition[] {
  const customItems: ItemDefinition[] = [];

  if (favorites.food && favorites.food.trim()) {
    customItems.push({
      id: "custom-food",
      name: favorites.food.trim(),
      icon: "",
      category: "food",
      source: "advancedSettings",
      locked: false,
    });
  }

  if (favorites.place && favorites.place.trim()) {
    customItems.push({
      id: "custom-place",
      name: favorites.place.trim(),
      icon: "",
      category: "place",
      source: "advancedSettings",
      locked: false,
    });
  }

  if (favorites.music && favorites.music.trim()) {
    customItems.push({
      id: "custom-music",
      name: favorites.music.trim(),
      icon: "",
      category: "music",
      source: "advancedSettings",
      locked: false,
    });
  }

  return customItems;
}

// Get all spawnable items including custom favorites
export function getAllSpawnableItems(favorites: { food: string; place: string; music: string }): ItemDefinition[] {
  const customItems = createCustomFavoriteItems(favorites);
  return [...allItems, ...customItems];
}

// Get hourly reward item - weighted towards food and music, includes custom favorites
export function getHourlyRewardItem(favorites?: { food: string; place: string; music: string }): ItemDefinition {
  const customItems = favorites ? createCustomFavoriteItems(favorites) : [];

  // Weight: 40% food, 35% music, 15% gift, 10% custom favorites (if any)
  const random = Math.random();

  // if (customItems.length > 0 && random < 0.1) {
  //   return getRandomItem(customItems);
  // } else if (random < 0.4) {
  //   return getRandomItem(foodItems);
  // } else if (random < 0.75) {
  //   return getRandomItem(musicItems);
  // } else {
  //   return getRandomItem(giftItems);
  // }

  if (customItems.length > 0 && random < 0.1) {
    return getRandomItem(customItems);
  } else if (random < 0.4) {
    return getRandomItem(giftItems);
  } else if (random < 0.75) {
    return getRandomItem(foodItems);
  } else {
    return getRandomItem(musicItems);
  }
}

// Find item by ID
export function findItemById(itemId: string): ItemDefinition | undefined {
  return allItems.find((item) => item.id === itemId);
}

// Helper to normalize category from old zodiacCategory format
export function normalizeItemCategory(item: InventoryItem): InventoryItem {
  // Handle legacy items with zodiacCategory
  const legacyItem = item as InventoryItem & { zodiacCategory?: string };
  if (legacyItem.zodiacCategory && !["food", "music", "gift", "place"].includes(item.category)) {
    const categoryMap: Record<string, ItemCategory> = {
      foods: "food",
      places: "place",
      music: "music",
    };
    return {
      ...item,
      category: categoryMap[legacyItem.zodiacCategory] || item.category,
    };
  }
  // Handle legacy 'customFavorite' category
  if ((item.category as string) === "customFavorite") {
    // Try to infer from id or name
    if (item.id.includes("food")) return { ...item, category: "food" };
    if (item.id.includes("place")) return { ...item, category: "place" };
    if (item.id.includes("music")) return { ...item, category: "music" };
    return { ...item, category: "gift" }; // fallback
  }
  return item;
}

// Category to preference key mapping for isPreferredItem checks
export function getCategoryPreferenceKey(category: ItemCategory): "foods" | "places" | "music" | "giftItems" {
  switch (category) {
    case "food":
      return "foods";
    case "place":
      return "places";
    case "music":
      return "music";
    case "gift":
      return "giftItems";
    default:
      return "foods";
  }
}
