// Affection-tiered completion replies for Pomodoro and Todo

export interface AffinityTier {
  min: number;
  max: number;
  name: string;
}

export const AFFINITY_TIERS: AffinityTier[] = [
  { min: 0, max: 19, name: 'stranger' },
  { min: 20, max: 39, name: 'acquaintance' },
  { min: 40, max: 59, name: 'friend' },
  { min: 60, max: 79, name: 'closeFriend' },
  { min: 80, max: 100, name: 'bestFriend' },
];

export type AffinityTierName = 'stranger' | 'acquaintance' | 'friend' | 'closeFriend' | 'bestFriend';

// Pomodoro completion replies by affinity tier
export const pomodoroReplies: Record<AffinityTierName, string[]> = {
  stranger: [
    '.',
    ' .',
    ' .',
  ],
  acquaintance: [
    ',  !',
    '~ .',
    ' .',
  ],
  friend: [
    '!   ',
    '    !',
    '~ ',
  ],
  closeFriend: [
    ' !   ',
    '     !',
    '   ? ',
  ],
  bestFriend: [
    '    ',
    '  .  ',
    '    ! ',
  ],
};

// Todo completion replies by affinity tier
export const todoReplies: Record<AffinityTierName, string[]> = {
  stranger: [
    '.',
    ' .',
    '  .',
  ],
  acquaintance: [
    ', !',
    '  .',
    ', ?',
  ],
  friend: [
    '! ? ',
    ' !',
    '   !',
  ],
  closeFriend: [
    '~ ! ',
    '   !',
    '    ',
  ],
  bestFriend: [
    '!    ',
    '    ',
    '  !  ',
  ],
};

// Get current affinity tier
export function getAffinityTier(affinity: number): AffinityTierName {
  for (const tier of AFFINITY_TIERS) {
    if (affinity >= tier.min && affinity <= tier.max) {
      return tier.name as AffinityTierName;
    }
  }
  return 'stranger';
}

// Get a random reply from the appropriate tier (optional seed for reproducibility)
export function getCompletionReply(
  type: 'pomodoro' | 'todo',
  affinity: number,
  seed?: number
): string {
  const tier = getAffinityTier(affinity);
  const replies = type === 'pomodoro' ? pomodoroReplies[tier] : todoReplies[tier];
  
  // Use seed for reproducible randomness if provided
  const index = seed !== undefined 
    ? Math.abs(seed) % replies.length 
    : Math.floor(Math.random() * replies.length);
  
  return replies[index];
}
