import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Trophy, Check, Coins, ChevronUp, ChevronDown, X, Gift, Star } from 'lucide-react';
import { useAppStore } from '@/stores/appStore';
import { CHALLENGES, getChallengeProgress, getChallengeProgressText } from '@/lib/challengesConfig';
import { getDailyChallengeProgress, DAILY_CHALLENGE_REWARD, DAILY_BONUS_REWARD } from '@/lib/dailyChallengesConfig';
import { Progress } from '@/components/ui/progress';
import { challengesPanelT, getLanguage } from '@/lib/i18n';

interface ChallengesPanelProps {
  onClose: () => void;
}

const COLLAPSED_HEIGHT = 220;
const EXPANDED_HEIGHT = 450;

const ChallengesPanel: React.FC<ChallengesPanelProps> = ({ onClose }) => {
  const store = useAppStore();
  const { completedChallenges, dailyChallenges, initializeDailyChallenges, getTodayStats } = store;
  const [isExpanded, setIsExpanded] = useState(false);
  const [showCompleted, setShowCompleted] = useState(false);
  const lang = getLanguage();

  // Initialize daily challenges on mount
  useEffect(() => {
    initializeDailyChallenges();
  }, [initializeDailyChallenges]);

  const todayStats = getTodayStats();
  const dailyChallengesList = dailyChallenges?.challenges ?? [];
  const completedDailyCount = dailyChallengesList.filter(c => c.completed).length;
  const allDailyComplete = completedDailyCount === 3;

  const incompleteChallenges = CHALLENGES.filter(
    (c) => !completedChallenges.some((cc) => cc.id === c.id)
  );

  const completedChallengesList = CHALLENGES.filter(
    (c) => completedChallenges.some((cc) => cc.id === c.id)
  );

  const toggleExpand = () => setIsExpanded(!isExpanded);

  const getRewardDisplay = (challenge: typeof CHALLENGES[0]) => {
    const { reward } = challenge;
    if (reward.type === 'coins' && reward.points) {
      return <><Coins className="w-3 h-3 text-primary" /><span className="text-[10px] font-bold text-primary">+{reward.points}</span></>;
    }
    if (reward.type === 'emoji' && reward.emojiId) {
      return <span className="text-sm">{reward.emojiId}</span>;
    }
    if (reward.type === 'item') {
      return <><Gift className="w-3 h-3 text-accent" /><span className="text-[10px] font-bold text-accent">Item</span></>;
    }
    return null;
  };

  return (
    <motion.div
      initial={{ height: COLLAPSED_HEIGHT }}
      animate={{ height: isExpanded ? EXPANDED_HEIGHT : COLLAPSED_HEIGHT }}
      transition={{ type: 'spring', stiffness: 300, damping: 30 }}
      className="flex flex-col overflow-hidden"
    >
      <div className="flex items-center gap-2 px-4 pt-4 pb-2">
        <Trophy className="w-4 h-4 text-primary" />
        <h3 className="font-semibold text-foreground text-sm flex-1">{challengesPanelT().challenges}</h3>
        <span className="text-xs text-muted-foreground">({completedChallenges.length}/{CHALLENGES.length})</span>
        <motion.button whileTap={{ scale: 0.9 }} onClick={toggleExpand} className="p-1.5 rounded-lg hover:bg-muted transition-colors">
          {isExpanded ? <ChevronDown className="w-4 h-4 text-muted-foreground" /> : <ChevronUp className="w-4 h-4 text-muted-foreground" />}
        </motion.button>
        <motion.button whileTap={{ scale: 0.9 }} onClick={onClose} className="p-1.5 rounded-lg hover:bg-muted transition-colors">
          <X className="w-4 h-4 text-muted-foreground" />
        </motion.button>
      </div>

      <div className="flex-1 overflow-y-auto overflow-x-hidden px-4 pb-4 space-y-2">
        {/* Daily Challenges Section - Always at top */}
        <div className="mb-3">
          <div className="flex items-center gap-2 mb-2">
            <Star className="w-3.5 h-3.5 text-warning" />
            <span className="text-[11px] font-semibold text-warning uppercase tracking-wide">
              {lang === 'ko' ? ' ' : 'Daily'}
            </span>
            <span className="text-[10px] text-muted-foreground">({completedDailyCount}/3)</span>
            {allDailyComplete && (
              <span className="text-[10px] bg-warning/20 text-warning px-1.5 py-0.5 rounded-full">
                +{DAILY_BONUS_REWARD} {lang === 'ko' ? '!' : 'bonus!'}
              </span>
            )}
          </div>

          {dailyChallengesList.map((challenge) => {
            const progress = getDailyChallengeProgress(challenge.condition, todayStats);

            if (challenge.completed) {
              return (
                <motion.div
                  key={challenge.templateId}
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  className="flex items-center gap-2 p-2 bg-success/10 rounded-xl mb-1.5"
                >
                  <div className="w-6 h-6 rounded-full bg-success/20 flex items-center justify-center flex-shrink-0">
                    <Check className="w-3 h-3 text-success" />
                  </div>
                  <p className="text-[11px] text-muted-foreground line-through flex-1">
                    {lang === 'ko' ? challenge.titleKo : challenge.title}
                  </p>
                  <span className="text-[10px] text-success">+{DAILY_CHALLENGE_REWARD}</span>
                </motion.div>
              );
            }

            return (
              <motion.div
                key={challenge.templateId}
                initial={{ opacity: 0, y: 5 }}
                animate={{ opacity: 1, y: 0 }}
                className="p-2 bg-warning/5 border border-warning/20 rounded-xl mb-1.5"
              >
                <div className="flex items-center gap-2">
                  <div className="w-6 h-6 rounded-full bg-warning/20 flex items-center justify-center flex-shrink-0">
                    <Star className="w-3 h-3 text-warning" />
                  </div>
                  <div className="flex-1 min-w-0">
                    <p className="text-[11px] font-medium text-foreground">
                      {lang === 'ko' ? challenge.titleKo : challenge.title}
                    </p>
                  </div>
                  <div className="flex items-center gap-0.5 flex-shrink-0">
                    <Coins className="w-3 h-3 text-warning" />
                    <span className="text-[10px] font-bold text-warning">+{DAILY_CHALLENGE_REWARD}</span>
                  </div>
                </div>
                <div className="mt-1.5 flex items-center gap-2">
                  <Progress value={progress.percentage} className="h-1 flex-1" />
                  <span className="text-[10px] text-muted-foreground">{progress.current}/{progress.target}</span>
                </div>
              </motion.div>
            );
          })}
        </div>

        {/* Regular Challenges */}
        {incompleteChallenges.map((challenge) => {
          const progress = getChallengeProgress(challenge.unlockCondition, store);
          const progressText = getChallengeProgressText(challenge.unlockCondition, store);

          return (
            <motion.div key={challenge.id} initial={{ opacity: 0, y: 5 }} animate={{ opacity: 1, y: 0 }} className="p-2 bg-widget-bg rounded-xl">
              <div className="flex items-center gap-2">
                <div className="w-8 h-8 rounded-full bg-muted flex items-center justify-center flex-shrink-0">
                  {challenge.reward.type === 'emoji' && challenge.reward.emojiId ? (
                    <span className="text-lg opacity-50">{challenge.reward.emojiId}</span>
                  ) : (
                    <Trophy className="w-4 h-4 text-muted-foreground" />
                  )}
                </div>
                <div className="flex-1 min-w-0">
                  <p className="text-xs font-medium text-foreground truncate">{challenge.title}</p>
                  <p className="text-[10px] text-muted-foreground truncate">
                    {lang === 'ko' ? challenge.descriptionKo : challenge.description}
                  </p>
                </div>
                <div className="flex items-center gap-0.5 flex-shrink-0">
                  {getRewardDisplay(challenge)}
                </div>
              </div>
              <div className="mt-2 flex items-center gap-2">
                <Progress value={progress} className="h-1.5 flex-1" />
                <span className="text-[10px] text-muted-foreground">{progressText}</span>
              </div>
            </motion.div>
          );
        })}

        {/* Completed Challenges - Collapsed by default */}
        {completedChallengesList.length > 0 && (
          <>
            <motion.button
              whileTap={{ scale: 0.98 }}
              onClick={() => setShowCompleted(!showCompleted)}
              className="flex items-center gap-2 w-full pt-2"
            >
              <div className="text-[10px] text-muted-foreground uppercase tracking-wide">
                {challengesPanelT().completed} ({completedChallengesList.length})
              </div>
              {showCompleted ? (
                <ChevronUp className="w-3 h-3 text-muted-foreground" />
              ) : (
                <ChevronDown className="w-3 h-3 text-muted-foreground" />
              )}
            </motion.button>

            <AnimatePresence>
              {showCompleted && completedChallengesList.map((challenge) => (
                <motion.div
                  key={challenge.id}
                  initial={{ opacity: 0, height: 0 }}
                  animate={{ opacity: 1, height: 'auto' }}
                  exit={{ opacity: 0, height: 0 }}
                  className="flex items-center gap-2 p-2 bg-muted/50 rounded-xl opacity-70"
                >
                  <div className="w-8 h-8 rounded-full bg-success/20 flex items-center justify-center flex-shrink-0">
                    <Check className="w-4 h-4 text-success" />
                  </div>
                  <div className="flex-1 min-w-0">
                    <p className="text-xs font-medium text-foreground truncate line-through">{challenge.title}</p>
                    <p className="text-[10px] text-muted-foreground truncate">
                      {lang === 'ko' ? challenge.descriptionKo : challenge.description}
                    </p>
                  </div>
                  <Check className="w-3 h-3 text-success flex-shrink-0" />
                </motion.div>
              ))}
            </AnimatePresence>
          </>
        )}

        {incompleteChallenges.length === 0 && completedChallengesList.length === 0 && dailyChallengesList.length === 0 && (
          <div className="flex flex-col items-center justify-center h-full text-center py-8">
            <div className="text-2xl mb-2"></div>
            <p className="text-xs text-muted-foreground">{challengesPanelT().noChallengesAvailable}</p>
          </div>
        )}
      </div>
    </motion.div>
  );
};

export default ChallengesPanel;