// Auto Message Frequency Presets

export type AutoMessagePreset = 'frequent' | 'regular' | 'occasional' | 'quiet';

export interface PresetConfig {
  id: AutoMessagePreset;
  label: string;
  minSeconds: number;
  maxSeconds: number;
  index: number;
}

export const AUTO_MESSAGE_PRESETS: PresetConfig[] = [
  { id: 'frequent', label: '', minSeconds: 30, maxSeconds: 180, index: 0 },
  { id: 'regular', label: '', minSeconds: 180, maxSeconds: 300, index: 1 },
  { id: 'occasional', label: '', minSeconds: 300, maxSeconds: 600, index: 2 },
  { id: 'quiet', label: '', minSeconds: 1200, maxSeconds: 1200, index: 3 },
];

export function getPresetByIndex(index: number): PresetConfig {
  return AUTO_MESSAGE_PRESETS[index] ?? AUTO_MESSAGE_PRESETS[1];
}

export function getPresetById(id: AutoMessagePreset): PresetConfig {
  return AUTO_MESSAGE_PRESETS.find(p => p.id === id) ?? AUTO_MESSAGE_PRESETS[1];
}

export function getPresetIndex(id: AutoMessagePreset): number {
  const preset = AUTO_MESSAGE_PRESETS.find(p => p.id === id);
  return preset?.index ?? 1;
}

export function getRandomDelayForPreset(preset: AutoMessagePreset): number {
  const config = getPresetById(preset);
  if (config.minSeconds === config.maxSeconds) {
    return config.minSeconds * 1000;
  }
  const range = config.maxSeconds - config.minSeconds;
  const randomOffset = Math.random() * range;
  return (config.minSeconds + randomOffset) * 1000;
}
