// Daily Challenges Configuration
// These challenges reset every day (local time) and provide +100 points per completion
// Completing all 3 gives an additional +300 bonus

export interface DailyChallengeTemplate {
  id: string;
  title: string;
  titleKo: string;
  description: string;
  descriptionKo: string;
  condition: DailyChallengeCondition;
}

export type DailyChallengeCondition =
  | { type: 'gift_any' }
  | { type: 'add_custom_message' }
  | { type: 'conversation_count'; count: number }
  | { type: 'pomodoro_today'; count: number }
  | { type: 'todo_today'; count: number }
  | { type: 'use_talk' };

export interface DailyChallengeInstance {
  templateId: string;
  title: string;
  titleKo: string;
  description: string;
  descriptionKo: string;
  condition: DailyChallengeCondition;
  completed: boolean;
}

export interface DailyChallengesState {
  dateStamp: string; // YYYY-MM-DD local time
  challenges: DailyChallengeInstance[];
  bonusClaimed: boolean;
}

