// Points configuration
export const DAILY_CHALLENGE_REWARD = 100;
export const DAILY_BONUS_REWARD = 300;
export const DAILY_CHALLENGES_COUNT = 3;

// Get local date string (YYYY-MM-DD)
export function getLocalDateString(): string {
  const now = new Date();
  return `${now.getFullYear()}-${String(now.getMonth() + 1).padStart(2, '0')}-${String(now.getDate()).padStart(2, '0')}`;
}

// Fisher-Yates shuffle
function shuffleArray<T>(array: T[]): T[] {
  const result = [...array];
  for (let i = result.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [result[i], result[j]] = [result[j], result[i]];
  }
  return result;
}

// Generate 3 random daily challenges for a new day
export function generateDailyChallenges(): DailyChallengesState {
  const shuffled = shuffleArray(DAILY_CHALLENGE_TEMPLATES);
  const selected = shuffled.slice(0, DAILY_CHALLENGES_COUNT);
  
  return {
    dateStamp: getLocalDateString(),
    challenges: selected.map(template => ({
      templateId: template.id,
      title: template.title,
      titleKo: template.titleKo,
      description: template.description,
      descriptionKo: template.descriptionKo,
      condition: template.condition,
      completed: false,
    })),
    bonusClaimed: false,
  };
}

// Check if daily challenges need to be regenerated
export function needsDailyReset(state: DailyChallengesState | null): boolean {
  if (!state) return true;
  return state.dateStamp !== getLocalDateString();
}

// Calculate progress for a daily challenge condition
export function getDailyChallengeProgress(
  condition: DailyChallengeCondition,
  todayStats: {
    giftGivenToday: boolean;
    customMessageAddedToday: boolean;
    conversationCountToday: number;
    pomodoroCountToday: number;
    todoCompletedToday: number;
    talkUsedToday: boolean;
  }
): { current: number; target: number; percentage: number } {
  let current = 0;
  let target = 1;

  switch (condition.type) {
    case 'gift_any':
      current = todayStats.giftGivenToday ? 1 : 0;
      target = 1;
      break;
    case 'add_custom_message':
      current = todayStats.customMessageAddedToday ? 1 : 0;
      target = 1;
      break;
    case 'conversation_count':
      current = todayStats.conversationCountToday;
      target = condition.count;
      break;
    case 'pomodoro_today':
      current = todayStats.pomodoroCountToday;
      target = condition.count;
      break;
    case 'todo_today':
      current = todayStats.todoCompletedToday;
      target = condition.count;
      break;
    case 'use_talk':
      current = todayStats.talkUsedToday ? 1 : 0;
      target = 1;
      break;
  }

  return {
    current: Math.min(current, target),
    target,
    percentage: Math.min(100, Math.floor((current / target) * 100)),
  };
}

// Check if a condition is met
export function isDailyChallengeComplete(
  condition: DailyChallengeCondition,
  todayStats: {
    giftGivenToday: boolean;
    customMessageAddedToday: boolean;
    conversationCountToday: number;
    pomodoroCountToday: number;
    todoCompletedToday: number;
    talkUsedToday: boolean;
  }
): boolean {
  const progress = getDailyChallengeProgress(condition, todayStats);
  return progress.percentage >= 100;
}
