import React, { useState, useRef, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { User, Calendar, ArrowRight, Check, CircleUserRound, Camera, X, Music } from 'lucide-react';
import { useAppStore } from '@/stores/appStore';
import { zodiacInfo, getZodiacSign } from '@/lib/zodiac';
import { processAvatarImage } from '@/lib/imageResize';
import { saveAvatarBlob } from '@/lib/avatarStorage';
import { toast } from 'sonner';
import { ToneKey, TONE_OPTIONS } from '@/lib/toneCatalogs';
import { onboardingT } from '@/lib/i18n';

const NAME_MAX_LENGTH = 24;

const OnboardingScreen: React.FC<{ onComplete: () => void }> = ({ onComplete }) => {
  const { 
    setPlayerName, 
    setPlayerBirthday,
    setCharacterName,
    setCharacterBirthday,
    setCharacterAvatar,
    setCharacterVoiceTone,
  } = useAppStore();

  const [step, setStep] = useState<1 | 2>(1);
  
  // Player (owner) info
  const [playerNameInput, setPlayerNameInput] = useState('');
  const [playerMonth, setPlayerMonth] = useState(1);
  const [playerDay, setPlayerDay] = useState(1);
  
  // Character info
  const [characterNameInput, setCharacterNameInput] = useState('');
  const [characterMonth, setCharacterMonth] = useState(1);
  const [characterDay, setCharacterDay] = useState(1);
  const [selectedTone, setSelectedTone] = useState<ToneKey>('default');
  
  // Avatar image handling
  const [avatarPreviewUrl, setAvatarPreviewUrl] = useState<string | null>(null);
  const [avatarBlob, setAvatarBlob] = useState<Blob | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const pad2 = (n: number) => String(n).padStart(2, '0');
  const daysInMonth = (month: number) => new Date(2001, month, 0).getDate();

  const getZodiacForDate = (month: number, day: number) => {
    const dateStr = `${pad2(month)}-${pad2(day)}`;
    return getZodiacSign(dateStr);
  };

  const playerZodiac = getZodiacForDate(playerMonth, playerDay);
  const characterZodiac = getZodiacForDate(characterMonth, characterDay);

  // Cleanup preview URL on unmount
  useEffect(() => {
    return () => {
      if (avatarPreviewUrl) {
        URL.revokeObjectURL(avatarPreviewUrl);
      }
    };
  }, [avatarPreviewUrl]);

  const handlePlayerNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    if (value.length > NAME_MAX_LENGTH) {
      toast.warning(`Name cannot exceed ${NAME_MAX_LENGTH} characters`);
      return;
    }
    setPlayerNameInput(value);
  };

  const handleCharacterNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    if (value.length > NAME_MAX_LENGTH) {
      toast.warning(`Name cannot exceed ${NAME_MAX_LENGTH} characters`);
      return;
    }
    setCharacterNameInput(value);
  };

  const handleFileSelect = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    
    const result = await processAvatarImage(file);
    
    if ('error' in result) {
      toast.error(result.error);
      return;
    }
    
    // Clean up old preview URL
    if (avatarPreviewUrl) {
      URL.revokeObjectURL(avatarPreviewUrl);
    }
    
    const newUrl = URL.createObjectURL(result.blob);
    setAvatarPreviewUrl(newUrl);
    setAvatarBlob(result.blob);
  };

  const handleRemovePhoto = () => {
    if (avatarPreviewUrl) {
      URL.revokeObjectURL(avatarPreviewUrl);
    }
    setAvatarPreviewUrl(null);
    setAvatarBlob(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const handleStep1Confirm = () => {
    if (!playerNameInput.trim()) return;
    
    setPlayerName(playerNameInput.trim());
    setPlayerBirthday(`${pad2(playerMonth)}-${pad2(playerDay)}`);
    setStep(2);
  };

  const handleStep2Confirm = async () => {
    if (!characterNameInput.trim()) return;
    
    // Save avatar to IndexedDB if selected
    if (avatarBlob) {
      try {
        await saveAvatarBlob(avatarBlob);
        setCharacterAvatar('custom');
      } catch (error) {
        console.error('Failed to save avatar:', error);
        toast.error('Failed to save profile photo');
      }
    } else {
      setCharacterAvatar('');
    }
    
    setCharacterName(characterNameInput.trim());
    setCharacterBirthday(`${pad2(characterMonth)}-${pad2(characterDay)}`);
    setCharacterVoiceTone(selectedTone);
    onComplete();
  };

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      className="h-full bg-gradient-to-b from-primary/10 via-background to-background flex flex-col overflow-y-auto"
    >
      <AnimatePresence mode="wait">
        {step === 1 ? (
          <motion.div
            key="step1"
            initial={{ opacity: 0, x: 50 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -50 }}
            className="flex-1 flex flex-col p-6"
          >
            {/* Header */}
            <div className="text-center mb-8 pt-8">
              <motion.div
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ delay: 0.2, type: 'spring' }}
                className="w-20 h-20 mx-auto mb-4 rounded-full bg-primary/20 flex items-center justify-center"
              >
                <User className="w-10 h-10 text-primary" />
              </motion.div>
              <h1 className="text-2xl font-bold text-foreground mb-2">{onboardingT().welcome}</h1>
              <p className="text-muted-foreground text-sm">{onboardingT().letsGetToKnowYou}</p>
            </div>

            <div className="flex-1 space-y-6">
              {/* Name */}
              <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                <label className="block font-semibold text-foreground mb-2">{onboardingT().yourName}</label>
                <input
                  type="text"
                  value={playerNameInput}
                  onChange={handlePlayerNameChange}
                  placeholder={onboardingT().enterYourName}
                  maxLength={NAME_MAX_LENGTH}
                  className="w-full px-4 py-3 bg-muted rounded-xl text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                  autoFocus
                />
              </div>

              {/* Birthday */}
              <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                <label className="block font-semibold text-foreground mb-2 flex items-center gap-2">
                  <Calendar className="w-4 h-4" />
                  {onboardingT().yourBirthday}
                </label>
                <div className="flex gap-2">
                  <select
                    value={playerMonth}
                    onChange={(e) => {
                      const newMonth = Number(e.target.value);
                      const maxDay = daysInMonth(newMonth);
                      setPlayerMonth(newMonth);
                      setPlayerDay(Math.min(playerDay, maxDay));
                    }}
                    className="flex-1 px-4 py-3 bg-muted rounded-xl text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                  >
                    {Array.from({ length: 12 }, (_, i) => i + 1).map((m) => (
                      <option key={m} value={m}>{pad2(m)}{onboardingT().monthSuffix}</option>
                    ))}
                  </select>
                  <select
                    value={playerDay}
                    onChange={(e) => setPlayerDay(Number(e.target.value))}
                    className="flex-1 px-4 py-3 bg-muted rounded-xl text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                  >
                    {Array.from({ length: daysInMonth(playerMonth) }, (_, i) => i + 1).map((d) => (
                      <option key={d} value={d}>{pad2(d)}{onboardingT().daySuffix}</option>
                    ))}
                  </select>
                </div>
                {playerZodiac && zodiacInfo[playerZodiac] && (
                  <div className="mt-3 flex items-center gap-2 text-sm text-muted-foreground">
                    <span className="text-lg">{zodiacInfo[playerZodiac].symbol}</span>
                    <span>{zodiacInfo[playerZodiac].name}</span>
                  </div>
                )}
              </div>
            </div>

            {/* Confirm Button */}
            <motion.button
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              onClick={handleStep1Confirm}
              disabled={!playerNameInput.trim()}
              className="w-full py-4 bg-primary text-primary-foreground rounded-2xl font-semibold flex items-center justify-center gap-2 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {onboardingT().continue}
              <ArrowRight className="w-5 h-5" />
            </motion.button>
          </motion.div>
        ) : (
          <motion.div
            key="step2"
            initial={{ opacity: 0, x: 50 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -50 }}
            className="flex-1 flex flex-col p-6"
          >
            {/* Header */}
            <div className="text-center mb-6 pt-4">
              <h1 className="text-xl font-bold text-foreground mb-1">{onboardingT().newContactRegistration}</h1>
              <p className="text-muted-foreground text-sm">{onboardingT().createYourCompanion}</p>
            </div>

            <div className="flex-1 space-y-4 overflow-y-auto">
              {/* Avatar Upload */}
              <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                <label className="block font-semibold text-foreground mb-3">{onboardingT().profilePhoto}</label>
                <div className="flex flex-col items-center gap-3">
                  <div className="relative">
                    <div className="w-24 h-24 rounded-full bg-gradient-to-br from-primary/30 to-secondary flex items-center justify-center overflow-hidden shadow-md">
                      {avatarPreviewUrl ? (
                        <img
                          src={avatarPreviewUrl}
                          alt="Avatar preview"
                          className="w-full h-full object-cover"
                        />
                      ) : (
                        <CircleUserRound className="w-16 h-16 text-muted-foreground" />
                      )}
                    </div>
                    {avatarPreviewUrl && (
                      <motion.button
                        whileTap={{ scale: 0.9 }}
                        onClick={handleRemovePhoto}
                        className="absolute -top-1 -right-1 w-6 h-6 bg-destructive text-destructive-foreground rounded-full flex items-center justify-center shadow-md"
                      >
                        <X className="w-4 h-4" />
                      </motion.button>
                    )}
                  </div>
                  <input
                    ref={fileInputRef}
                    type="file"
                    accept="image/jpeg,image/png"
                    onChange={handleFileSelect}
                    className="hidden"
                  />
                  <motion.button
                    whileTap={{ scale: 0.95 }}
                    onClick={() => fileInputRef.current?.click()}
                    className="flex items-center gap-2 px-4 py-2 bg-muted rounded-xl text-sm text-foreground"
                  >
                    <Camera className="w-4 h-4" />
                    {avatarPreviewUrl ? onboardingT().changePhoto : onboardingT().uploadPhoto}
                  </motion.button>
                  <p className="text-xs text-muted-foreground">{onboardingT().photoHint}</p>
                </div>
              </div>

              {/* Character Name */}
              <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                <label className="block font-semibold text-foreground mb-2">{onboardingT().characterName}</label>
                <input
                  type="text"
                  value={characterNameInput}
                  onChange={handleCharacterNameChange}
                  placeholder={onboardingT().enterCharacterName}
                  maxLength={NAME_MAX_LENGTH}
                  className="w-full px-4 py-3 bg-muted rounded-xl text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                />
              </div>

              {/* Character Birthday */}
              <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                <label className="block font-semibold text-foreground mb-2 flex items-center gap-2">
                  <Calendar className="w-4 h-4" />
                  {onboardingT().characterBirthday}
                </label>
                <div className="flex gap-2">
                  <select
                    value={characterMonth}
                    onChange={(e) => {
                      const newMonth = Number(e.target.value);
                      const maxDay = daysInMonth(newMonth);
                      setCharacterMonth(newMonth);
                      setCharacterDay(Math.min(characterDay, maxDay));
                    }}
                    className="flex-1 px-4 py-3 bg-muted rounded-xl text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                  >
                    {Array.from({ length: 12 }, (_, i) => i + 1).map((m) => (
                      <option key={m} value={m}>{pad2(m)}{onboardingT().monthSuffix}</option>
                    ))}
                  </select>
                  <select
                    value={characterDay}
                    onChange={(e) => setCharacterDay(Number(e.target.value))}
                    className="flex-1 px-4 py-3 bg-muted rounded-xl text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                  >
                    {Array.from({ length: daysInMonth(characterMonth) }, (_, i) => i + 1).map((d) => (
                      <option key={d} value={d}>{pad2(d)}{onboardingT().daySuffix}</option>
                    ))}
                  </select>
                </div>
                {characterZodiac && zodiacInfo[characterZodiac] && (
                  <div className="mt-3 flex items-center gap-2 text-sm text-muted-foreground">
                    <span className="text-lg">{zodiacInfo[characterZodiac].symbol}</span>
                    <span>{zodiacInfo[characterZodiac].name}</span>
                  </div>
                )}
              </div>

              {/* Voice Tone */}
              <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                <label className="block font-semibold text-foreground mb-2 flex items-center gap-2">
                  <Music className="w-4 h-4" />
                  {onboardingT().voiceTone}
                </label>
                <div className="grid grid-cols-3 gap-2">
                  {TONE_OPTIONS.map(option => (
                    <motion.button
                      key={option.id}
                      whileTap={{ scale: 0.95 }}
                      onClick={() => setSelectedTone(option.id)}
                      className={`flex flex-col items-center gap-1 px-3 py-3 rounded-xl transition-all ${
                        selectedTone === option.id
                          ? 'bg-primary text-primary-foreground'
                          : 'bg-muted text-muted-foreground'
                      }`}
                    >
                      <span className="text-lg">{option.emoji}</span>
                      <span className="text-xs">{option.label}</span>
                    </motion.button>
                  ))}
                </div>
              </div>
            </div>

            {/* Confirm Button */}
            <motion.button
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              onClick={handleStep2Confirm}
              disabled={!characterNameInput.trim()}
              className="w-full py-4 bg-primary text-primary-foreground rounded-2xl font-semibold flex items-center justify-center gap-2 disabled:opacity-50 disabled:cursor-not-allowed mt-4"
            >
              <Check className="w-5 h-5" />
              {onboardingT().confirm}
            </motion.button>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.div>
  );
};

export default OnboardingScreen;
