// lib/toneCatalogs.ts
// Unified tone/voice catalogs for all text groups
// Switching activeTone changes ALL reply text groups together

import { processTemplate } from './templateText';
import {
  defaultCatalog,
  squirrelCatalog,
  angelCatalog,
  type ToneCatalog,
  type ToneMeta,
  type TimeOfDay,
  type AutoMessageEntry,
  type ZodiacUnlockMessages,
} from './toneCatalog/toneCatalogExports';

// Re-export types for use by other modules
export type { ToneCatalog, ToneMeta, TimeOfDay, AutoMessageEntry, ZodiacUnlockMessages };

//    import (    )
import type { AffinityTierName } from './completionReplies';
import type { ZodiacSign } from './zodiac';

// Registry of all available tones with their metadata
export const TONE_REGISTRY = {
  default: { label: 'Default', emoji: '' },
  squirrel: { label: 'Squirrel', emoji: '' },
  angel: { label: 'Angel', emoji: '' },
} as const satisfies Record<string, ToneMeta>;

export type ToneKey = keyof typeof TONE_REGISTRY;

// Pre-built array for UI tone pickers (Settings, Onboarding, etc.)
export const TONE_OPTIONS: { id: ToneKey; label: string; emoji: string }[] = (
  Object.entries(TONE_REGISTRY) as [ToneKey, ToneMeta][]
).map(([id, meta]) => ({ id, label: meta.label, emoji: meta.emoji }));

// Helper: get time of day (exported for use in appStore)
export const getTimeOfDay = (): TimeOfDay => {
  const hour = new Date().getHours();
  if (hour >= 0 && hour < 6) return 'dawn';
  if (hour >= 6 && hour < 12) return 'morning';
  if (hour >= 12 && hour < 18) return 'afternoon';
  return 'evening';
};

// Fortune required affinity constant
export const FORTUNE_REQUIRED_AFFINITY = 100;

// Registry mapping tone keys to catalogs
export const toneCatalogs: Record<ToneKey, ToneCatalog> = {
  default: defaultCatalog,
  squirrel: squirrelCatalog,
  angel: angelCatalog,
};

//     TONE_CATALOGS     alias 
export const TONE_CATALOGS = toneCatalogs;

// Get active catalog with fallback to default for missing sections
export function getActiveCatalog(tone: ToneKey): ToneCatalog {
  return toneCatalogs[tone] ?? toneCatalogs.default;
}

// Get specific section from active catalog with default fallback
export function getCatalogSection<K extends keyof ToneCatalog>(
  tone: ToneKey,
  section: K
): ToneCatalog[K] {
  const catalog = getActiveCatalog(tone);
  return (catalog[section] ?? toneCatalogs.default[section]) as ToneCatalog[K];
}

// Get greeting for current time (with optional playerName interpolation)
export function getGreetingForTone(
  tone: ToneKey,
  timeOfDay: TimeOfDay,
  playerName?: string
): string {
  const greetings = getCatalogSection(tone, 'greetings');
  const pool = greetings[timeOfDay] ?? [];
  const template = pool.length ? pool[Math.floor(Math.random() * pool.length)] : 'Hey!';
  return processTemplate(template, { playerName });
}

// Fortune result with selected message
export interface FortuneResultWithMessage {
  id: string;
  label: string;
  icon: string;
  message: string;
}

// Get fortune for tone (randomly selects one message from the messages array)
export function getFortuneForTone(tone: ToneKey, playerName?: string): FortuneResultWithMessage {
  const fortunes = getCatalogSection(tone, 'fortuneOutcomes');
  const fortune = fortunes[Math.floor(Math.random() * fortunes.length)];
  const randomMessage = fortune.messages[Math.floor(Math.random() * fortune.messages.length)];
  return {
    id: fortune.id,
    label: fortune.label,
    icon: fortune.icon,
    message: processTemplate(randomMessage, { playerName }),
  };
}

// Get gift reply for tone (with playerName interpolation)
export function getGiftReplyForTone(
  tone: ToneKey,
  itemName: string,
  isPreferred: boolean,
  playerName?: string
): string {
  const replies = getCatalogSection(tone, 'giftReplies');
  const pool = isPreferred ? replies.preferred : replies.normal;
  const template = pool[Math.floor(Math.random() * pool.length)];
  return processTemplate(template.replace(/{itemName}/g, itemName), { playerName });
}

// Get completion reply for tone (with playerName interpolation)
export function getCompletionReplyForTone(
  tone: ToneKey,
  type: 'pomodoro' | 'todo',
  tier: AffinityTierName,
  playerName?: string
): string {
  const replies = getCatalogSection(tone, 'completionReplies');
  const pool = replies[type][tier] ?? [];
  const template = pool.length ? pool[Math.floor(Math.random() * pool.length)] : 'Nice!';
  return processTemplate(template, { playerName });
}

// Get auto messages for tone (weighted random selection with playerName interpolation)
export function getRandomAutoMessageForTone(tone: ToneKey, playerName?: string): string {
  const messages = getCatalogSection(tone, 'autoMessages');
  if (!messages.length) return processTemplate('Hey! ', { playerName });

  const totalWeight = messages.reduce((sum, m) => sum + m.weight, 0);
  let random = Math.random() * totalWeight;

  for (const msg of messages) {
    random -= msg.weight;
    if (random <= 0) return processTemplate(msg.text, { playerName });
  }

  return processTemplate(messages[0].text, { playerName });
}

// Get please reply messages for tone
export function getPleaseReplyMessagesForTone(tone: ToneKey): string[] {
  return getCatalogSection(tone, 'pleaseReplyMessages');
}

// Pick random unique please reply messages (with playerName interpolation)
export function pickRandomPleaseReplyMessages(
  tone: ToneKey,
  count: number,
  playerName?: string
): string[] {
  const messages = getPleaseReplyMessagesForTone(tone);
  const shuffled = [...messages].sort(() => Math.random() - 0.5);
  return shuffled.slice(0, count).map((msg) => processTemplate(msg, { playerName }));
}

// Get zodiac unlock messages for tone (with playerName interpolation)
export function getZodiacUnlockMessagesForTone(
  tone: ToneKey,
  zodiac: ZodiacSign | null,
  playerName?: string
) {
  const catalog = getActiveCatalog(tone);
  const messages = zodiac ? catalog.zodiacUnlockMessages[zodiac] : catalog.defaultUnlockMessages;

  return {
    food: processTemplate(messages.food, { playerName }),
    place: processTemplate(messages.place, { playerName }),
    music: processTemplate(messages.music, { playerName }),
  };
}
