import { useState, useEffect, useCallback } from 'react';
import { getAvatarBlob, saveAvatarBlob, deleteAvatarBlob } from '@/lib/avatarStorage';
import { processAvatarImage } from '@/lib/imageResize';
import { toast } from 'sonner';

export function useCharacterAvatar() {
  const [avatarUrl, setAvatarUrl] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  
  // Load avatar from IndexedDB on mount
  useEffect(() => {
    let mounted = true;
    let currentUrl: string | null = null;
    
    const loadAvatar = async () => {
      try {
        const blob = await getAvatarBlob();
        if (mounted && blob) {
          currentUrl = URL.createObjectURL(blob);
          setAvatarUrl(currentUrl);
        }
      } catch (error) {
        console.error('Failed to load avatar:', error);
      } finally {
        if (mounted) {
          setIsLoading(false);
        }
      }
    };
    
    loadAvatar();
    
    return () => {
      mounted = false;
      if (currentUrl) {
        URL.revokeObjectURL(currentUrl);
      }
    };
  }, []);
  
  // Upload and save a new avatar
  const uploadAvatar = useCallback(async (file: File): Promise<boolean> => {
    const result = await processAvatarImage(file);
    
    if ('error' in result) {
      toast.error(result.error);
      return false;
    }
    
    try {
      await saveAvatarBlob(result.blob);
      
      // Revoke old URL
      if (avatarUrl) {
        URL.revokeObjectURL(avatarUrl);
      }
      
      // Create new URL
      const newUrl = URL.createObjectURL(result.blob);
      setAvatarUrl(newUrl);
      toast.success('Profile photo updated!');
      return true;
    } catch (error) {
      console.error('Failed to save avatar:', error);
      toast.error('Failed to save profile photo');
      return false;
    }
  }, [avatarUrl]);
  
  // Remove avatar
  const removeAvatar = useCallback(async (): Promise<boolean> => {
    try {
      await deleteAvatarBlob();
      
      if (avatarUrl) {
        URL.revokeObjectURL(avatarUrl);
      }
      
      setAvatarUrl(null);
      toast.success('Profile photo removed');
      return true;
    } catch (error) {
      console.error('Failed to remove avatar:', error);
      toast.error('Failed to remove profile photo');
      return false;
    }
  }, [avatarUrl]);
  
  return {
    avatarUrl,
    isLoading,
    uploadAvatar,
    removeAvatar,
    hasAvatar: !!avatarUrl,
  };
}
