// Default language setting
let currentLanguage: Language = "ko";

export const setLanguage = (lang: Language) => {
  currentLanguage = lang;
};

export const getLanguage = (): Language => currentLanguage;

export const t = (): SystemMessages => systemMessages[currentLanguage];

// Settings UI translations with fallback to Korean
export const ui = (): SettingsUI => settingsUI[currentLanguage] || settingsUI["ko"];

// Component UI translations with fallback to Korean
export const pomodoroT = (): PomodoroUI => pomodoroUI[currentLanguage] || pomodoroUI["ko"];
export const todoT = (): TodoUI => todoUI[currentLanguage] || todoUI["ko"];
export const characterProfileT = (): CharacterProfileUI =>
  characterProfileUI[currentLanguage] || characterProfileUI["ko"];
export const attachmentMenuT = (): AttachmentMenuUI => attachmentMenuUI[currentLanguage] || attachmentMenuUI["ko"];
export const storePanelT = (): StorePanelUI => storePanelUI[currentLanguage] || storePanelUI["ko"];
export const challengesPanelT = (): ChallengesPanelUI => challengesPanelUI[currentLanguage] || challengesPanelUI["ko"];
export const notifyT = (): NotificationMessages => notificationMessages[currentLanguage] || notificationMessages["ko"];
export const onboardingT = (): OnboardingUI => onboardingUI[currentLanguage] || onboardingUI["ko"];
export const zodiacT = (): ZodiacNames => zodiacNames[currentLanguage] || zodiacNames["ko"];
export const monthT = (): MonthNames => monthNames[currentLanguage] || monthNames["ko"];

export const availableLanguages: { code: Language; label: string }[] = [
  { code: "ko", label: "" },
  { code: "en", label: "English" },
];
