import React, { useState, useRef, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import {
  ArrowLeft,
  Image,
  Sparkles,
  Heart,
  Cloud,
  User,
  Palette,
  Music,
  Calendar,
  ChevronRight,
  ChevronDown,
  RotateCcw,
  MessageCircle,
  Download,
  Upload,
  Trash2,
  Utensils,
  MapPin,
  Globe,
} from "lucide-react";
import { useAppStore, BackgroundGradient } from "@/stores/appStore";
import { availableLanguages, Language, ui, zodiacT } from "@/lib/i18n";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Slider } from "@/components/ui/slider";
import { zodiacInfo, getZodiacSign } from "@/lib/zodiac";
import { useCharacterAvatar } from "@/hooks/useCharacterAvatar";
import { CircleUserRound } from "lucide-react";
import { toast } from "sonner";
import { ColorPicker } from "@/components/ui/color-picker";
import { ThemePreset, themePalettes, PALETTE_LABELS, ThemePalette } from "@/lib/themeConfig";
import { ToneKey, TONE_OPTIONS } from "@/lib/toneCatalogs";
import { AUTO_MESSAGE_PRESETS, getPresetByIndex, getPresetIndex } from "@/lib/autoMessagePresets";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";
import { downloadBackup, readBackupFile, importAllData } from "@/lib/backupRestore";
import CustomMessageCreator from "@/components/settings/CustomMessageCreator";

const NAME_MAX_LENGTH = 24;

const getChatWallpaperOptions = (translations: ReturnType<typeof ui>) => [
  { id: "default", label: translations.wallpaperDefault, icon: Image, color: "bg-muted" },
  { id: "stars", label: translations.wallpaperStars, icon: Sparkles, color: "bg-indigo-200" },
  { id: "hearts", label: translations.wallpaperHearts, icon: Heart, color: "bg-rose-200" },
  { id: "clouds", label: translations.wallpaperClouds, icon: Cloud, color: "bg-sky-200" },
] as const;

const getBackgroundGradientOptions = (translations: ReturnType<typeof ui>): { id: BackgroundGradient; label: string; className: string }[] => [
  { id: "none", label: translations.bgNone, className: "bg-background" },
  { id: "calm-1", label: translations.bgBlueGreen, className: "bg-gradient-calm-1" },
  { id: "calm-2", label: translations.bgRosePeach, className: "bg-gradient-calm-2" },
  { id: "calm-3", label: translations.bgPurpleBlue, className: "bg-gradient-calm-3" },
  { id: "calm-4", label: translations.bgWarmSand, className: "bg-gradient-calm-4" },
  { id: "calm-5", label: translations.bgOcean, className: "bg-gradient-calm-5" },
];

const themePresetOptions: { id: ThemePreset; label: string; colors: string[] }[] = [
  { id: "default", label: "Lofi", colors: ["hsl(340, 45%, 65%)", "hsl(200, 30%, 85%)"] },
  { id: "destiel", label: "Destiel", colors: ["hsl(200, 60%, 45%)", "hsl(160, 40%, 75%)"] },
  { id: "stars", label: "Night", colors: ["#211b2c", "hsl(280, 58%, 65%)"] },
  { id: "cacao", label: "Cacao", colors: ["#acc1d1", "#fde42d"] },
];

// Simplified advanced color keys - only essential options
const ADVANCED_COLOR_KEYS: (keyof ThemePalette)[] = ["primary", "secondary", "phoneFrame"];

const SettingsScreen: React.FC = () => {
  const {
    setCurrentScreen,
    chatWallpaper,
    setChatWallpaper,
    backgroundGradient,
    setBackgroundGradient,
    selectedTheme,
    colorOverrides,
    setSelectedTheme,
    setColorOverride,
    resetColorOverrides,
    applyTheme,
    characterProfile,
    setCharacterName,
    setCharacterAvatar,
    setCharacterBirthday,
    setCharacterVoiceTone,
    getZodiacSign: getCharacterZodiac,
    playerProfile,
    setPlayerName,
    setPlayerBirthday,
    setPlayerFavoriteFood,
    setPlayerFavoritePlace,
    setPlayerFavoriteMusic,
    autoMessageEnabled,
    setAutoMessageEnabled,
    autoMessagePreset,
    setAutoMessagePreset,
    language,
    setLanguage,
  } = useAppStore();

  const translations = ui();
  const chatWallpaperOptions = getChatWallpaperOptions(translations);
  const backgroundGradientOptions = getBackgroundGradientOptions(translations);

  useEffect(() => {
    applyTheme();
  }, []);

  const { avatarUrl, uploadAvatar, removeAvatar, hasAvatar } = useCharacterAvatar();
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [editingCharName, setEditingCharName] = useState(false);
  const [tempCharName, setTempCharName] = useState(characterProfile.name);
  const [editingPlayerName, setEditingPlayerName] = useState(false);
  const [tempPlayerName, setTempPlayerName] = useState(playerProfile.name);
  const [showAdvancedColors, setShowAdvancedColors] = useState(false);

  // Character favorites state
  const [favoriteFood, setFavoriteFood] = useState(playerProfile.favoriteFood);
  const [favoritePlace, setFavoritePlace] = useState(playerProfile.favoritePlace);
  const [favoriteMusic, setFavoriteMusic] = useState(playerProfile.favoriteMusic);

  const pad2 = (n: number) => String(n).padStart(2, "0");
  const parseBirthday = (value: string) => {
    if (!value) return { m: 1, d: 1 };
    if (/^\d{4}-\d{2}-\d{2}$/.test(value)) {
      return { m: Number(value.slice(5, 7)) || 1, d: Number(value.slice(8, 10)) || 1 };
    }
    if (/^\d{2}-\d{2}$/.test(value)) {
      const [mm, dd] = value.split("-");
      return { m: Number(mm) || 1, d: Number(dd) || 1 };
    }
    return { m: 1, d: 1 };
  };
  const daysInMonth = (month: number) => new Date(2001, month, 0).getDate();

  const { m: charInitialMonth, d: charInitialDay } = parseBirthday(characterProfile.birthday);
  const [charBirthMonth, setCharBirthMonth] = useState<number>(charInitialMonth);
  const [charBirthDay, setCharBirthDay] = useState<number>(Math.min(charInitialDay, daysInMonth(charInitialMonth)));

  const { m: playerInitialMonth, d: playerInitialDay } = parseBirthday(playerProfile.birthday);
  const [playerBirthMonth, setPlayerBirthMonth] = useState<number>(playerInitialMonth);
  const [playerBirthDay, setPlayerBirthDay] = useState<number>(
    Math.min(playerInitialDay, daysInMonth(playerInitialMonth)),
  );

  const persistCharBirthday = (m: number, d: number) => setCharacterBirthday(`${pad2(m)}-${pad2(d)}`);
  const persistPlayerBirthday = (m: number, d: number) => setPlayerBirthday(`${pad2(m)}-${pad2(d)}`);
  const charZodiac = getCharacterZodiac();
  const charZodiacDisplay = charZodiac ? zodiacInfo[charZodiac] : null;
  const playerZodiac = getZodiacSign(playerProfile.birthday);
  const playerZodiacDisplay = playerZodiac ? zodiacInfo[playerZodiac] : null;

  const handleCharNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.value.length > NAME_MAX_LENGTH) {
      toast.warning(`Name cannot exceed ${NAME_MAX_LENGTH} characters`);
      return;
    }
    setTempCharName(e.target.value);
  };
  const handlePlayerNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.value.length > NAME_MAX_LENGTH) {
      toast.warning(`Name cannot exceed ${NAME_MAX_LENGTH} characters`);
      return;
    }
    setTempPlayerName(e.target.value);
  };
  const handleCharNameSave = () => {
    if (tempCharName.trim()) setCharacterName(tempCharName.trim());
    setEditingCharName(false);
  };
  const handlePlayerNameSave = () => {
    if (tempPlayerName.trim()) setPlayerName(tempPlayerName.trim());
    setEditingPlayerName(false);
  };

  const handleFileSelect = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    const success = await uploadAvatar(file);
    if (success) setCharacterAvatar("custom");
    if (fileInputRef.current) fileInputRef.current.value = "";
  };
  const handleRemovePhoto = async () => {
    const success = await removeAvatar();
    if (success) setCharacterAvatar("");
  };

  const currentPalette = themePalettes[selectedTheme];
  const hasOverrides = Object.keys(colorOverrides).length > 0;

  return (
    <motion.div
      initial={{ x: 50, opacity: 0 }}
      animate={{ x: 0, opacity: 1 }}
      exit={{ x: 50, opacity: 0 }}
      className="h-full bg-phone-home-bg flex flex-col"
    >
      <div className="flex items-center gap-3 py-3 bg-widget-bg/80 backdrop-blur-lg border-b border-border px-[32px] mx-0">
        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          onClick={() => setCurrentScreen("home")}
          className="p-2 -ml-2 rounded-full hover:bg-muted"
        >
          <ArrowLeft className="w-5 h-5 text-foreground" />
        </motion.button>
        <h2 className="font-semibold text-foreground text-lg">{translations.settings}</h2>
      </div>

      <Tabs defaultValue="profile" className="flex-1 flex flex-col overflow-hidden mx-[10px]">
        <TabsList className="mx-4 mt-3 grid grid-cols-4 h-10">
          <TabsTrigger value="profile" className="text-xs gap-1">
            {translations.tabProfile}
          </TabsTrigger>
          <TabsTrigger value="character" className="text-xs gap-1">
            {translations.tabCharacter}
          </TabsTrigger>
          <TabsTrigger value="theme" className="text-xs gap-1">
            {translations.tabTheme}
          </TabsTrigger>
          <TabsTrigger value="general" className="text-xs gap-1">
            {translations.tabSystem}
          </TabsTrigger>
        </TabsList>

        <div className="flex-1 overflow-y-auto">
          {/* Profile Tab */}
          <TabsContent value="profile" className="p-4 space-y-4 mt-0">
            <div className="bg-widget-bg rounded-2xl p-4 shadow-widget mx-0">
              <h3 className="font-semibold text-foreground mb-3 flex items-center gap-2">
                <User className="w-4 h-4" />
                {translations.yourName}
              </h3>
              {editingPlayerName ? (
                <div className="flex gap-2">
                  <input
                    type="text"
                    value={tempPlayerName}
                    onChange={handlePlayerNameChange}
                    onKeyDown={(e) => e.key === "Enter" && handlePlayerNameSave()}
                    autoFocus
                    maxLength={NAME_MAX_LENGTH}
                    className="flex-1 px-3 py-2 bg-muted rounded-xl text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                  />
                  <motion.button
                    whileTap={{ scale: 0.95 }}
                    onClick={handlePlayerNameSave}
                    className="px-4 py-2 bg-primary text-primary-foreground rounded-xl text-sm"
                  >
                    {translations.save}
                  </motion.button>
                </div>
              ) : (
                <motion.button
                  whileTap={{ scale: 0.98 }}
                  onClick={() => {
                    setTempPlayerName(playerProfile.name);
                    setEditingPlayerName(true);
                  }}
                  className="w-full px-4 py-3 bg-muted rounded-xl text-left text-foreground"
                >
                  {playerProfile.name || translations.tapToSetYourName}
                </motion.button>
              )}
            </div>
            <div className="bg-widget-bg rounded-2xl p-4 shadow-widget mx-0">
              <h3 className="font-semibold text-foreground mb-3 flex items-center gap-2">
                <Calendar className="w-4 h-4" />
                {translations.yourBirthday}
              </h3>
              <div className="flex gap-2">
                <select
                  value={playerBirthMonth}
                  onChange={(e) => {
                    const newMonth = Number(e.target.value);
                    const maxDay = daysInMonth(newMonth);
                    const nextDay = Math.min(playerBirthDay, maxDay);
                    setPlayerBirthMonth(newMonth);
                    setPlayerBirthDay(nextDay);
                    persistPlayerBirthday(newMonth, nextDay);
                  }}
                  className="flex-1 px-4 py-3 bg-muted rounded-xl text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                >
                  {Array.from({ length: 12 }, (_, i) => i + 1).map((m) => (
                    <option key={m} value={m}>
                      {pad2(m)}{translations.monthSuffix ? ` ${translations.monthSuffix}` : ''}
                    </option>
                  ))}
                </select>
                <select
                  value={playerBirthDay}
                  onChange={(e) => {
                    const newDay = Number(e.target.value);
                    setPlayerBirthDay(newDay);
                    persistPlayerBirthday(playerBirthMonth, newDay);
                  }}
                  className="flex-1 px-4 py-3 bg-muted rounded-xl text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                >
                  {Array.from({ length: daysInMonth(playerBirthMonth) }, (_, i) => i + 1).map((d) => (
                    <option key={d} value={d}>
                      {pad2(d)}{translations.daySuffix ? ` ${translations.daySuffix}` : ''}
                    </option>
                  ))}
                </select>
              </div>
              {playerZodiacDisplay && playerZodiac && (
                <div className="mt-3 flex items-center gap-2 text-sm text-muted-foreground">
                  <span className="text-lg">{playerZodiacDisplay.symbol}</span>
                  <span>{zodiacT()[playerZodiac]}</span>
                </div>
              )}
            </div>
          </TabsContent>

          {/* Character Tab with nested tabs */}
          <TabsContent value="character" className="mt-0 flex flex-col">
            <Tabs defaultValue="char-profile" className="flex-1 flex flex-col">
              <TabsList className="mx-4 mt-2 grid grid-cols-3 h-9">
                <TabsTrigger value="char-profile" className="text-xs">
                  {translations.charProfile}
                </TabsTrigger>
                <TabsTrigger value="char-message" className="text-xs">
                  {translations.charMessage}
                </TabsTrigger>
                <TabsTrigger value="char-advanced" className="text-xs">
                  {translations.charAdvanced}
                </TabsTrigger>
              </TabsList>

              {/* Character Profile Sub-tab */}
              <TabsContent value="char-profile" className="p-4 space-y-4 mt-0">
                <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                  <h3 className="font-semibold text-foreground mb-3">{translations.profilePhoto}</h3>
                  <div className="flex flex-col items-center gap-3">
                    <div className="relative">
                      <div className="w-20 h-20 rounded-full bg-gradient-to-br from-primary/30 to-secondary flex items-center justify-center overflow-hidden shadow-md">
                        {avatarUrl ? (
                          <img src={avatarUrl} alt="Character avatar" className="w-full h-full object-cover" />
                        ) : (
                          <CircleUserRound className="w-12 h-12 text-muted-foreground" />
                        )}
                      </div>
                      {hasAvatar && (
                        <motion.button
                          whileTap={{ scale: 0.9 }}
                          onClick={handleRemovePhoto}
                          className="absolute -top-1 -right-1 w-5 h-5 bg-destructive text-destructive-foreground rounded-full flex items-center justify-center shadow-md text-xs"
                        >
                          
                        </motion.button>
                      )}
                    </div>
                    <input
                      ref={fileInputRef}
                      type="file"
                      accept="image/jpeg,image/png"
                      onChange={handleFileSelect}
                      className="hidden"
                    />
                    <motion.button
                      whileTap={{ scale: 0.95 }}
                      onClick={() => fileInputRef.current?.click()}
                      className="flex items-center gap-2 px-3 py-2 bg-muted rounded-xl text-xs text-foreground"
                    >
                      {hasAvatar ? translations.change : translations.upload}
                    </motion.button>
                  </div>
                </div>
                <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                  <h3 className="font-semibold text-foreground mb-3">{translations.name}</h3>
                  {editingCharName ? (
                    <div className="flex gap-2">
                      <input
                        type="text"
                        value={tempCharName}
                        onChange={handleCharNameChange}
                        onKeyDown={(e) => e.key === "Enter" && handleCharNameSave()}
                        autoFocus
                        maxLength={NAME_MAX_LENGTH}
                        className="flex-1 px-3 py-2 bg-muted rounded-xl text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                      />
                      <motion.button
                        whileTap={{ scale: 0.95 }}
                        onClick={handleCharNameSave}
                        className="px-3 py-2 bg-primary text-primary-foreground rounded-xl text-sm"
                      >
                        {translations.save}
                      </motion.button>
                    </div>
                  ) : (
                    <motion.button
                      whileTap={{ scale: 0.98 }}
                      onClick={() => {
                        setTempCharName(characterProfile.name);
                        setEditingCharName(true);
                      }}
                      className="w-full px-4 py-3 bg-muted rounded-xl text-left text-foreground"
                    >
                      {characterProfile.name}
                    </motion.button>
                  )}
                </div>
                <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                  <h3 className="font-semibold text-foreground mb-3 flex items-center gap-2">
                    <Calendar className="w-4 h-4" />
                    {translations.birthday}
                  </h3>
                  <div className="flex gap-2">
                    <select
                      value={charBirthMonth}
                      onChange={(e) => {
                        const newMonth = Number(e.target.value);
                        const maxDay = daysInMonth(newMonth);
                        const nextDay = Math.min(charBirthDay, maxDay);
                        setCharBirthMonth(newMonth);
                        setCharBirthDay(nextDay);
                        persistCharBirthday(newMonth, nextDay);
                      }}
                      className="flex-1 px-3 py-2 bg-muted rounded-xl text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                    >
                      {Array.from({ length: 12 }, (_, i) => i + 1).map((m) => (
                        <option key={m} value={m}>
                          {pad2(m)}{translations.monthSuffix ? ` ${translations.monthSuffix}` : ''}
                        </option>
                      ))}
                    </select>
                    <select
                      value={charBirthDay}
                      onChange={(e) => {
                        const newDay = Number(e.target.value);
                        setCharBirthDay(newDay);
                        persistCharBirthday(charBirthMonth, newDay);
                      }}
                      className="flex-1 px-3 py-2 bg-muted rounded-xl text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                    >
                      {Array.from({ length: daysInMonth(charBirthMonth) }, (_, i) => i + 1).map((d) => (
                        <option key={d} value={d}>
                          {pad2(d)}{translations.daySuffix ? ` ${translations.daySuffix}` : ''}
                        </option>
                      ))}
                    </select>
                  </div>
                  {charZodiacDisplay && charZodiac && (
                    <div className="mt-2 flex items-center gap-2 text-sm text-muted-foreground">
                      <span className="text-lg">{charZodiacDisplay.symbol}</span>
                      <span>{zodiacT()[charZodiac]}</span>
                    </div>
                  )}
                </div>
              </TabsContent>

              {/* Character Message Sub-tab */}
              <TabsContent value="char-message" className="p-4 space-y-4 mt-0">
                <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                  <h3 className="font-semibold text-foreground mb-3 flex items-center gap-2">
                    <Music className="w-4 h-4" />
                    {translations.voiceTone}
                  </h3>
                  <div className="grid grid-cols-3 gap-2">
                    {TONE_OPTIONS.map((option) => (
                      <motion.button
                        key={option.id}
                        whileTap={{ scale: 0.95 }}
                        onClick={() => setCharacterVoiceTone(option.id)}
                        className={`flex flex-col items-center gap-1 px-2 py-2 rounded-xl transition-all ${characterProfile.voiceTone === option.id ? "bg-primary text-primary-foreground" : "bg-muted text-muted-foreground"}`}
                      >
                        <span className="text-lg">{option.emoji}</span>
                        <span className="text-xs">{option.label}</span>
                      </motion.button>
                    ))}
                  </div>
                </div>
                <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                  <h3 className="font-semibold text-foreground mb-3 flex items-center gap-2">
                    <MessageCircle className="w-4 h-4" />
                    {translations.autoMessage}
                  </h3>
                  <motion.button
                    whileTap={{ scale: 0.98 }}
                    onClick={() => setAutoMessageEnabled(!autoMessageEnabled)}
                    className={`w-full flex items-center justify-between px-4 py-3 rounded-xl transition-colors mb-3 ${autoMessageEnabled ? "bg-primary text-primary-foreground" : "bg-muted text-muted-foreground"}`}
                  >
                    <span className="text-sm">{autoMessageEnabled ? translations.on : translations.off}</span>
                    <div
                      className={`w-10 h-6 rounded-full transition-colors ${autoMessageEnabled ? "bg-white/30" : "bg-foreground/20"} p-0.5`}
                    >
                      <motion.div
                        animate={{ x: autoMessageEnabled ? 16 : 0 }}
                        className={`w-5 h-5 rounded-full ${autoMessageEnabled ? "bg-white" : "bg-foreground/50"}`}
                      />
                    </div>
                  </motion.button>
                  {autoMessageEnabled && (
                    <div className="space-y-2">
                      <div className="text-center">
                        <span className="text-xs font-medium text-primary">
                          {AUTO_MESSAGE_PRESETS.find((p) => p.id === autoMessagePreset)?.label}
                        </span>
                      </div>
                      <Slider
                        value={[getPresetIndex(autoMessagePreset)]}
                        onValueChange={(value) => setAutoMessagePreset(getPresetByIndex(value[0]).id)}
                        min={0}
                        max={3}
                        step={1}
                      />
                      <div className="flex justify-between text-[9px] text-muted-foreground">
                        {AUTO_MESSAGE_PRESETS.map((p) => (
                          <span key={p.id} className={autoMessagePreset === p.id ? "text-primary font-medium" : ""}>
                            {p.label}
                          </span>
                        ))}
                      </div>
                    </div>
                  )}
                </div>

                {/* Custom Messages Section */}
                <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                  <h3 className="font-semibold text-foreground mb-3 flex items-center gap-2">
                    <MessageCircle className="w-4 h-4" />
                    {translations.customMessages}
                  </h3>
                  {/* <p className="text-xs text-muted-foreground mb-3">
                    {translations.createScriptedConversations}
                  </p> */}
                  <CustomMessageCreator />
                </div>
              </TabsContent>

              {/* Character Advanced Sub-tab */}
              <TabsContent value="char-advanced" className="p-4 space-y-4 mt-0">
                <div className="bg-widget-bg rounded-2xl p-4 shadow-widget">
                  <h3 className="font-semibold text-foreground mb-3">{translations.preferences}</h3>
                  <p className="text-xs text-muted-foreground mb-3">{translations.setFavorites(characterProfile.name)}</p>
                  <div className="space-y-3">
                    <div>
                      <label className="flex items-center gap-1 text-xs text-foreground mb-1">
                        <Utensils className="w-3 h-3" />
                        {translations.food}
                      </label>
                      <div className="flex gap-2">
                        <input
                          value={favoriteFood}
                          onChange={(e) => setFavoriteFood(e.target.value)}
                          placeholder={translations.foodPlaceholder}
                          className="flex-1 px-2 py-1.5 bg-muted rounded-lg text-xs text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                        />
                        <motion.button
                          whileTap={{ scale: 0.95 }}
                          onClick={() => {
                            setPlayerFavoriteFood(favoriteFood.trim());
                            toast.success(translations.saved);
                          }}
                          className="px-2 py-1.5 bg-primary text-primary-foreground rounded-lg text-xs"
                        >
                          {translations.save}
                        </motion.button>
                      </div>
                    </div>
                    <div>
                      <label className="flex items-center gap-1 text-xs text-foreground mb-1">
                        <MapPin className="w-3 h-3" />
                        {translations.place}
                      </label>
                      <div className="flex gap-2">
                        <input
                          value={favoritePlace}
                          onChange={(e) => setFavoritePlace(e.target.value)}
                          placeholder={translations.placePlaceholder}
                          className="flex-1 px-2 py-1.5 bg-muted rounded-lg text-xs text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                        />
                        <motion.button
                          whileTap={{ scale: 0.95 }}
                          onClick={() => {
                            setPlayerFavoritePlace(favoritePlace.trim());
                            toast.success(translations.saved);
                          }}
                          className="px-2 py-1.5 bg-primary text-primary-foreground rounded-lg text-xs"
                        >
                          {translations.save}
                        </motion.button>
                      </div>
                    </div>
                    <div>
                      <label className="flex items-center gap-1 text-xs text-foreground mb-1">
                        <Music className="w-3 h-3" />
                        {translations.music}
                      </label>
                      <div className="flex gap-2">
                        <input
                          value={favoriteMusic}
                          onChange={(e) => setFavoriteMusic(e.target.value)}
                          placeholder={translations.musicPlaceholder}
                          className="flex-1 px-2 py-1.5 bg-muted rounded-lg text-xs text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                        />
                        <motion.button
                          whileTap={{ scale: 0.95 }}
                          onClick={() => {
                            setPlayerFavoriteMusic(favoriteMusic.trim());
                            toast.success(translations.saved);
                          }}
                          className="px-2 py-1.5 bg-primary text-primary-foreground rounded-lg text-xs"
                        >
                          {translations.save}
                        </motion.button>
                      </div>
                    </div>
                  </div>
                </div>
              </TabsContent>
            </Tabs>
          </TabsContent>

          {/* Theme Tab */}
          <TabsContent value="theme" className="p-4 space-y-4 mt-0">
            {/* Theme Presets */}
            <div className="bg-widget-bg rounded-2xl p-4 shadow-widget mx-0">
              <h3 className="font-semibold text-foreground mb-3">{translations.theme}</h3>
              <div className="grid grid-cols-2 gap-2">
                {themePresetOptions.map((option) => (
                  <motion.button
                    key={option.id}
                    whileTap={{ scale: 0.95 }}
                    onClick={() => setSelectedTheme(option.id)}
                    className={`flex items-center gap-2 px-3 py-3 rounded-xl transition-all ${selectedTheme === option.id ? "ring-2 ring-primary ring-offset-2" : "bg-muted"}`}
                  >
                    <div className="flex gap-1">
                      {option.colors.map((c, i) => (
                        <div key={i} className="w-4 h-4 rounded-full" style={{ backgroundColor: c }} />
                      ))}
                    </div>
                    <span className="text-sm text-foreground">{option.label}</span>
                  </motion.button>
                ))}
              </div>
            </div>

            {/* Advanced Colors Dropdown */}
            <div className="bg-widget-bg rounded-2xl p-4 shadow-widget mx-0">
              <motion.button
                whileTap={{ scale: 0.98 }}
                onClick={() => setShowAdvancedColors(!showAdvancedColors)}
                className="w-full flex items-center justify-between"
              >
                <h3 className="font-semibold text-foreground flex items-center gap-2">
                  <Palette className="w-4 h-4" />
                  {translations.advancedColors}
                </h3>
                <ChevronDown
                  className={`w-5 h-5 text-muted-foreground transition-transform ${showAdvancedColors ? "rotate-180" : ""}`}
                />
              </motion.button>
              <AnimatePresence>
                {showAdvancedColors && (
                  <motion.div
                    initial={{ height: 0, opacity: 0 }}
                    animate={{ height: "auto", opacity: 1 }}
                    exit={{ height: 0, opacity: 0 }}
                    className="overflow-hidden"
                  >
                    <div className="pt-4 space-y-3">
                      {hasOverrides && (
                        <motion.button
                          whileTap={{ scale: 0.95 }}
                          onClick={resetColorOverrides}
                          className="w-full flex items-center justify-center gap-2 px-4 py-2 bg-muted rounded-xl text-sm text-foreground"
                        >
                          <RotateCcw className="w-4 h-4" />
                          {translations.resetAdvancedOverrides}
                        </motion.button>
                      )}
                      {ADVANCED_COLOR_KEYS.map((key) => (
                        <ColorPicker
                          key={key}
                          label={PALETTE_LABELS[key]}
                          value={colorOverrides[key] || currentPalette[key]}
                          onChange={(value) => setColorOverride(key, value)}
                        />
                      ))}
                    </div>
                  </motion.div>
                )}
              </AnimatePresence>
            </div>

            {/* Background Gradient */}
            <div className="bg-widget-bg rounded-2xl p-4 shadow-widget mx-0">
              <h3 className="font-semibold text-foreground mb-3">{translations.background}</h3>
              <div className="flex gap-2 justify-center">
                {backgroundGradientOptions.map((option) => (
                  <motion.button
                    key={option.id}
                    whileTap={{ scale: 0.95 }}
                    onClick={() => setBackgroundGradient(option.id)}
                    className={`w-10 h-10 rounded-full ${option.className} border-2 transition-all ${backgroundGradient === option.id ? "border-primary ring-2 ring-primary ring-offset-2" : "border-border"}`}
                    title={option.label}
                  />
                ))}
              </div>
            </div>

            {/* Chat wallpaper */}
            <div className="bg-widget-bg rounded-2xl p-4 shadow-widget mx-0">
              <h3 className="font-semibold text-foreground mb-3">{translations.chatWallpaper}</h3>
              <div className="grid grid-cols-4 gap-2">
                {chatWallpaperOptions.map((option) => (
                  <motion.button
                    key={option.id}
                    whileTap={{ scale: 0.95 }}
                    onClick={() => setChatWallpaper(option.id)}
                    className={`flex flex-col items-center gap-2 p-3 rounded-xl transition-all ${chatWallpaper === option.id ? "ring-2 ring-primary ring-offset-2" : ""}`}
                  >
                    <div className={`w-10 h-10 ${option.color} rounded-lg flex items-center justify-center`}>
                      <option.icon className="w-5 h-5 text-foreground/70" />
                    </div>
                    {/* <span className="text-xs text-muted-foreground">{option.label}</span> */}
                  </motion.button>
                ))}
              </div>
            </div>
          </TabsContent>

          {/* General Tab */}
          <TabsContent value="general" className="p-4 space-y-4 mt-0">
            {/* Language */}
            <div className="bg-widget-bg rounded-2xl p-4 shadow-widget mx-0">
              <h3 className="font-semibold text-foreground mb-3 flex items-center gap-2">
                <Globe className="w-4 h-4" />
                {translations.language}
              </h3>
              <div className="flex gap-2">
                {availableLanguages.map((lang) => (
                  <motion.button
                    key={lang.code}
                    whileTap={{ scale: 0.95 }}
                    onClick={() => setLanguage(lang.code)}
                    className={`flex-1 px-4 py-3 rounded-xl text-sm font-medium transition-all ${language === lang.code
                      ? "bg-primary text-primary-foreground"
                      : "bg-muted text-foreground"
                      }`}
                  >
                    {lang.label}
                  </motion.button>
                ))}
              </div>
            </div>

            {/* Export Data */}
            <div className="bg-widget-bg rounded-2xl p-4 shadow-widget mx-0">
              <h3 className="font-semibold text-foreground mb-3 flex items-center gap-2">
                <Download className="w-4 h-4" />
                {translations.exportData}
              </h3>
              <motion.button
                whileTap={{ scale: 0.95 }}
                onClick={async () => {
                  try {
                    await downloadBackup();
                    toast.success(translations.dataExported);
                  } catch (e) {
                    toast.error(translations.failedToExport);
                  }
                }}
                className="w-full px-4 py-3 bg-muted rounded-xl text-foreground flex items-center justify-center gap-2"
              >
                <Download className="w-4 h-4" />
                {translations.saveToFile}
              </motion.button>
            </div>

            {/* Import Data */}
            <div className="bg-widget-bg rounded-2xl p-4 shadow-widget mx-0">
              <h3 className="font-semibold text-foreground mb-3 flex items-center gap-2">
                <Upload className="w-4 h-4" />
                {translations.importData}
              </h3>
              <input
                type="file"
                accept=".cbak"
                id="import-file"
                className="hidden"
                onChange={async (e) => {
                  const file = e.target.files?.[0];
                  if (!file) return;
                  try {
                    const data = await readBackupFile(file);
                    await importAllData(data);

                    const maybeAppStorage = data.appStorage as any;
                    const importedState = maybeAppStorage?.state ?? maybeAppStorage;

                    if (importedState && typeof importedState === "object") {
                      // Replace entire store state to avoid persist overwrite
                      useAppStore.setState(importedState, true);
                    }

                    toast.success(translations.dataImported);
                    setTimeout(() => window.location.reload(), 1000);
                    // window.location.reload();

                    console.log("Imported character name:", importedState.characterProfile?.name);
                    console.log("Imported player name:", importedState.playerProfile?.name);

                  } catch (err) {
                    toast.error(err instanceof Error ? err.message : translations.failedToImport);
                  }
                  e.target.value = "";
                }}
              />
              <motion.button
                whileTap={{ scale: 0.95 }}
                onClick={() => document.getElementById("import-file")?.click()}
                className="w-full px-4 py-3 bg-muted rounded-xl text-foreground flex items-center justify-center gap-2"
              >
                <Upload className="w-4 h-4" />
                {translations.loadFromFile}
              </motion.button>
            </div>

            {/* Reset App */}
            <div className="bg-widget-bg rounded-2xl p-4 shadow-widget mx-0">
              <h3 className="font-semibold text-foreground mb-3 flex items-center gap-2">
                <Trash2 className="w-4 h-4" />
                {translations.resetApp}
              </h3>
              <AlertDialog>
                <AlertDialogTrigger asChild>
                  <motion.button
                    whileTap={{ scale: 0.95 }}
                    className="w-full px-4 py-3 bg-destructive/10 rounded-xl text-destructive flex items-center justify-center gap-2"
                  >
                    <RotateCcw className="w-4 h-4" />
                    {translations.resetAllData}
                  </motion.button>
                </AlertDialogTrigger>
                <AlertDialogContent className="max-w-[300px] rounded-2xl">
                  <AlertDialogHeader>
                    <AlertDialogTitle>{translations.resetAppQuestion}</AlertDialogTitle>
                    <AlertDialogDescription>
                      {translations.resetAppDescription}
                    </AlertDialogDescription>
                  </AlertDialogHeader>
                  <AlertDialogFooter>
                    <AlertDialogCancel>{translations.cancel}</AlertDialogCancel>
                    <AlertDialogAction
                      onClick={() => {
                        localStorage.clear();
                        toast.success(translations.appReset);
                        setTimeout(() => window.location.reload(), 500);
                      }}
                      className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
                    >
                      {translations.reset}
                    </AlertDialogAction>
                  </AlertDialogFooter>
                </AlertDialogContent>
              </AlertDialog>
              <p className="text-xs text-muted-foreground text-center mt-2">
                {translations.clearSettingsNote}
              </p>
            </div>
          </TabsContent>
        </div>
      </Tabs>
    </motion.div>
  );
};

export default SettingsScreen;
