const CharacterAvatar: React.FC<CharacterAvatarProps> = ({ size = "md", onClick, interactive = true }) => {
  const { characterProfile } = useAppStore();
  const { avatarUrl, isLoading } = useCharacterAvatar();

  const sizeClasses = {
    xs: "w-10 h-10",
    sm: "w-10 h-10",
    md: "w-16 h-16",
    lg: "w-24 h-24",
    xl: "w-32 h-32",
  };

  const fontSizes = {
    xs: "text-sm",
    sm: "text-lg",
    md: "text-2xl",
    lg: "text-4xl",
    xl: "text-5xl",
  };

  const iconSizes = {
    xs: "w-4 h-4",
    sm: "w-6 h-6",
    md: "w-10 h-10",
    lg: "w-16 h-16",
    xl: "w-20 h-20",
  };

  // If custom avatar exists, show it
  if (avatarUrl && !isLoading) {
    return (
      <motion.div
        whileHover={interactive ? { scale: 1.05 } : undefined}
        whileTap={interactive ? { scale: 0.95 } : undefined}
        onClick={interactive ? onClick : undefined}
        className={`
          ${sizeClasses[size]}
          rounded-full
          bg-gradient-to-br from-primary/30 to-secondary
          flex items-center justify-center
          shadow-md overflow-hidden
          ${interactive ? "cursor-pointer" : "cursor-default"}
          ${interactive ? "" : "pointer-events-none"}
        `}
      >
        <img src={avatarUrl} alt="Character avatar" className="w-full h-full object-cover" />
      </motion.div>
    );
  }

  // Show emoji avatar if set and no custom image
  // const emoji = avatarEmojis[characterProfile.avatar];
  // if (emoji) {
  //   return (
  //     <motion.div
  //       whileHover={interactive ? { scale: 1.05 } : undefined}
  //       whileTap={interactive ? { scale: 0.95 } : undefined}
  //       onClick={interactive ? onClick : undefined}
  //       className={`
  //         ${sizeClasses[size]}
  //         rounded-full
  //         bg-gradient-to-br from-primary/30 to-secondary
  //         flex items-center justify-center
  //         shadow-md overflow-hidden
  //         ${interactive ? "cursor-pointer" : "cursor-default"}
  //         ${interactive ? "" : "pointer-events-none"}
  //       `}
  //     >
  //       <span className={fontSizes[size]}>{emoji}</span>
  //     </motion.div>
  //   );
  // }

  // Default: show CircleUserRound icon
  return (
    <motion.div
      whileHover={interactive ? { scale: 1.05 } : undefined}
      whileTap={interactive ? { scale: 0.95 } : undefined}
      onClick={interactive ? onClick : undefined}
      className={`
        ${sizeClasses[size]}
        rounded-full
        bg-gradient-to-br from-primary/30 to-secondary
        flex items-center justify-center
        shadow-md overflow-hidden
        ${interactive ? "cursor-pointer" : "cursor-default"}
        ${interactive ? "" : "pointer-events-none"}
      `}
    >
      <CircleUserRound className={`${iconSizes[size]} text-muted-foreground`} />
    </motion.div>
  );
};

export default CharacterAvatar;
