import React from 'react';
import { motion } from 'framer-motion';
import { Message } from '@/stores/appStore';
import CharacterAvatar from './CharacterAvatar';

interface MessageBubbleProps {
  message: Message;
}

const TypingIndicator: React.FC = () => (
  <div className="flex gap-1 items-center h-5">
    {[0, 1, 2].map((i) => (
      <motion.div
        key={i}
        className="w-2 h-2 bg-muted-foreground/60 rounded-full"
        animate={{ y: [0, -4, 0] }}
        transition={{
          duration: 0.6,
          repeat: Infinity,
          delay: i * 0.15,
          ease: 'easeInOut',
        }}
      />
    ))}
  </div>
);

const MessageBubble: React.FC<MessageBubbleProps> = ({ message }) => {
  const isUser = message.isUser;

  // System message (centered, no bubble)
  if (message.isSystem) {
    return (
      <motion.div
        initial={{ opacity: 0, y: 10 }}
        animate={{ opacity: 1, y: 0 }}
        className="flex justify-center py-2"
      >
        <p className="text-xs text-muted-foreground text-center px-4 py-1.5 bg-muted/50 rounded-full">
          {message.content}
        </p>
      </motion.div>
    );
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20, scale: 0.9 }}
      animate={{ opacity: 1, y: 0, scale: 1 }}
      transition={{ type: 'spring', damping: 25, stiffness: 300 }}
      className={`flex items-end gap-2 ${isUser ? 'flex-row-reverse' : 'flex-row'}`}
    >
      {!isUser && <CharacterAvatar size="xs" />}
      
      <div
        className={`max-w-[75%] px-4 py-2.5 rounded-2xl ${
          isUser
            ? 'bg-bubble-user text-bubble-user-foreground rounded-br-md'
            : 'bg-bubble-character text-bubble-character-foreground rounded-bl-md shadow-sm'
        }`}
      >
        {message.isTyping ? (
          <TypingIndicator />
        ) : (
          <p className={`${isUser && !message.isScripted ? 'text-2xl' : 'text-sm leading-relaxed'} whitespace-pre-line`}>
            {message.content}
          </p>
        )}
      </div>
    </motion.div>
  );
};

export default MessageBubble;
