type JosaKey = "" | "" | "" | "" | "";

/**
 *    ()  .
 * -  (~)  null
 */
function getHangulJongseongCode(char: string): number | null {
    const code = char.charCodeAt(0);
    const HANGUL_START = 0xac00;
    const HANGUL_END = 0xd7a3;
    if (code < HANGUL_START || code > HANGUL_END) return null;

    const syllableIndex = code - HANGUL_START;
    const jong = syllableIndex % 28; // 0  , 1~27  
    return jong;
}

/**
 *    
 * - "/"  '' '' ( )
 */
export function pickJosa(word: string, key: JosaKey): string {
    const trimmed = (word ?? "").trim();
    if (!trimmed) {
        //  (  )
        if (key === "") return "";
        if (key === "") return "";
        if (key === "") return "";
        if (key === "") return "";
        if (key === "") return "";
        return "";
    }

    const lastChar = trimmed[trimmed.length - 1];
    const jong = getHangulJongseongCode(lastChar);

    //  :     (   )
    if (jong === null) {
        if (key === "") return "";
        if (key === "") return "";
        if (key === "") return "";
        if (key === "") return "";
        if (key === "") return "";
        return "";
    }

    const hasBatchim = jong !== 0;

    if (key === "") return hasBatchim ? "" : "";
    if (key === "") return hasBatchim ? "" : "";
    if (key === "") return hasBatchim ? "" : "";
    if (key === "") return hasBatchim ? "" : "";

    // / :
    // -  : 
    // -  : 
    // - ,  (  8)  
    if (key === "") {
        if (!hasBatchim) return "";
        const JONG_L = 8; // 
        return jong === JONG_L ? "" : "";
    }

    return "";
}

/**
 *  {playerName}{}    
 * vars: { playerName: "" }  
 */
export function applyJosaTemplate(
    template: string,
    vars: Record<string, string | number>
): string {
    return template.replace(
        /\{(\w+)(?::(||||))?\}/g,
        (_, key: string, josaKey?: JosaKey) => {
            const value = String(vars[key] ?? "");

            if (!josaKey) {
                //     
                return value;
            }

            return value + pickJosa(value, josaKey);
        }
    );
}