// Template text utility for replacing placeholders like {playerName}

export interface TemplateContext {
  playerName?: string;
  characterName?: string;
}

const DEFAULT_FALLBACK = 'there';

/**
 * Replaces template placeholders in text with actual values.
 * Supports: {playerName}, {characterName}
 * Falls back to empty string or generic term if value is empty.
 */
export function processTemplate(text: string, context: TemplateContext): string {
  let result = text;
  
  // Replace {playerName} - fall back to empty string if not set
  const playerName = context.playerName?.trim() || DEFAULT_FALLBACK;
  result = result.replace(/\{playerName\}/g, playerName);
  
  // Clean up any double spaces that might result from empty replacements
  result = result.replace(/\s{2,}/g, ' ').trim();
  
  // Replace {characterName}
  if (context.characterName) {
    result = result.replace(/\{characterName\}/g, context.characterName);
  }
  
  return result;
}

/**
 * Creates a template processor bound to a specific context
 */
export function createTemplateProcessor(context: TemplateContext) {
  return (text: string) => processTemplate(text, context);
}
