// YouTubePlayer.tsx
import React, { useEffect, useRef, useState, useCallback } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Music, Waves, Play, Square, SkipForward, X } from "lucide-react";

// Ambient sounds configuration
const AMBIENT_SOUNDS = [
  { id: "rain", icon: "", label: "Rain" },
  { id: "forest", icon: "", label: "Forest" },
  { id: "ocean", icon: "", label: "Ocean" },
  { id: "fire", icon: "", label: "Fire" },
  { id: "wind", icon: "", label: "Wind" },
];

// Lofi Radio playlist - multiple YouTube Live URLs (id + per-playlist title)
const LOFI_PLAYLIST: Array<{ id: string; title: string }> = [
  { id: "jfKfPfyJRdk", title: "Lofi Girl Radio" },
  { id: "qH3fETPsqXU", title: "Chill Lofi Stream" },
  { id: "5yx6BWlEVcY", title: "Chillhop Radio" },
  { id: "4xDzrJKXOOY", title: "Synthwave Radio" },
  { id: "b9uAduDqP7Q", title: "Led Zeppelin Lofi" },
];

interface YouTubePlayerProps {
  className?: string;
  isGlobal?: boolean;
}

declare global {
  interface Window {
    YT: {
      Player: new (
        elementId: string,
        options: {
          videoId: string;
          playerVars?: Record<string, number | string>;
          events?: {
            onReady?: (event: { target: YTPlayer }) => void;
            onStateChange?: (event: { data: number }) => void;
            onError?: (event: { data: number }) => void;
          };
        }
      ) => YTPlayer;
      PlayerState: {
        PLAYING: number;
        PAUSED: number;
        ENDED: number;
      };
    };
    onYouTubeIframeAPIReady?: () => void;
    lofiPlayerRef?: YTPlayer | null;
    lofiPlayerReady?: boolean;
    lofiPlayerPlaying?: boolean;
    lofiPlayerCurrentIndex?: number;
  }
}

interface YTPlayer {
  playVideo: () => void;
  pauseVideo: () => void;
  stopVideo: () => void;
  mute: () => void;
  unMute: () => void;
  isMuted: () => boolean;
  setVolume: (volume: number) => void;
  getVolume: () => number;
  loadVideoById: (videoId: string) => void;
  destroy: () => void;
}

// Global YouTube Player component - renders hidden iframe globally
export const GlobalYouTubePlayer: React.FC = () => {
  const playerRef = useRef<YTPlayer | null>(null);
  const [currentVideoIndex, setCurrentVideoIndex] = useState(0);

  const initializePlayer = useCallback(
    (videoId: string) => {
      if (playerRef.current) {
        playerRef.current.destroy();
      }

      playerRef.current = new window.YT.Player("youtube-player-global", {
        videoId,
        playerVars: {
          autoplay: 0,
          controls: 0,
          disablekb: 1,
          fs: 0,
          modestbranding: 1,
          playsinline: 1,
          rel: 0,
        },
        events: {
          onReady: () => {
            window.lofiPlayerRef = playerRef.current;
            window.lofiPlayerReady = true;
            window.lofiPlayerCurrentIndex = currentVideoIndex;
          },
          onStateChange: (event) => {
            if (event.data === window.YT.PlayerState.PLAYING) {
              window.lofiPlayerPlaying = true;
            } else if (
              event.data === window.YT.PlayerState.PAUSED ||
              event.data === window.YT.PlayerState.ENDED
            ) {
              window.lofiPlayerPlaying = false;
            }
          },
          onError: () => {
            console.log("YouTube player error");
          },
        },
      });

      window.lofiPlayerRef = playerRef.current;
    },
    [currentVideoIndex]
  );

  useEffect(() => {
    // Load YouTube IFrame API
    if (!document.getElementById("youtube-api-script")) {
      const tag = document.createElement("script");
      tag.id = "youtube-api-script";
      tag.src = "https://www.youtube.com/iframe_api";
      const firstScriptTag = document.getElementsByTagName("script")[0];
      firstScriptTag.parentNode?.insertBefore(tag, firstScriptTag);
    }

    const initPlayer = () => {
      initializePlayer(LOFI_PLAYLIST[currentVideoIndex].id);
    };

    if (window.YT && window.YT.Player) {
      initPlayer();
    } else {
      window.onYouTubeIframeAPIReady = initPlayer;
    }

    return () => {
      // Don't destroy on unmount since this is global
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div
      id="youtube-player-global"
      className="fixed opacity-0 pointer-events-none"
      style={{ width: "1px", height: "1px", top: "-9999px", left: "-9999px" }}
    />
  );
};

// UI Controls component - can be rendered anywhere
const YouTubePlayer: React.FC<YouTubePlayerProps> = ({ className, isGlobal = false }) => {
  const [isReady, setIsReady] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);
  const [showAmbient, setShowAmbient] = useState(false);
  const [selectedAmbient, setSelectedAmbient] = useState<string | null>(null);
  const [currentVideoIndex, setCurrentVideoIndex] = useState(0);

  // Poll for global player state
  useEffect(() => {
    const interval = setInterval(() => {
      setIsReady(window.lofiPlayerReady || false);
      setIsPlaying(window.lofiPlayerPlaying || false);
      setCurrentVideoIndex(window.lofiPlayerCurrentIndex || 0);
    }, 100);
    return () => clearInterval(interval);
  }, []);

  const handlePlay = () => {
    if (!window.lofiPlayerRef || !isReady) return;
    window.lofiPlayerRef.unMute();
    window.lofiPlayerRef.setVolume(50);
    window.lofiPlayerRef.playVideo();
  };

  const handleStop = () => {
    if (!window.lofiPlayerRef || !isReady) return;
    window.lofiPlayerRef.pauseVideo();
  };

  const handleNext = () => {
    if (!window.lofiPlayerRef || !isReady) return;

    let nextIndex = (currentVideoIndex + 1) % LOFI_PLAYLIST.length;
    if (LOFI_PLAYLIST.length > 1 && nextIndex === currentVideoIndex) {
      nextIndex = (nextIndex + 1) % LOFI_PLAYLIST.length;
    }

    setCurrentVideoIndex(nextIndex);
    window.lofiPlayerCurrentIndex = nextIndex;

    window.lofiPlayerRef.loadVideoById(LOFI_PLAYLIST[nextIndex].id);
    window.lofiPlayerRef.unMute();
    window.lofiPlayerRef.setVolume(50);
  };

  const handleAmbientSelect = (ambientId: string) => {
    setSelectedAmbient(ambientId === selectedAmbient ? null : ambientId);
    // TODO: Implement actual ambient sound playback
  };

  const playlistTitle = LOFI_PLAYLIST[currentVideoIndex]?.title ?? "";

  return (
    <div
      className={`bg-widget-bg rounded-3xl px-[16px] py-[16px] my-[10px] relative shadow-widget mx-0 ${className}`}
    >
      <AnimatePresence mode="wait">
        {showAmbient ? (
          <motion.div
            key="ambient"
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            className="space-y-2"
          >
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-2">
                <Waves className="w-4 h-4 text-secondary-foreground" />
                <span className="text-sm font-medium text-foreground">Ambient Sounds</span>
              </div>
              <motion.button
                whileTap={{ scale: 0.9 }}
                onClick={() => setShowAmbient(false)}
                className="p-1 rounded-full hover:bg-muted"
              >
                <X className="w-5 h-5 text-muted-foreground" />
              </motion.button>
            </div>

            <div className="flex justify-around gap-2">
              {AMBIENT_SOUNDS.map((sound) => (
                <motion.button
                  key={sound.id}
                  whileHover={{ scale: 1.1 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={() => handleAmbientSelect(sound.id)}
                  className={`w-12 h-12 rounded-full flex items-center justify-center text-xl transition-colors ${selectedAmbient === sound.id
                    ? "bg-primary text-primary-foreground"
                    : "bg-muted hover:bg-muted/80"
                    }`}
                  title={sound.label}
                >
                  {sound.icon}
                </motion.button>
              ))}
            </div>
          </motion.div>
        ) : (
          <motion.div
            key="player"
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            className="space-y-2"
          >
            {/* Title row with playing indicator + playlist title */}
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-2 min-w-0">
                <Music className="w-4 h-4 text-primary flex-shrink-0" />
                <span className="text-sm font-medium text-foreground leading-tight flex-shrink-0">
                  Lofi Radio
                </span>
                <span className="text-xs text-muted-foreground truncate">
                  {playlistTitle}
                </span>

                {isPlaying && (
                  <motion.div
                    animate={{ opacity: [0.5, 1, 0.5] }}
                    transition={{ duration: 1.5, repeat: Infinity }}
                    className="flex items-center gap-0.5 flex-shrink-0"
                  >
                    <span className="w-1 h-3 bg-primary rounded-full animate-pulse" />
                    <span
                      className="w-1 h-4 bg-primary rounded-full animate-pulse"
                      style={{ animationDelay: "0.2s" }}
                    />
                    <span
                      className="w-1 h-2 bg-primary rounded-full animate-pulse"
                      style={{ animationDelay: "0.4s" }}
                    />
                  </motion.div>
                )}
              </div>
            </div>

            {/* Controls row: Ambient Sounds button moved to the far right (2nd row) */}
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-1">
                {/* Play button */}
                <motion.button
                  whileHover={{ scale: 1.1 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={handlePlay}
                  disabled={!isReady || isPlaying}
                  className={`p-2 rounded-full transition-colors ${isPlaying
                    ? "bg-muted/50 text-muted-foreground/50 cursor-not-allowed"
                    : "bg-primary text-primary-foreground hover:bg-primary/90"
                    } ${!isReady ? "opacity-50" : ""}`}
                  title="Play"
                >
                  <Play className="w-4 h-4" />
                </motion.button>

                {/* Stop button */}
                <motion.button
                  whileHover={{ scale: 1.1 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={handleStop}
                  disabled={!isReady || !isPlaying}
                  className={`p-2 rounded-full transition-colors ${!isPlaying
                    ? "bg-muted/50 text-muted-foreground/50 cursor-not-allowed"
                    : "bg-muted text-foreground hover:bg-muted/80"
                    } ${!isReady ? "opacity-50" : ""}`}
                  title="Stop"
                >
                  <Square className="w-4 h-4" />
                </motion.button>

                {/* Next button */}
                <motion.button
                  whileHover={{ scale: 1.1 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={handleNext}
                  disabled={!isReady}
                  className={`p-2 rounded-full bg-muted text-foreground hover:bg-muted/80 transition-colors ${!isReady ? "opacity-50" : ""
                    }`}
                  title="Next Station"
                >
                  <SkipForward className="w-4 h-4" />
                </motion.button>
              </div>

              {/* Ambient button (now on the far right of the 2nd row) */}
              <motion.button
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
                onClick={() => setShowAmbient(true)}
                className="p-2 rounded-full bg-muted text-foreground hover:bg-muted/80 transition-colors"
                title="Ambient Sounds"
              >
                <Waves className="w-4 h-4 text-secondary-foreground" />
              </motion.button>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default YouTubePlayer;