import React, { useEffect } from 'react';
import { AnimatePresence } from 'framer-motion';
import PhoneFrame from '@/components/phone/PhoneFrame';
import HomeScreen from '@/components/screens/HomeScreen';
import ChatScreen from '@/components/screens/ChatScreen';
import SettingsScreen from '@/components/screens/SettingsScreen';
import RecordsScreen from '@/components/screens/RecordsScreen';
import CharacterProfileScreen from '@/components/screens/CharacterProfileScreen';
import OnboardingScreen from '@/components/screens/OnboardingScreen';
import { GlobalYouTubePlayer } from '@/components/widgets/YouTubePlayer';
import { useAppStore } from '@/stores/appStore';
import { useAutoMessage } from '@/hooks/useAutoMessage';

const Index = () => {
  const { 
    currentScreen, 
    backgroundGradient, 
    updateConsecutiveDays, 
    incrementUsageTime, 
    checkAndGrantHourlyReward,
    onboardingCompleted,
    setOnboardingCompleted,
    applyTheme
  } = useAppStore();

  // Initialize auto message system
  useAutoMessage();

  useEffect(() => {
    // Apply theme on mount
    applyTheme();
  }, [applyTheme]);

  useEffect(() => {
    updateConsecutiveDays();
    checkAndGrantHourlyReward();
  }, [updateConsecutiveDays, checkAndGrantHourlyReward]);

  useEffect(() => {
    const interval = setInterval(() => {
      incrementUsageTime();
      checkAndGrantHourlyReward();
    }, 60000);
    return () => clearInterval(interval);
  }, [incrementUsageTime, checkAndGrantHourlyReward]);

  const getBackgroundClass = () => {
    if (backgroundGradient === 'none') return '';
    return `bg-gradient-calm-${backgroundGradient.replace('calm-', '')}`;
  };

  const handleOnboardingComplete = () => {
    setOnboardingCompleted(true);
  };

  const renderScreen = () => {
    // Show onboarding if not completed
    if (!onboardingCompleted) {
      return <OnboardingScreen onComplete={handleOnboardingComplete} />;
    }

    switch (currentScreen) {
      case 'chat':
        return <ChatScreen />;
      case 'settings':
        return <SettingsScreen />;
      case 'records':
        return <RecordsScreen />;
      case 'profile':
        return <CharacterProfileScreen />;
      case 'home':
      default:
        return <HomeScreen />;
    }
  };

  return (
    <div className={`min-h-screen w-full flex items-center justify-center ${getBackgroundClass()}`}>
      {/* Global YouTube Player - persists across all views */}
      <GlobalYouTubePlayer />
      
      <PhoneFrame>
        <AnimatePresence mode="wait">
          {renderScreen()}
        </AnimatePresence>
      </PhoneFrame>
    </div>
  );
};

export default Index;
