// Comprehensive backup/restore utility for all user data

import { getAvatarBlob, saveAvatarBlob, deleteAvatarBlob } from './avatarStorage';

// Current backup schema version
export const BACKUP_VERSION = 2;

// Encryption key (embedded in app - prevents casual editing)
const ENCRYPTION_KEY = 'companion-backup-key-2024-secure';
const ENCRYPTION_SALT = new Uint8Array([0x43, 0x6f, 0x6d, 0x70, 0x61, 0x6e, 0x69, 0x6f, 0x6e, 0x41, 0x70, 0x70, 0x42, 0x61, 0x63, 0x6b]);

// All localStorage keys used by the app
const STORAGE_KEYS = {
  appStorage: 'companion-app-storage',
  emotionState: 'emotion-engine-state',
  customMessages: 'custom-messages-library',
} as const;

/**
 * Derive a CryptoKey from the embedded password
 */
async function deriveKey(): Promise<CryptoKey> {
  const encoder = new TextEncoder();
  const keyMaterial = await crypto.subtle.importKey(
    'raw',
    encoder.encode(ENCRYPTION_KEY),
    'PBKDF2',
    false,
    ['deriveKey']
  );
  
  return crypto.subtle.deriveKey(
    {
      name: 'PBKDF2',
      salt: ENCRYPTION_SALT,
      iterations: 100000,
      hash: 'SHA-256',
    },
    keyMaterial,
    { name: 'AES-GCM', length: 256 },
    false,
    ['encrypt', 'decrypt']
  );
}

/**
 * Encrypt data using AES-GCM
 */
async function encryptData(data: string): Promise<string> {
  const key = await deriveKey();
  const encoder = new TextEncoder();
  const iv = crypto.getRandomValues(new Uint8Array(12));
  
  const encrypted = await crypto.subtle.encrypt(
    { name: 'AES-GCM', iv },
    key,
    encoder.encode(data)
  );
  
  // Combine IV + encrypted data and encode as base64
  const combined = new Uint8Array(iv.length + encrypted.byteLength);
  combined.set(iv, 0);
  combined.set(new Uint8Array(encrypted), iv.length);
  
  return btoa(String.fromCharCode(...combined));
}

/**
 * Decrypt data using AES-GCM
 */
async function decryptData(encryptedBase64: string): Promise<string> {
  const key = await deriveKey();
  
  // Decode base64 and split IV from encrypted data
  const combined = new Uint8Array(
    atob(encryptedBase64).split('').map(c => c.charCodeAt(0))
  );
  const iv = combined.slice(0, 12);
  const encrypted = combined.slice(12);
  
  const decrypted = await crypto.subtle.decrypt(
    { name: 'AES-GCM', iv },
    key,
    encrypted
  );
  
  return new TextDecoder().decode(decrypted);
}

// Backup data structure
export interface BackupData {
  version: number;
  exportedAt: string;
  // Main Zustand store (all app state)
  appStorage: Record<string, unknown> | null;
  // Emotion engine state
  emotionState: Record<string, unknown> | null;
  // Character avatar as base64 (from IndexedDB)
  avatarBase64: string | null;
  avatarMimeType: string | null;
  // Custom message library
  customMessages: unknown[] | null;
  // Legacy field - kept for backward compatibility when importing old backups
  autoMessages?: Record<string, unknown[]> | null;
}

/**
 * Convert Blob to base64 string
 */
async function blobToBase64(blob: Blob): Promise<string> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = () => {
      const result = reader.result as string;
      // Remove the data URL prefix (e.g., "data:image/png;base64,")
      const base64 = result.split(',')[1] || '';
      resolve(base64);
    };
    reader.onerror = reject;
    reader.readAsDataURL(blob);
  });
}

/**
 * Convert base64 string to Blob
 */
function base64ToBlob(base64: string, mimeType: string): Blob {
  const byteCharacters = atob(base64);
  const byteNumbers = new Array(byteCharacters.length);
  for (let i = 0; i < byteCharacters.length; i++) {
    byteNumbers[i] = byteCharacters.charCodeAt(i);
  }
  const byteArray = new Uint8Array(byteNumbers);
  return new Blob([byteArray], { type: mimeType });
}


/**
 * Export all user data to a backup object
 */
export async function exportAllData(): Promise<BackupData> {
  // Get main app storage (Zustand), preserving the { state, version } structure
  let appStorage: Record<string, unknown> | null = null;
  try {
    const data = localStorage.getItem(STORAGE_KEYS.appStorage);
    if (data) {
      const parsed = JSON.parse(data);
      // Remove messages from backup - they don't need to be preserved
      // But keep the Zustand { state, version } wrapper structure
      if (parsed.state) {
        const { messages, ...restState } = parsed.state;
        appStorage = { ...parsed, state: restState };
      } else {
        // Legacy format without wrapper
        const { messages, ...rest } = parsed;
        appStorage = rest;
      }
    }
  } catch (e) {
    console.error('Failed to read app storage:', e);
  }

  // Get emotion state
  let emotionState: Record<string, unknown> | null = null;
  try {
    const data = localStorage.getItem(STORAGE_KEYS.emotionState);
    emotionState = data ? JSON.parse(data) : null;
  } catch (e) {
    console.error('Failed to read emotion state:', e);
  }

  // Get avatar from IndexedDB
  let avatarBase64: string | null = null;
  let avatarMimeType: string | null = null;
  try {
    const avatarBlob = await getAvatarBlob();
    if (avatarBlob) {
      avatarBase64 = await blobToBase64(avatarBlob);
      avatarMimeType = avatarBlob.type || 'image/png';
    }
  } catch (e) {
    console.error('Failed to read avatar from IndexedDB:', e);
  }

  // Get custom messages library
  let customMessages: unknown[] | null = null;
  try {
    const data = localStorage.getItem(STORAGE_KEYS.customMessages);
    customMessages = data ? JSON.parse(data) : null;
  } catch (e) {
    console.error('Failed to read custom messages:', e);
  }

  return {
    version: BACKUP_VERSION,
    exportedAt: new Date().toISOString(),
    appStorage,
    emotionState,
    avatarBase64,
    avatarMimeType,
    customMessages,
  };
}

/**
 * Validate backup data structure
 */
export function validateBackup(data: unknown): data is BackupData {
  if (!data || typeof data !== 'object') return false;
  const backup = data as Partial<BackupData>;
  
  // Version is required
  if (typeof backup.version !== 'number') return false;
  
  // appStorage is required (can be null but key must exist)
  if (!('appStorage' in backup)) return false;
  
  return true;
}

/**
 * Migrate old backup formats to current version
 */
function migrateBackup(data: BackupData): BackupData {
  // Version 1 -> 2: Old format had different keys
  if (data.version === 1) {
    // Version 1 used "avatarData" as a localStorage string (not base64)
    // and "autoMessages" as a single object instead of per-tone
    const migrated: BackupData = {
      ...data,
      version: BACKUP_VERSION,
      emotionState: data.emotionState ?? null,
      avatarBase64: null,
      avatarMimeType: null,
    };
    
    // If old format had autoMessages as single object, migrate to 'default' tone
    if (data.autoMessages && !('default' in data.autoMessages)) {
      migrated.autoMessages = {
        default: data.autoMessages as unknown as unknown[],
      };
    }
    
    return migrated;
  }
  
  return data;
}

/**
 * Import all user data from a backup object
 */
export async function importAllData(rawData: BackupData): Promise<void> {
  // Migrate if needed
  const data = migrateBackup(rawData);

  // Restore main app storage
  if (data.appStorage) {
    localStorage.setItem(STORAGE_KEYS.appStorage, JSON.stringify(data.appStorage));
  }

  // Restore emotion state
  if (data.emotionState) {
    localStorage.setItem(STORAGE_KEYS.emotionState, JSON.stringify(data.emotionState));
  }

  // Note: autoMessages are intentionally NOT restored (excluded from backup/restore)

  // Restore custom messages library
  if (data.customMessages) {
    localStorage.setItem(STORAGE_KEYS.customMessages, JSON.stringify(data.customMessages));
  }

  // Restore avatar to IndexedDB
  if (data.avatarBase64 && data.avatarMimeType) {
    try {
      const blob = base64ToBlob(data.avatarBase64, data.avatarMimeType);
      await saveAvatarBlob(blob);
    } catch (e) {
      console.error('Failed to restore avatar to IndexedDB:', e);
    }
  } else {
    // Clear any existing avatar if backup doesn't have one
    try {
      await deleteAvatarBlob();
    } catch (e) {
      // Ignore - might not exist
    }
  }
}

/**
 * Download backup as encrypted file + debug JSON
 */
export async function downloadBackup(): Promise<void> {
  const data = await exportAllData();
  const jsonString = JSON.stringify(data, null, 2);
  const encrypted = await encryptData(jsonString);
  
  const dateStr = new Date().toISOString().split('T')[0];
  
  // 1. Download encrypted .cbak file
  const wrapper = {
    format: 'encrypted-v1',
    data: encrypted,
  };
  
  const cbakBlob = new Blob([JSON.stringify(wrapper)], { type: 'application/json' });
  const cbakUrl = URL.createObjectURL(cbakBlob);
  const cbakLink = document.createElement('a');
  cbakLink.href = cbakUrl;
  cbakLink.download = `texxting_${dateStr}.cbak`;
  cbakLink.click();
  URL.revokeObjectURL(cbakUrl);
  
  // 2. Download plain .json file (for debugging)
  // setTimeout(() => {
  //   const jsonBlob = new Blob([jsonString], { type: 'application/json' });
  //   const jsonUrl = URL.createObjectURL(jsonBlob);
  //   const jsonLink = document.createElement('a');
  //   jsonLink.href = jsonUrl;
  //   jsonLink.download = `companion-backup-${dateStr}-debug.json`;
  //   jsonLink.click();
  //   URL.revokeObjectURL(jsonUrl);
  // }, 100);
}

/**
 * Read and parse encrypted backup file
 */
export async function readBackupFile(file: File): Promise<BackupData> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = async (event) => {
      try {
        const content = event.target?.result as string;
        const wrapper = JSON.parse(content);
        
        let data: unknown;
        
        // Check if it's the new encrypted format
        if (wrapper.format === 'encrypted-v1' && wrapper.data) {
          try {
            const decrypted = await decryptData(wrapper.data);
            data = JSON.parse(decrypted);
          } catch (e) {
            reject(new Error('Failed to decrypt backup. The file may be corrupted or tampered with.'));
            return;
          }
        } else {
          // Legacy unencrypted format - still support for backward compatibility
          data = wrapper;
        }
        
        if (!validateBackup(data)) {
          reject(new Error('Invalid backup file format'));
          return;
        }
        
        resolve(data);
      } catch (e) {
        reject(new Error('Failed to parse backup file'));
      }
    };
    reader.onerror = () => reject(new Error('Failed to read file'));
    reader.readAsText(file);
  });
}
